import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, useLocation } from 'react-router-dom';
import { Toaster } from 'react-hot-toast';
import { AuthProvider } from './context/AuthContext';
import ProtectedRoute from './components/ProtectedRoute';
import RouteGuard from './components/RouteGuard';
import AdminLayout from './components/AdminLayout';
import BottomNav from './components/BottomNav';
import Welcome from './pages/Welcome';
import Login from './pages/Login';
import Signup from './pages/Signup';
import Home from './pages/Home';
import Watch from './pages/Watch';
import Search from './pages/Search';
import CategoryProducts from './pages/CategoryProducts';
import Listing from './pages/Listing';
import UploadListing from './pages/UploadListing';
import MyListings from './pages/MyListings';
import ProviderRegistration from './pages/ProviderRegistration';
import ProviderRequestWaiting from './pages/ProviderRequestWaiting';
import Wishlist from './pages/Wishlist';
import Profile from './pages/Profile';
import EditProfile from './pages/EditProfile';
import Settings from './pages/Settings';
import Analytics from './pages/Analytics';
import Followers from './pages/Followers';
import Following from './pages/Following';
import AdminDashboard from './pages/AdminDashboard';
import AdminReports from './pages/AdminReports';
import AdminSettings from './pages/AdminSettings';
import UserManagement from './pages/UserManagement';
import InfluencerManagement from './pages/InfluencerManagement';
import BrandManagement from './pages/BrandManagement';
import ListingManagement from './pages/ListingManagement';
import VideoManagement from './pages/VideoManagement';
import SubscriptionManagement from './pages/SubscriptionManagement';
import PromotionManagement from './pages/PromotionManagement';
import PaymentManagement from './pages/PaymentManagement';
import NotificationManagement from './pages/NotificationManagement';
import CMSManagement from './pages/CMSManagement';
import InfluencerVideoRequest from './pages/InfluencerVideoRequest';
import BrandVideoRequest from './pages/BrandVideoRequest';
import PromotionSubmission from './pages/PromotionSubmission';
import SubscriptionUpgrade from './pages/SubscriptionUpgrade';
import StaticPage from './pages/StaticPage';

function AppLayout() {
  const location = useLocation();
  const hideBottomNav =
    location.pathname === '/watch' ||
    location.pathname.startsWith('/subscription/upgrade');

  return (
    <div className="flex flex-col min-h-screen">
      <div className="flex-1">
        <Routes>
          <Route
            path="/home"
            element={
              <ProtectedRoute>
                <Home />
              </ProtectedRoute>
            }
          />
          <Route
            path="/watch"
            element={
              <ProtectedRoute>
                <Watch />
              </ProtectedRoute>
            }
          />
          <Route
            path="/search"
            element={
              <ProtectedRoute>
                <Search />
              </ProtectedRoute>
            }
          />
          <Route
            path="/category"
            element={
              <ProtectedRoute>
                <CategoryProducts />
              </ProtectedRoute>
            }
          />
          <Route
            path="/provider/register"
            element={
              <ProtectedRoute>
                <ProviderRegistration />
              </ProtectedRoute>
            }
          />
          <Route
            path="/provider-request-waiting"
            element={
              <ProtectedRoute>
                <ProviderRequestWaiting />
              </ProtectedRoute>
            }
          />
          <Route
            path="/subscription/upgrade"
            element={
              <ProtectedRoute>
                <SubscriptionUpgrade />
              </ProtectedRoute>
            }
          />
          <Route
            path="/listing"
            element={
              <ProtectedRoute>
                <Listing />
              </ProtectedRoute>
            }
          />
          <Route
            path="/listing/upload"
            element={
              <ProtectedRoute>
                <UploadListing />
              </ProtectedRoute>
            }
          />
          <Route
            path="/listing/my"
            element={
              <ProtectedRoute>
                <MyListings />
              </ProtectedRoute>
            }
          />
          <Route
            path="/wishlist"
            element={
              <ProtectedRoute>
                <Wishlist />
              </ProtectedRoute>
            }
          />
          <Route
            path="/profile"
            element={
              <ProtectedRoute>
                <Profile />
              </ProtectedRoute>
            }
          />
          <Route
            path="/profile/:username"
            element={<Profile />}
          />
          <Route
            path="/profile/edit"
            element={
              <ProtectedRoute>
                <EditProfile />
              </ProtectedRoute>
            }
          />
          <Route
            path="/settings"
            element={
              <ProtectedRoute>
                <Settings />
              </ProtectedRoute>
            }
          />
          <Route
            path="/profile/analytics"
            element={
              <ProtectedRoute>
                <Analytics />
              </ProtectedRoute>
            }
          />
          <Route
            path="/followers"
            element={
              <ProtectedRoute>
                <Followers />
              </ProtectedRoute>
            }
          />
          <Route
            path="/following"
            element={
              <ProtectedRoute>
                <Following />
              </ProtectedRoute>
            }
          />
          <Route
            path="/influencer/video-request"
            element={
              <ProtectedRoute>
                <InfluencerVideoRequest />
              </ProtectedRoute>
            }
          />
          <Route
            path="/brand/video-request"
            element={
              <ProtectedRoute>
                <BrandVideoRequest />
              </ProtectedRoute>
            }
          />
          <Route
            path="/promotion-submission"
            element={
              <ProtectedRoute>
                <PromotionSubmission />
              </ProtectedRoute>
            }
          />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </div>
      {!hideBottomNav && <BottomNav />}
    </div>
  );
}

function App() {
  return (
    <Router>
      <AuthProvider>
        <div className="flex flex-col min-h-screen">
          <Toaster
            position="top-center"
            toastOptions={{
              duration: 3000,
              style: {
                background: '#ffffff',
                color: '#111827',
                borderRadius: '12px',
                padding: '16px 20px',
                border: '1px solid #E5E7EB',
                boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
                fontSize: '14px',
                fontFamily: 'Poppins, sans-serif',
              },
              success: {
                iconTheme: {
                  primary: '#6C4DFF',
                  secondary: '#ffffff',
                },
                style: {
                  background: '#ffffff',
                  color: '#111827',
                  borderRadius: '12px',
                  padding: '16px 20px',
                  border: '1px solid #E5E7EB',
                  boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
                  fontSize: '14px',
                  fontFamily: 'Poppins, sans-serif',
                },
              },
              error: {
                iconTheme: {
                  primary: '#EF4444',
                  secondary: '#ffffff',
                },
                style: {
                  background: '#ffffff',
                  color: '#111827',
                  borderRadius: '12px',
                  padding: '16px 20px',
                  border: '1px solid #FEE2E2',
                  boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
                  fontSize: '14px',
                  fontFamily: 'Poppins, sans-serif',
                },
              },
            }}
          />
          <RouteGuard>
            <Routes>
              {/* Welcome page for non-authenticated users */}
              <Route path="/" element={<Welcome />} />
              
              {/* Auth Routes */}
              <Route path="/login" element={<Login />} />
              <Route path="/signup" element={<Signup />} />
              <Route path="/pages/:slug" element={<StaticPage />} />

            {/* Admin Routes - Protected by existing auth system */}
            <Route
              path="/admin/*"
              element={
                <ProtectedRoute adminOnly={true}>
                  <AdminLayout>
                    <Routes>
                      <Route path="/dashboard" element={<AdminDashboard />} />
                      <Route path="/reports" element={<AdminReports />} />
                      <Route path="/settings" element={<AdminSettings />} />
                      <Route path="/users" element={<UserManagement />} />
                      <Route path="/influencers" element={<InfluencerManagement />} />
                      <Route path="/brands" element={<BrandManagement />} />
                      <Route path="/listings" element={<ListingManagement />} />
                      <Route path="/videos" element={<VideoManagement />} />
                      <Route path="/subscriptions" element={<SubscriptionManagement />} />
                      <Route path="/promotions" element={<PromotionManagement />} />
                      <Route path="/payments" element={<PaymentManagement />} />
                      <Route path="/notifications" element={<NotificationManagement />} />
            <Route path="/cms" element={<CMSManagement />} />
                      <Route path="/" element={<Navigate to="/admin/dashboard" replace />} />
                      <Route path="*" element={<Navigate to="/admin/dashboard" replace />} />
                    </Routes>
                  </AdminLayout>
                </ProtectedRoute>
              }
            />

            {/* Regular App Routes */}
            <Route
              path="/*"
              element={
                <AppLayout />
              }
            />
            </Routes>
          </RouteGuard>
        </div>
      </AuthProvider>
    </Router>
  );
}

export default App;
