import React, { useState, useEffect, useCallback } from 'react';
import {
  User,
  MapPin,
  Shield,
  Package,
  Briefcase,
  Eye,
  Heart,
  Plus,
  UserPlus,
  UserMinus,
  Video,
  Play
} from 'lucide-react';
// assume default export is an axios instance and named exports are helpers
import api, { userAPI, providerAPI, subscriptionAPI, listingAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import SettingsDropdown from '../components/SettingsDropdown';
import { useNavigate, useSearchParams, useParams } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const Profile = () => {
  const [searchParams] = useSearchParams();
  const { username: usernameParam } = useParams();
  const userIdParam = searchParams.get('user_id');
  const { user: currentUser, logout } = useAuth();
  
  const [user, setUser] = useState(null);
  const [provider, setProvider] = useState(null);
  const [subscription, setSubscription] = useState(null);
  const [listings, setListings] = useState([]);
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('ALL'); // 'ALL', 'PRODUCT', 'SERVICE'
  const [followStatus, setFollowStatus] = useState({ is_following: false, follower_count: 0, following_count: 0 });
  const [loadingFollow, setLoadingFollow] = useState(false);
  const navigate = useNavigate();
  
  const userIdParamNum = userIdParam ? Number(userIdParam) : null;
  const isViewingByUsername = !!usernameParam;
  // profileUserId should be the ID of the profile being viewed, not the current user
  const profileUserId = userIdParamNum || (isViewingByUsername ? null : (currentUser?.user_id ?? null));
  // When viewing by username, it's always non-editable (even if it's your own profile)
  // When viewing by user_id param, check if it matches current user
  const isOwnProfile = (!userIdParam && !usernameParam) || (currentUser && userIdParamNum && Number(currentUser.user_id) === userIdParamNum);
  const isViewingOwnProfileByUsername = isViewingByUsername && currentUser && usernameParam && currentUser.username === usernameParam;

  const loadProfileData = useCallback(async () => {
    try {
      setLoading(true);
      
      if (isViewingByUsername) {
        // Load profile by username (public, non-editable)
        if (!usernameParam) {
          toast.error('Invalid username');
          return;
        }

        const response = await userAPI.getProfileByUsername(usernameParam);
        if (response?.ok) {
          setUser(response.user ?? null);
          setProvider(response.provider ?? null);
          setListings(response.listings ?? []);
          setSubscription(null); // No subscription info for public profiles
          
          // Fetch videos if provider exists
          if (response.provider?.provider_id) {
            try {
              const videosResponse = await api.get(`/api/watch/provider/${response.provider.provider_id}/videos`);
              setVideos(videosResponse.data?.videos ?? []);
            } catch (err) {
              console.error('Error fetching videos:', err);
              setVideos([]);
            }
          } else {
            setVideos([]);
          }
          
          // Set follow status if available
          if (response.follow_status) {
            setFollowStatus({
              is_following: response.follow_status.is_following ?? false,
              follower_count: response.follow_status.follower_count ?? 0,
              following_count: response.follow_status.following_count ?? 0
            });
          }
          
          // Note: Even if viewing own profile by username, it remains non-editable
          // The is_own_profile flag from backend is used for follow button logic
        } else {
          toast.error(response?.error || 'User not found');
        }
      } else if (isOwnProfile) {
        // Load own profile
        const [userData, providerData, subscriptionData, listingsData, videosData] = await Promise.all([
          userAPI.getProfile().catch((err) => {
            console.error('Error fetching user profile:', err);
            return null;
          }),
          providerAPI.getMe().catch((err) => {
            console.error('Error fetching provider:', err);
            return null;
          }),
          subscriptionAPI.getStatus().catch((err) => {
            console.error('Error fetching subscription:', err);
            return null;
          }),
          listingAPI.getMy().catch((err) => {
            console.error('Error fetching listings:', err);
            return { listings: [], pending_requests: [] };
          }),
          userAPI.getMyVideos().catch((err) => {
            console.error('Error fetching videos:', err);
            return { ok: false, videos: [] };
          })
        ]);

        setUser(userData?.user ?? userData ?? null);
        setProvider(providerData?.provider ?? providerData ?? null);
        setSubscription(subscriptionData ?? null);
        
        // Combine pending requests and approved listings (keep order: pending first)
        const allListings = [
          ...(listingsData?.pending_requests ?? []),
          ...(listingsData?.listings ?? [])
        ];
        setListings(allListings);
        
        // Set videos (only approved for public view, but all for own profile)
        const allVideos = videosData?.videos ?? [];
        setVideos(allVideos);
      } else {
        // Load other user's profile by user_id
        if (!profileUserId) {
          toast.error('Invalid user');
          return;
        }

        const response = await api.get(`/api/users/${profileUserId}/profile`);
        if (response?.data?.ok) {
          setUser(response.data.user ?? null);
          setProvider(response.data.provider ?? null);
          setListings(response.data.listings ?? []);
          setSubscription(response.data.subscription ?? null);
          
          // Fetch videos if provider exists
          if (response.data.provider?.provider_id) {
            try {
              const videosResponse = await api.get(`/api/watch/provider/${response.data.provider.provider_id}/videos`);
              setVideos(videosResponse.data?.videos ?? []);
            } catch (err) {
              console.error('Error fetching videos:', err);
              setVideos([]);
            }
          } else {
            setVideos([]);
          }
        } else {
          toast.error(response?.data?.error || 'User not found');
        }
      }
    } catch (error) {
      console.error('Error loading profile:', error);
      toast.error('Could not fetch profile data');
    } finally {
      setLoading(false);
    }
  }, [isOwnProfile, isViewingByUsername, profileUserId, usernameParam, setUser, setProvider, setSubscription, setListings, setVideos, setLoading, setFollowStatus]);

  const loadFollowStatus = useCallback(async (targetUserId) => {
    if (!targetUserId) return;
    
    try {
      const data = await userAPI.getFollowStatus(targetUserId);
      if (data && data.ok !== false) {
        setFollowStatus({
          is_following: !!data.is_following,
          follower_count: Number(data.follower_count ?? 0),
          following_count: Number(data.following_count ?? 0)
        });
      }
    } catch (error) {
      console.error('Error loading follow status:', error);
    }
  }, []);

  useEffect(() => {
    loadProfileData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loadProfileData]);

  // Load follow status after user data is loaded (only if not already loaded from username endpoint)
  useEffect(() => {
    // Skip if viewing by username - follow status is already loaded in loadProfileData
    if (isViewingByUsername) return;
    
    if (user?.user_id) {
      loadFollowStatus(user.user_id);
    } else if (profileUserId) {
      loadFollowStatus(profileUserId);
    }
  }, [user?.user_id, profileUserId, loadFollowStatus, isViewingByUsername]);

  const handleFollowToggle = async () => {
    // Determine the target user ID - the profile being viewed
    let targetUserId = null;
    if (userIdParamNum) {
      targetUserId = userIdParamNum;
    } else if (isViewingByUsername && user?.user_id) {
      targetUserId = user.user_id;
    } else if (profileUserId) {
      targetUserId = profileUserId;
    } else if (user?.user_id) {
      targetUserId = user.user_id;
    }
    
    if (!targetUserId || !currentUser) {
      toast.error('Please log in to follow users');
      return;
    }
    
    // Prevent following yourself
    const currentUserId = currentUser.user_id ? Number(currentUser.user_id) : null;
    const targetId = Number(targetUserId);
    
    if (currentUserId && currentUserId === targetId) {
      toast.error('Cannot follow yourself');
      return;
    }
    
    setLoadingFollow(true);
    try {
      if (followStatus.is_following) {
        await userAPI.unfollow(targetUserId);
        setFollowStatus(prev => ({
          ...prev,
          is_following: false,
          follower_count: Math.max(0, (prev.follower_count ?? 0) - 1)
        }));
        toast.success('Unfollowed');
      } else {
        await userAPI.follow(targetUserId);
        setFollowStatus(prev => ({
          ...prev,
          is_following: true,
          follower_count: (prev.follower_count ?? 0) + 1
        }));
        toast.success('Followed');
      }
    } catch (error) {
      console.error('Error toggling follow:', error);
      const errorMessage = error?.response?.data?.error || 'Failed to update follow status';
      toast.error(errorMessage);
    } finally {
      setLoadingFollow(false);
    }
  };


  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    try {
    return new Date(dateString).toLocaleDateString('en-IN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
    } catch (e) {
      return 'N/A';
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <div className="text-gray-900 text-lg font-poppins">Loading profile...</div>
      </div>
    );
  }

  const isVerified = provider && provider.status === 'APPROVED';
  const displayName = provider?.business_name ?? user?.full_name ?? user?.name ?? 'User';
  const displayBio = provider?.handle_address ?? user?.email ?? '';
  const profilePicture = user?.profile_picture ?? user?.profile_image_url ?? null;
  const bannerUrl = user?.banner ?? null;
  const displayUsername = user?.username ?? '';
  const userType = user?.type ?? 'NORMAL';
  const totalLikes = user?.total_likes ?? 0;
  const totalViews = user?.total_views ?? 0;
  
  const stats = {
    listings: listings?.length ?? 0,
    products: (listings ?? []).filter(l => l.type === 'PRODUCT').length,
    totalViews: (listings ?? []).reduce((sum, l) => sum + (Number(l.view_count ?? 0)), 0),
    totalLikes: (listings ?? []).reduce((sum, l) => sum + (Number(l.like_count ?? 0)), 0)
  };
  
  const displayStats = isOwnProfile ? stats : {
    listings: listings?.length ?? 0,
    totalViews: (listings ?? []).reduce((sum, l) => sum + (Number(l.view_count ?? 0)), 0),
    totalLikes: (listings ?? []).reduce((sum, l) => sum + (Number(l.like_count ?? 0)), 0)
  };

  // Filter listings based on active tab
  const filteredListings = (listings ?? []).filter(listing =>
    activeTab === 'ALL' || listing.type === activeTab
  );
  
  // For ALL tab, combine videos and products. For PRODUCT tab, only show products
  const displayItems = activeTab === 'ALL' 
    ? [
        ...videos.map(v => ({
          id: `video_${v.video_id}`,
          video_id: v.video_id,
          thumbnail_url: v.thumbnail_url,
          video_url: v.video_url,
          like_count: v.like_count ?? 0,
          view_count: v.view_count ?? 0,
          created_at: v.created_at,
          video_status: v.video_status,
          status: v.video_status, // For consistency with listings
          is_video: true
        })),
        ...filteredListings.map(l => ({
          id: `listing_${l.listing_id ?? l.request_id ?? l.id}`,
          listing_id: l.listing_id,
          request_id: l.request_id,
          thumbnail_url: l.thumbnail_url,
          video_url: l.video_url,
          product_name: l.product_name,
          like_count: l.like_count ?? 0,
          view_count: l.view_count ?? 0,
          status: l.status,
          video_status: l.video_status, // Include video_status from backend
          created_at: l.created_at,
          is_video: false
        }))
      ].sort((a, b) => new Date(b.created_at) - new Date(a.created_at)) // Sort by date, newest first
    : filteredListings.map(l => ({
        id: `listing_${l.listing_id ?? l.request_id ?? l.id}`,
        listing_id: l.listing_id,
        request_id: l.request_id,
        thumbnail_url: l.thumbnail_url,
        video_url: l.video_url,
        product_name: l.product_name,
        like_count: l.like_count ?? 0,
        view_count: l.view_count ?? 0,
        status: l.status,
        video_status: l.video_status, // Include video_status from backend
        created_at: l.created_at,
        is_video: false
      }));

  return (
    <div className="min-h-screen bg-white text-gray-900">
      {/* Header */}
      <div className="bg-white/80 backdrop-blur border-b border-gray-200 px-4 sm:px-6 py-4 sticky top-0 z-20">
        <div className="flex items-center justify-between max-w-4xl mx-auto">
          <h1 className="text-xl font-bold text-gray-900 font-poppins">
            {isOwnProfile && !isViewingByUsername ? (user?.name ?? user?.full_name ?? 'Profile') : (user?.full_name ?? user?.username ?? 'Profile')}
          </h1>
          {isOwnProfile && !isViewingByUsername && !isViewingOwnProfileByUsername && (
            <SettingsDropdown user={user || currentUser} />
          )}
        </div>
      </div>

      <div className="max-w-4xl mx-auto pb-32 sm:pb-24">
        {/* Banner */}
        <div className="w-full h-44 sm:h-56 bg-gray-100 relative">
          {bannerUrl ? (
            <img src={bannerUrl} alt="Banner" className="w-full h-full object-cover" />
          ) : (
            <div className="w-full h-full bg-gradient-to-r from-purple-100 to-blue-100" />
          )}
        </div>

        {/* Profile Card overlapping banner */}
        <div className="-mt-12 sm:-mt-16 px-4 sm:px-6 relative z-10">
          <div className="bg-white rounded-2xl shadow-xl border border-gray-200 p-5 sm:p-6">
            <div className="flex flex-col sm:flex-row gap-6 sm:gap-8">
              {/* Profile Picture */}
              <div className="flex-shrink-0 -mt-10 sm:-mt-12 relative">
                {profilePicture ? (
                  <img
                    src={profilePicture}
                    alt="Profile"
                    className="w-20 h-20 sm:w-28 sm:h-28 rounded-full object-cover border-4 border-white shadow-md"
                  />
                ) : (
                  <div className="w-20 h-20 sm:w-28 sm:h-28 rounded-full bg-gradient-to-br from-[#6A00FF] to-purple-600 flex items-center justify-center border-4 border-white shadow-md">
                    <User className="w-10 h-10 sm:w-16 sm:h-16 text-white" />
                  </div>
                )}
              </div>

              {/* Profile Info */}
              <div className="flex-1 min-w-0">
              {/* Name and Verification */}
              <div className="mb-3">
                <div className="flex items-center justify-between gap-2 mb-1">
                  <div className="flex items-center gap-2 flex-1 min-w-0">
                    <h2 className="text-xl sm:text-2xl font-poppins text-gray-900 truncate">{displayName}</h2>
                    {isVerified && (
                      <div className="flex items-center justify-center flex-shrink-0">
                        <svg 
                          className="w-5 h-5 sm:w-6 sm:h-6" 
                          viewBox="0 0 24 24" 
                          fill="none" 
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <circle cx="12" cy="12" r="12" fill="#0095F6" />
                          <path d="M8 12L11 15L16 9" stroke="white" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round" fill="none" />
                        </svg>
                      </div>
                    )}
                  </div>
                  {/* Follow Button - moved here beside verification badge */}
                  {!isOwnProfile && !isViewingOwnProfileByUsername && (
                    <button
                      onClick={handleFollowToggle}
                      disabled={loadingFollow || !currentUser}
                      className={`px-3 py-1.5 rounded-lg transition-colors text-xs sm:text-sm font-poppins flex items-center gap-1.5 flex-shrink-0 ${
                        followStatus.is_following
                          ? 'bg-gray-100 hover:bg-gray-200 text-gray-900'
                          : 'bg-[#6A00FF] hover:bg-purple-700 text-white'
                      }`}
                    >
                      {loadingFollow ? (
                        <span className="text-xs">...</span>
                      ) : followStatus.is_following ? (
                        <>
                          <UserMinus className="w-3.5 h-3.5" />
                          <span className="hidden sm:inline">Unfollow</span>
                        </>
                      ) : (
                        <>
                          <UserPlus className="w-3.5 h-3.5" />
                          <span className="hidden sm:inline">Follow</span>
                        </>
                      )}
                    </button>
                  )}
                </div>
                {displayUsername && (
                  <p className="text-gray-600 text-sm font-poppins">@{displayUsername}</p>
                )}
                {userType && userType !== 'NORMAL' && (
                  <p className="text-gray-500 text-xs font-poppins capitalize mt-1">{userType.toLowerCase()}</p>
                )}
              </div>

              {/* Stats */}
              <div className="flex gap-6 sm:gap-8 mb-4 flex-wrap">
                <div 
                  className="text-center sm:text-left cursor-pointer"
                  onClick={() => {
                      const targetUserId = userIdParamNum || user?.user_id;
                      if (targetUserId) navigate(`/followers?user_id=${targetUserId}`);
                  }}
                >
                    <span className="font-semibold text-gray-900 font-poppins">{followStatus.follower_count ?? 0}</span>
                    <span className="text-gray-600 text-sm font-poppins ml-1">followers</span>
                </div>

                <div 
                  className="text-center sm:text-left cursor-pointer"
                  onClick={() => {
                      const targetUserId = userIdParamNum || user?.user_id;
                      if (targetUserId) navigate(`/following?user_id=${targetUserId}`);
                  }}
                >
                    <span className="font-semibold text-gray-900 font-poppins">{followStatus.following_count ?? 0}</span>
                  <span className="text-gray-600 text-sm font-poppins ml-1">following</span>
                </div>

                {isOwnProfile && (
                    <div className="text-center sm:text-left">
                      <span className="font-semibold text-gray-900 font-poppins">{displayStats.listings}</span>
                      <span className="text-gray-600 text-sm font-poppins ml-1">listings</span>
                    </div>
                )}
                
                {!isViewingByUsername && totalLikes > 0 && (
                  <div className="text-center sm:text-left">
                    <span className="font-semibold text-gray-900 font-poppins">{totalLikes}</span>
                    <span className="text-gray-600 text-sm font-poppins ml-1">likes</span>
                  </div>
                )}
                
                {!isViewingByUsername && totalViews > 0 && (
                  <div className="text-center sm:text-left">
                    <span className="font-semibold text-gray-900 font-poppins">{totalViews}</span>
                    <span className="text-gray-600 text-sm font-poppins ml-1">views</span>
                  </div>
                )}
              </div>

              {/* Bio */}
              <div className="mb-4">
                <p className="font-semibold text-gray-900 font-poppins text-sm mb-1">
                  {provider?.business_name && user?.name && user.name !== provider.business_name ? user.name : ''}
                  {provider?.type && isOwnProfile && !isViewingByUsername && (
                      <span className="ml-2 text-gray-600 font-poppins text-sm font-normal capitalize">{provider.type.toLowerCase()}</span>
                  )}
                </p>

                {provider?.location && (
                  <p className="text-gray-900 font-poppins text-sm flex items-center gap-1 mb-1">
                    <MapPin className="w-4 h-4" />
                    {provider.location}
                  </p>
                )}

                {displayBio && (
                  <p className="text-gray-900 font-poppins text-sm">{displayBio}</p>
                )}

                {provider && provider.status === 'PENDING' && (
                  <p className="text-yellow-600 font-poppins text-sm mt-2">Verification pending</p>
                )}
              </div>

                {/* User details - compact grid */}
              {isOwnProfile && !isViewingByUsername && (
                <div className="grid grid-cols-2 gap-3 mb-4">
                    <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Email</p>
                      <p className="text-sm text-gray-900 truncate font-poppins">{user?.email ?? ''}</p>
                    </div>

                    <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Phone</p>
                      <p className="text-sm text-gray-900 truncate font-poppins">{user?.phone ?? ''}</p>
                    </div>

                    <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Joined</p>
                      <p className="text-sm text-gray-900 truncate font-poppins">{formatDate(user?.created_at)}</p>
                    </div>
                  </div>
              )}
              
              {/* Public user info - only show non-confidential info */}
              {!isOwnProfile && !isViewingByUsername && (
                <div className="grid grid-cols-2 gap-3 mb-4">
                    <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Joined</p>
                      <p className="text-sm text-gray-900 truncate font-poppins">{formatDate(user?.created_at)}</p>
                    </div>
                  </div>
              )}
              
              {isViewingByUsername && (
                <div className="grid grid-cols-2 gap-3 mb-4">
                    <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Joined</p>
                      <p className="text-sm text-gray-900 truncate font-poppins">{formatDate(user?.created_at)}</p>
                    </div>
                  </div>
              )}

              {/* Provider details - compact grid */}
              {provider && isOwnProfile && !isViewingByUsername && (
                <div className="grid grid-cols-2 gap-3 mb-4">
                  <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                    <p className="text-[11px] text-gray-500 font-poppins">Provider Type</p>
                    <p className="text-sm text-gray-900 truncate font-poppins">{(provider.provider_type || provider.type || '').toString().toUpperCase()}</p>
                  </div>
                  <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                    <p className="text-[11px] text-gray-500 font-poppins">Status</p>
                    <p className="text-sm text-gray-900 truncate font-poppins">{provider.status}</p>
                  </div>
                  <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                    <p className="text-[11px] text-gray-500 font-poppins">GST</p>
                    <p className="text-sm text-gray-900 truncate font-poppins">{provider.gst || ''}</p>
                  </div>
                  <div className="bg-gray-50 rounded-lg p-3 border border-gray-200">
                    <p className="text-[11px] text-gray-500 font-poppins">Upload Limit</p>
                    <p className="text-sm text-gray-900 truncate font-poppins">{provider.upload_limit ?? ''}</p>
                  </div>
                  {provider.business_address && (
                    <div className="col-span-2 bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Business Address</p>
                      <p className="text-sm text-gray-900 font-poppins whitespace-pre-wrap">{provider.business_address}</p>
                    </div>
                  )}
                  {provider.handle_address && (
                    <div className="col-span-2 bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Handle/Website</p>
                      <p className="text-sm text-gray-900 font-poppins break-all">{provider.handle_address}</p>
                    </div>
                  )}
                </div>
              )}
              
              {/* Public provider info - only show non-confidential info */}
              {provider && (!isOwnProfile || isViewingByUsername) && (
                <div className="grid grid-cols-2 gap-3 mb-4">
                  {provider.business_address && (
                    <div className="col-span-2 bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Business Address</p>
                      <p className="text-sm text-gray-900 font-poppins whitespace-pre-wrap">{provider.business_address}</p>
                    </div>
                  )}
                  {provider.handle_address && (
                    <div className="col-span-2 bg-gray-50 rounded-lg p-3 border border-gray-200">
                      <p className="text-[11px] text-gray-500 font-poppins">Handle/Website</p>
                      <p className="text-sm text-gray-900 font-poppins break-all">{provider.handle_address}</p>
                    </div>
                  )}
                </div>
              )}
              {/* Action Buttons */}
              {isOwnProfile && !isViewingByUsername && !provider && (
                <div className="flex gap-2">
                  <button
                    onClick={() => navigate('/provider/register')}
                    className="bg-[#6A00FF] hover:bg-purple-700 text-white px-4 py-2 rounded-lg transition-colors text-sm font-poppins"
                  >
                    Register as Provider
                  </button>
                </div>
              )}
              </div>
            </div>
          </div>
        </div>

        {/* Tabs */}
        <div className="flex justify-center border-b border-gray-200">
          <div className="flex gap-8">
            <button
              onClick={() => setActiveTab('ALL')}
              className={`py-4 px-1 border-b-2 transition-colors font-poppins text-sm ${
                activeTab === 'ALL' ? 'border-gray-900 text-gray-900 font-semibold' : 'border-transparent text-gray-500'
              }`}
            >
              ALL
            </button>

            <button
              onClick={() => setActiveTab('PRODUCT')}
              className={`py-4 px-1 border-b-2 transition-colors font-poppins text-sm flex items-center gap-1 ${
                activeTab === 'PRODUCT' ? 'border-gray-900 text-gray-900 font-semibold' : 'border-transparent text-gray-500'
              }`}
            >
              <Package className="w-4 h-4" />
              PRODUCTS
            </button>
          </div>
        </div>

        {/* Listings Grid - Instagram Style */}
        {displayItems.length > 0 ? (
          <div className="grid grid-cols-3 gap-1 sm:gap-px py-4">
            {displayItems.map((item) => (
              <div
                key={item.id}
                onClick={() => {
                  if (item.is_video && item.video_id) {
                    navigate(`/watch?video=${item.video_id}`);
                  } else if (item.listing_id) {
                    navigate(`/watch?video=${item.listing_id}`);
                  }
                }}
                className="aspect-square bg-gray-100 relative group cursor-pointer overflow-hidden"
              >
                {item.thumbnail_url ? (
                  <img
                    src={item.thumbnail_url}
                    alt={item.product_name ?? 'Video'}
                    className="w-full h-full object-cover group-hover:opacity-75 transition-opacity"
                  />
                ) : item.video_url ? (
                  <video
                    src={item.video_url}
                    className="w-full h-full object-cover group-hover:opacity-75 transition-opacity"
                    muted
                  />
                ) : (
                  <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-[#6A00FF] to-purple-600">
                    {item.is_video ? (
                      <Video className="w-8 h-8 text-white opacity-50" />
                    ) : (
                      <Package className="w-8 h-8 text-white opacity-50" />
                    )}
                  </div>
                )}
                
                {/* Type Icon - Video or Product indicator */}
                {activeTab === 'ALL' && (
                  <div className="absolute top-2 left-2">
                    {item.is_video ? (
                      <div className="bg-black/60 backdrop-blur-sm rounded-full p-1.5">
                        <Play className="w-3 h-3 text-white fill-white" />
                      </div>
                    ) : (
                      <div className="bg-black/60 backdrop-blur-sm rounded-full p-1.5">
                        <Package className="w-3 h-3 text-white" />
                      </div>
                    )}
                  </div>
                )}
                
                {/* Overlay on hover */}
                <div className="absolute inset-0 bg-black/0 group-hover:bg-black/30 transition-colors flex items-center justify-center gap-4 opacity-0 group-hover:opacity-100">
                  <div className="flex items-center gap-1 text-white">
                    <Heart className="w-5 h-5" />
                    <span className="font-poppins text-sm">{item.like_count ?? 0}</span>
                  </div>
                  <div className="flex items-center gap-1 text-white">
                    <Eye className="w-5 h-5" />
                    <span className="font-poppins text-sm">{item.view_count ?? 0}</span>
                  </div>
                </div>

                {/* Pending Badge - Show for pending listings or pending videos (only for own profile) */}
                {isOwnProfile && !isViewingByUsername && (item.status === 'PENDING' || item.video_status === 'PENDING') && (
                  <div className="absolute top-2 right-2 bg-yellow-500 text-white text-xs px-2 py-1 rounded font-poppins font-semibold shadow-md z-10">
                    Pending
                  </div>
                )}
              </div>
            ))}
          </div>
        ) : (
          <div className="py-12 text-center">
            {activeTab === 'ALL' ? (
              <Video className="w-16 h-16 text-gray-300 mx-auto mb-4" />
            ) : (
              <Package className="w-16 h-16 text-gray-300 mx-auto mb-4" />
            )}
            {isOwnProfile && !isViewingByUsername ? (
              <>
                <p className="text-gray-500 font-poppins mb-4">
                  {activeTab === 'ALL' ? 'No videos or listings yet' : 'No listings yet'}
                </p>
                <button
                  onClick={() => navigate('/listing/upload')}
                  className="bg-[#6A00FF] hover:bg-purple-700 text-white px-6 py-2 rounded-lg transition-colors text-sm font-poppins flex items-center gap-2 mx-auto"
                >
                  <Plus className="w-4 h-4" />
                  Upload Your First Listing
                </button>
              </>
            ) : (
              <>
                <p className="text-gray-500 font-poppins mb-2">
                  {activeTab === 'ALL' 
                    ? "This user doesn't have any videos or listings yet"
                    : "This user doesn't have any listings yet"}
                </p>
                <p className="text-gray-400 text-sm font-poppins">Follow to stay updated</p>
              </>
            )}
          </div>
        )}

      {/* Additional Info Section */}
      {!isViewingByUsername && (
      <div className="mt-8 space-y-4 pb-8">
        {/* Admin Panel Access */}
        {user?.is_admin && (
          <div className="bg-gradient-to-r from-blue-100 to-purple-100 rounded-lg p-4 border border-blue-200">
              <button onClick={() => navigate('/admin/dashboard')} className="w-full bg-gradient-to-r from-blue-600 to-[#6A00FF] hover:from-blue-700 hover:to-purple-700 text-white px-6 py-3 rounded-lg transition-all font-medium flex items-center justify-center gap-2 font-poppins">
              <Shield className="w-5 h-5" />
              Access Admin Dashboard
            </button>
          </div>
        )}
      </div>
      )}

      </div>
    </div>
  );
};

export default Profile;
