import { 
  signInWithPopup, 
  signInWithRedirect,
  getRedirectResult,
  signOut as firebaseSignOut,
  onAuthStateChanged
} from 'firebase/auth';
import { auth, googleProvider } from '../config/firebase';

/**
 * Sign in with Google using popup
 * @returns {Promise<Object>} User credential object
 */
export const signInWithGooglePopup = async () => {
  try {
    const result = await signInWithPopup(auth, googleProvider);
    return {
      user: result.user,
      credential: result.credential,
    };
  } catch (error) {
    console.error('Google sign-in error:', error);
    throw error;
  }
};

/**
 * Sign in with Google using redirect (for mobile/popup-blocked scenarios)
 * @returns {Promise<void>}
 */
export const signInWithGoogleRedirect = async () => {
  try {
    await signInWithRedirect(auth, googleProvider);
  } catch (error) {
    console.error('Google redirect sign-in error:', error);
    throw error;
  }
};

/**
 * Get the result of a redirect sign-in
 * Call this after page load to check if user signed in via redirect
 * @returns {Promise<Object|null>} User credential object or null
 */
export const getGoogleRedirectResult = async () => {
  try {
    const result = await getRedirectResult(auth);
    if (result) {
      return {
        user: result.user,
        credential: result.credential,
      };
    }
    return null;
  } catch (error) {
    console.error('Get redirect result error:', error);
    throw error;
  }
};

/**
 * Sign out from Firebase
 * @returns {Promise<void>}
 */
export const signOut = async () => {
  try {
    await firebaseSignOut(auth);
  } catch (error) {
    console.error('Sign out error:', error);
    throw error;
  }
};

/**
 * Get the current Firebase user
 * @returns {Object|null} Current user or null
 */
export const getCurrentFirebaseUser = () => {
  return auth.currentUser;
};

/**
 * Get Firebase ID token
 * @returns {Promise<string>} ID token
 */
export const getIdToken = async () => {
  const user = auth.currentUser;
  if (!user) {
    throw new Error('No user is signed in');
  }
  return await user.getIdToken();
};

/**
 * Listen to auth state changes
 * @param {Function} callback - Callback function that receives the user object
 * @returns {Function} Unsubscribe function
 */
export const onAuthStateChange = (callback) => {
  return onAuthStateChanged(auth, callback);
};

