import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';

const ListingManagement = () => {
  const [activeTab, setActiveTab] = useState('pending');
  const [listings, setListings] = useState([]);
  const [pendingListings, setPendingListings] = useState([]);
  const [approvedListings, setApprovedListings] = useState([]);
  const [rejectedListings, setRejectedListings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedListing, setSelectedListing] = useState(null);
  const [showListingModal, setShowListingModal] = useState(false);
  const [actionLoading, setActionLoading] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    fetchListings();
  }, [activeTab]);

  const fetchListings = async () => {
    try {
      setLoading(true);
      const status = activeTab === 'all' ? null : activeTab.toUpperCase();
      const response = await adminAPI.getListings({ status });
      
      if (response.ok) {
        const data = response.listings || [];
        console.log('Fetched listings:', data.length, 'total'); // Debug log
        console.log('Sample listing:', data[0]); // Debug log
        setListings(data);
        
        // Filter by status (handle both grouped and individual listings)
        setPendingListings(data.filter(l => (l.status || 'PENDING') === 'PENDING'));
        setApprovedListings(data.filter(l => (l.status || 'PENDING') === 'APPROVED'));
        setRejectedListings(data.filter(l => (l.status || 'PENDING') === 'REJECTED'));
      } else {
        setError(response.message || 'Failed to load listings');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleApproval = async (requestId, action, reason = '') => {
    try {
      setActionLoading(requestId);
      setError(''); // Clear previous errors
      const response = await adminAPI.approveListing(requestId, action, reason);
      
      if (response.ok) {
        await fetchListings();
        if (selectedListing && selectedListing.request_id === requestId) {
          setShowListingModal(false);
          setSelectedListing(null);
        }
      } else {
        setError(response.message || 'Failed to update listing status');
      }
    } catch (err) {
      console.error('Approval error:', err);
      const errorMessage = err.response?.data?.message || err.message || 'Network error. Please try again.';
      setError(errorMessage);
    } finally {
      setActionLoading(null);
    }
  };

  const openListingModal = async (listing) => {
    try {
      const requestId = listing.first_request_id || listing.request_id;
      const response = await adminAPI.getListingDetails(requestId);
      if (response.ok) {
        setSelectedListing(response.data);
        setShowListingModal(true);
      } else {
        setError(response.message || 'Failed to load listing details');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'APPROVED': return 'bg-green-100 text-green-800';
      case 'PENDING': return 'bg-yellow-100 text-yellow-800';
      case 'REJECTED': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const filteredListings = (list) => {
    if (!searchTerm) return list;
    return list.filter(listing => {
      // Handle grouped listings (new format)
      if (listing.products && Array.isArray(listing.products)) {
        return listing.products.some(product =>
          product.product_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
          product.user_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
          product.provider_name?.toLowerCase().includes(searchTerm.toLowerCase())
        );
      }
      // Handle individual listings (old format - backward compatibility)
      return listing.product_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      listing.user_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        listing.provider_name?.toLowerCase().includes(searchTerm.toLowerCase());
    });
  };

  const getActiveListings = () => {
    switch (activeTab) {
      case 'pending': return filteredListings(pendingListings);
      case 'approved': return filteredListings(approvedListings);
      case 'rejected': return filteredListings(rejectedListings);
      default: return filteredListings(listings);
    }
  };

  const ListingCard = ({ listing, showActions = true }) => {
    // Handle new grouped format (video with multiple products)
    const isGrouped = listing.products && Array.isArray(listing.products);
    const firstProduct = isGrouped ? listing.products[0] : listing;
    const videoThumbnail = listing.video?.thumbnail_url || listing.thumbnail_url;
    const requestId = listing.first_request_id || listing.request_id;
    const productCount = listing.product_count || 1;
    
    return (
    <div className="bg-white rounded-xl sm:rounded-2xl p-4 sm:p-6 shadow-lg border border-gray-100 hover:shadow-xl transition-all duration-300">
      <div className="flex flex-col sm:flex-row sm:items-start gap-4">
        <div className="flex items-start space-x-3 sm:space-x-4 flex-1">
            {videoThumbnail ? (
              <img src={videoThumbnail} alt="Video thumbnail" className="w-16 h-16 sm:w-20 sm:h-20 object-cover rounded-lg sm:rounded-xl flex-shrink-0" />
          ) : (
            <div className="w-16 h-16 sm:w-20 sm:h-20 bg-purple-100 rounded-lg sm:rounded-xl flex items-center justify-center flex-shrink-0">
              <svg className="w-8 h-8 sm:w-10 sm:h-10 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
              </svg>
            </div>
          )}
          <div className="flex-1 min-w-0">
              <div className="flex flex-wrap items-center gap-2 mb-1">
                <h3 className="text-base sm:text-xl font-bold text-gray-900 truncate">
                  {isGrouped ? `${productCount} Product${productCount > 1 ? 's' : ''}` : (firstProduct.product_name || 'No Name')}
                </h3>
                {isGrouped && (
                  <span className="px-2 py-0.5 sm:py-1 rounded-full text-xs font-semibold bg-purple-100 text-purple-800">
                    Video Group
                  </span>
                )}
              </div>
              {isGrouped && listing.products && (
                <div className="mb-2">
                  <p className="text-sm text-gray-600 mb-1">Products:</p>
                  <div className="flex flex-wrap gap-2">
                    {listing.products.slice(0, 3).map((product, idx) => (
                      <span key={idx} className="text-xs bg-gray-100 px-2 py-1 rounded">
                        {product.product_name}
                      </span>
                    ))}
                    {listing.products.length > 3 && (
                      <span className="text-xs bg-gray-100 px-2 py-1 rounded">
                        +{listing.products.length - 3} more
                      </span>
                    )}
                  </div>
                </div>
              )}
              {!isGrouped && (
                <>
                  <p className="text-gray-600 mb-2">{firstProduct.user_name || 'Unknown User'}</p>
                  {firstProduct.provider_name && (
                    <p className="text-gray-500 text-sm mb-3">Provider: {firstProduct.provider_name}</p>
                  )}
                </>
            )}
            <div className="flex items-center space-x-3 mb-3">
                <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(listing.status || firstProduct.status)}`}>
                  {listing.status || firstProduct.status || 'PENDING'}
              </span>
                {!isGrouped && (
                  <>
              <span className="px-3 py-1.5 rounded-full text-xs font-semibold bg-blue-100 text-blue-800">
                      {firstProduct.type || 'PRODUCT'}
              </span>
              <span className="px-3 py-1.5 rounded-full text-xs font-semibold bg-gray-100 text-gray-800">
                      {Number(firstProduct.price || 0).toLocaleString()}
              </span>
                  </>
                )}
            </div>
              {!isGrouped && firstProduct.description && (
                <p className="text-sm text-gray-600 line-clamp-2 mb-3">{firstProduct.description}</p>
            )}
          </div>
        </div>
        <div className="flex flex-col space-y-2 ml-4">
          <button
            onClick={() => openListingModal(listing)}
            className="bg-blue-600 text-white px-4 py-2 rounded-xl hover:bg-blue-700 transition-colors text-sm font-medium"
          >
            View Details
          </button>
            {showActions && (listing.status || firstProduct.status) === 'PENDING' && (
            <div className="flex space-x-2">
              <button
                  onClick={() => handleApproval(requestId, 'approve')}
                  disabled={actionLoading === requestId}
                className="bg-green-600 text-white px-3 py-1.5 rounded-lg hover:bg-green-700 transition-colors text-xs font-medium disabled:opacity-50"
              >
                  {actionLoading === requestId ? '...' : 'Approve All'}
              </button>
              <button
                onClick={() => {
                  const reason = prompt('Rejection reason (optional):');
                  if (reason !== null) {
                      handleApproval(requestId, 'reject', reason);
                  }
                }}
                  disabled={actionLoading === requestId}
                className="bg-red-600 text-white px-3 py-1.5 rounded-lg hover:bg-red-700 transition-colors text-xs font-medium disabled:opacity-50"
              >
                  {actionLoading === requestId ? '...' : 'Reject'}
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-purple-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Listings</h2>
          <p className="text-lg text-gray-600">Fetching listing requests...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-4 sm:px-6 py-4 sm:py-6 lg:py-8">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3 sm:gap-4">
            <div>
              <h1 className="text-xl sm:text-2xl lg:text-4xl font-bold text-gray-900 mb-1 sm:mb-2">Listing Management</h1>
              <p className="text-gray-600 text-sm sm:text-base lg:text-lg">Review and approve listing requests</p>
            </div>
            <button 
              onClick={fetchListings}
              className="bg-purple-600 text-white px-4 sm:px-6 py-2.5 sm:py-3 rounded-lg sm:rounded-xl hover:bg-purple-700 transition-all duration-200 flex items-center space-x-2 sm:space-x-3 shadow-lg text-sm sm:text-base"
            >
              <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
              <span className="font-semibold">Refresh</span>
            </button>
          </div>
        </div>
      </div>

      <div className="px-4 sm:px-6 py-4 sm:py-6 lg:py-8">
        {/* Search */}
        <div className="bg-white rounded-xl sm:rounded-2xl p-4 sm:p-6 shadow-lg border border-gray-100 mb-6 sm:mb-8">
          <div className="relative">
            <input
              type="text"
              placeholder="Search listings..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-9 sm:pl-10 pr-3 sm:pr-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
            />
            <svg className="absolute left-3 top-3 sm:top-3.5 w-4 h-4 sm:w-5 sm:h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-xl p-4 mb-6">
            <div className="flex items-center">
              <svg className="w-5 h-5 text-red-600 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-red-800">{error}</p>
            </div>
          </div>
        )}

        {/* Tabs */}
        <div className="bg-white rounded-xl sm:rounded-2xl shadow-lg border border-gray-100 mb-6 sm:mb-8 overflow-x-auto">
          <div className="flex space-x-1 p-1.5 sm:p-2 min-w-max sm:min-w-0">
            {['pending', 'approved', 'rejected', 'all'].map(tab => (
              <button
                key={tab}
                onClick={() => setActiveTab(tab)}
                className={`flex-1 py-2 sm:py-3 px-3 sm:px-4 rounded-lg sm:rounded-xl font-medium sm:font-semibold transition-all text-xs sm:text-sm whitespace-nowrap ${
                  activeTab === tab
                    ? 'bg-purple-600 text-white shadow-md'
                    : 'text-gray-600 hover:bg-gray-100'
                }`}
              >
                {tab.charAt(0).toUpperCase() + tab.slice(1)}
              </button>
            ))}
          </div>
        </div>

        {/* Listings Grid */}
        <div className="space-y-6">
          {getActiveListings().length === 0 ? (
            <div className="bg-white rounded-2xl p-12 text-center shadow-lg border border-gray-100">
              <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 7l-8-4-8 4m16 0l-8 4m8-4v10l-8 4m0-10L4 7m8 4v10M4 7v10l8 4" />
                </svg>
              </div>
              <p className="text-lg font-medium text-gray-600">No listings found</p>
              <p className="text-sm text-gray-500 mt-2">There are no listings with status "{activeTab}"</p>
            </div>
          ) : (
            getActiveListings().map((listing, idx) => (
              <ListingCard key={listing.first_request_id || listing.request_id || listing.group_key || idx} listing={listing} />
            ))
          )}
        </div>
      </div>

      {/* Listing Details Modal */}
      {showListingModal && selectedListing && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-4xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="text-2xl font-bold text-gray-900">Listing Details</h2>
                <button
                  onClick={() => {
                    setShowListingModal(false);
                    setSelectedListing(null);
                  }}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
            </div>
            <div className="p-6 space-y-6">
              {/* Top: Video and Request Meta */}
              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="md:col-span-1">
                  <div className="border border-gray-200 rounded-xl overflow-hidden">
                    {selectedListing.video?.video_url ? (
                      <video
                        src={selectedListing.video.video_url}
                        poster={selectedListing.video.thumbnail_url}
                        controls
                        className="w-full h-48 object-cover bg-black"
                      />
                    ) : selectedListing.video?.thumbnail_url ? (
                      <img
                        src={selectedListing.video.thumbnail_url}
                        alt="Video thumbnail"
                        className="w-full h-48 object-cover bg-gray-100"
                      />
                    ) : (
                      <div className="w-full h-48 bg-gray-100 flex items-center justify-center text-gray-500">
                        No video preview
                </div>
                    )}
                </div>
                  <div className="mt-3 space-y-2">
                    <div className="flex items-center justify-between">
                      <span className="text-sm text-gray-500">Request ID</span>
                      <span className="text-sm font-medium text-gray-900">{selectedListing.request_id}</span>
                </div>
                    <div className="flex items-center justify-between">
                      <span className="text-sm text-gray-500">Status</span>
                      <span className={`px-2 py-0.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(selectedListing.status || 'PENDING')}`}>
                        {selectedListing.status || 'PENDING'}
                  </span>
                </div>
                    {selectedListing.video?.video_status && (
                      <div className="flex items-center justify-between">
                        <span className="text-sm text-gray-500">Video Status</span>
                        <span className="text-sm font-medium text-gray-900">{selectedListing.video.video_status}</span>
                    </div>
                    )}
                    {selectedListing.created_at && (
                      <div className="flex items-center justify-between">
                        <span className="text-sm text-gray-500">Created</span>
                        <span className="text-sm font-medium text-gray-900">
                          {new Date(selectedListing.created_at).toLocaleString()}
                        </span>
                    </div>
                )}
              </div>
                </div>
                {/* Requester info */}
                <div className="md:col-span-2">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    {selectedListing.user && (
                      <div className="bg-gray-50 rounded-xl p-4">
                        <div className="text-sm text-gray-500 mb-1">User</div>
                        <div className="text-lg font-semibold text-gray-900">
                          {selectedListing.user.full_name || selectedListing.user_name}
                        </div>
                        <div className="text-sm text-gray-700">{selectedListing.user.email}</div>
                        {selectedListing.user.phone && (
                          <div className="text-sm text-gray-700">{selectedListing.user.phone}</div>
                        )}
                </div>
              )}
                    {selectedListing.provider && (
                      <div className="bg-gray-50 rounded-xl p-4">
                        <div className="text-sm text-gray-500 mb-1">Provider</div>
                        <div className="text-lg font-semibold text-gray-900">
                          {selectedListing.provider.business_name || selectedListing.provider_name}
                        </div>
                        {selectedListing.provider.provider_type && (
                          <div className="text-sm text-gray-700">Type: {selectedListing.provider.provider_type}</div>
                        )}
                </div>
              )}
                  </div>
                </div>
              </div>

              {/* Products list */}
                <div>
                <h3 className="text-lg font-semibold text-gray-900 mb-3">Products in this request</h3>
                {Array.isArray(selectedListing.products) && selectedListing.products.length > 0 ? (
                  <div className="space-y-3">
                    {selectedListing.products.map((p, idx) => (
                      <div key={p.product_id || idx} className="border border-gray-200 rounded-xl p-4">
                        <div className="flex items-start gap-4">
                          <div className="w-20 h-20 bg-gray-100 rounded-lg overflow-hidden flex-shrink-0">
                            {p.thumbnail ? (
                              <img src={p.thumbnail} alt={p.product_name} className="w-full h-full object-cover" />
                            ) : (
                              <div className="w-full h-full flex items-center justify-center text-gray-400 text-xs">No thumbnail</div>
                            )}
                          </div>
                          <div className="flex-1">
                            <div className="flex items-center justify-between mb-1">
                              <div className="text-base font-semibold text-gray-900">{p.product_name}</div>
                              <div className="flex items-center gap-2">
                                <span className="px-2 py-0.5 rounded-full text-xs font-semibold bg-blue-100 text-blue-800">
                                  {p.type}
                                </span>
                                {p.price != null && (
                                  <span className="px-2 py-0.5 rounded-full text-xs font-semibold bg-gray-100 text-gray-800">
                                    {Number(p.price || 0).toLocaleString()}
                                  </span>
                                )}
                              </div>
                            </div>
                            {p.description && (
                              <p className="text-sm text-gray-700 mb-1">{p.description}</p>
                            )}
                            <div className="flex items-center gap-4 text-sm text-gray-600">
                              {p.category_name && <span>Category: {p.category_name}</span>}
                              {p.link && (
                                <a href={p.link} className="text-blue-600 hover:underline" target="_blank" rel="noopener noreferrer">
                                  Link
                                </a>
                              )}
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                </div>
                ) : (
                  <div className="text-sm text-gray-600">No products found for this request.</div>
              )}
              </div>

              {/* Actions */}
              {(selectedListing.status || 'PENDING') === 'PENDING' && (
                <div className="flex space-x-3 pt-4 border-t">
                  <button
                    onClick={() => handleApproval(selectedListing.request_id, 'approve')}
                    disabled={actionLoading === selectedListing.request_id}
                    className="flex-1 bg-green-600 text-white px-6 py-3 rounded-xl hover:bg-green-700 transition-colors font-semibold disabled:opacity-50"
                  >
                    {actionLoading === selectedListing.request_id ? 'Processing...' : 'Approve All'}
                  </button>
                  <button
                    onClick={() => {
                      const reason = prompt('Rejection reason (optional):');
                      if (reason !== null) {
                        handleApproval(selectedListing.request_id, 'reject', reason);
                      }
                    }}
                    disabled={actionLoading === selectedListing.request_id}
                    className="flex-1 bg-red-600 text-white px-6 py-3 rounded-xl hover:bg-red-700 transition-colors font-semibold disabled:opacity-50"
                  >
                    {actionLoading === selectedListing.request_id ? 'Processing...' : 'Reject'}
                  </button>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ListingManagement;
