import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { ArrowLeft, Eye, Heart, Play, ThumbsUp, Share2, Link as LinkIcon, PackageSearch } from 'lucide-react';
import { listingAPI, wishlistAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const CategoryProducts = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const { isAuthenticated } = useAuth();
  const categoryId = searchParams.get('category_id');
  const categoryName = searchParams.get('category_name') || 'Category';
  
  const [categoryListings, setCategoryListings] = useState([]);
  const [wishlistIds, setWishlistIds] = useState([]);
  const [likedItems, setLikedItems] = useState({});
  const [lovedItems, setLovedItems] = useState({});
  const [engagementCounts, setEngagementCounts] = useState({});
  const [loadingCategory, setLoadingCategory] = useState(true);
  const prefersReducedMotion = window.matchMedia('(prefers-reduced-motion: reduce)').matches;

  // Format price for display
  const formatPrice = (price) => {
    if (!price) return '0';
    return `${parseInt(price).toLocaleString('en-IN')}`;
  };

  // Format view count for display (e.g., 34000 -> "34k")
  const formatViewCount = (count) => {
    if (count >= 1000000) {
      return `${(count / 1000000).toFixed(1)}M`;
    } else if (count >= 1000) {
      return `${(count / 1000).toFixed(0)}k`;
    }
    return count.toString();
  };

  // Load category products
  useEffect(() => {
    if (categoryId) {
      loadCategoryProducts(parseInt(categoryId));
      loadWishlist();
    } else {
      setLoadingCategory(false);
      toast.error('Invalid category');
      navigate('/home');
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [categoryId]);

  // Load wishlist
  const loadWishlist = async () => {
    if (!isAuthenticated) {
      setWishlistIds([]);
      return;
    }

    try {
      const data = await wishlistAPI.getAll();
      if (data && data.wishlist) {
        const ids = data.wishlist.map(item => item.listing_id);
        setWishlistIds(ids);
      } else {
        setWishlistIds([]);
      }
    } catch (error) {
      console.error('Error fetching wishlist:', error);
      setWishlistIds([]);
    }
  };

  const loadCategoryProducts = async (categoryId) => {
    try {
      setLoadingCategory(true);
      const data = await listingAPI.getAll({ category_id: categoryId, limit: 50 });
      
      if (data && data.listings && Array.isArray(data.listings)) {
        setCategoryListings(data.listings);
        // Load engagement statuses for category products
        await loadEngagementStatuses(data.listings);
      } else {
        setCategoryListings([]);
      }
    } catch (error) {
      console.error('Error loading category products:', error);
      setCategoryListings([]);
      toast.error('Failed to load category products');
    } finally {
      setLoadingCategory(false);
    }
  };

  // Load engagement statuses for listings
  const loadEngagementStatuses = async (listings) => {
    if (!isAuthenticated || !listings || listings.length === 0) {
      // Initialize counts from listings data
      const counts = {};
      listings.forEach(listing => {
        counts[listing.listing_id] = {
          likes: listing.like_count || 0,
          loves: listing.love_count || 0,
          clicks: listing.click_count || 0,
          views: listing.view_count || 0
        };
      });
      setEngagementCounts(prev => ({ ...prev, ...counts }));
      return;
    }

    try {
      const statusPromises = listings.map(async (listing) => {
        try {
          const [likeStatus, loveStatus] = await Promise.all([
            listingAPI.getLikeStatus(listing.listing_id),
            listingAPI.getLoveStatus(listing.listing_id)
          ]);
          
          return {
            listingId: listing.listing_id,
            liked: likeStatus.liked || false,
            loved: loveStatus.loved || false,
            likeCount: likeStatus.like_count || listing.like_count || 0,
            loveCount: loveStatus.love_count || listing.love_count || 0,
            clickCount: listing.click_count || 0,
            viewCount: listing.view_count || 0
          };
        } catch (error) {
          console.error(`Error loading status for ${listing.listing_id}:`, error);
          return {
            listingId: listing.listing_id,
            liked: false,
            loved: false,
            likeCount: listing.like_count || 0,
            loveCount: listing.love_count || 0,
            clickCount: listing.click_count || 0,
            viewCount: listing.view_count || 0
          };
        }
      });

      const statuses = await Promise.all(statusPromises);
      
      const newLikedItems = {};
      const newLovedItems = {};
      const newCounts = {};
      
      statuses.forEach(status => {
        newLikedItems[status.listingId] = status.liked;
        newLovedItems[status.listingId] = status.loved;
        newCounts[status.listingId] = {
          likes: status.likeCount,
          loves: status.loveCount,
          clicks: status.clickCount,
          views: status.viewCount
        };
      });
      
      setLikedItems(prev => ({ ...prev, ...newLikedItems }));
      setLovedItems(prev => ({ ...prev, ...newLovedItems }));
      setEngagementCounts(prev => ({ ...prev, ...newCounts }));
    } catch (error) {
      console.error('Error loading engagement statuses:', error);
    }
  };

  const handleCardTap = (e, listing) => {
    if (!prefersReducedMotion && e.currentTarget) {
      e.currentTarget.style.transform = 'scale(0.96)';
      setTimeout(() => {
        if (e.currentTarget) {
          e.currentTarget.style.transform = 'scale(1)';
        }
      }, 150);
    }
    // Navigate to Watch page with video ID
    if (listing?.listing_id) {
      navigate(`/watch?video=${listing.listing_id}`);
    }
  };

  const handleWishlistToggle = async (e, listing) => {
    e.stopPropagation();
    if (!listing?.listing_id) return;

    const isInWishlist = wishlistIds.includes(listing.listing_id);
    
    try {
      if (isInWishlist) {
        await wishlistAPI.remove(listing.listing_id);
        setWishlistIds(wishlistIds.filter(id => id !== listing.listing_id));
        toast.success('Removed from wishlist');
      } else {
        await wishlistAPI.add(listing.listing_id);
        setWishlistIds([...wishlistIds, listing.listing_id]);
        toast.success('Added to wishlist');
      }
    } catch (error) {
      console.error('Error toggling wishlist:', error);
      toast.error(error.response?.data?.error || 'Failed to update wishlist');
    }
  };

  const handleLike = async (e, listing) => {
    e.stopPropagation();
    if (!isAuthenticated) {
      toast.error('Please log in to like products');
      return;
    }
    
    if (!listing?.listing_id) return;
    
    try {
      const isCurrentlyLiked = likedItems[listing.listing_id];
      
      // Optimistically update UI
      setLikedItems(prev => ({
        ...prev,
        [listing.listing_id]: !isCurrentlyLiked
      }));
      
      // Call the appropriate API endpoint
      let response;
      if (isCurrentlyLiked) {
        response = await listingAPI.unlike(listing.listing_id);
      } else {
        response = await listingAPI.like(listing.listing_id);
      }
      
      // Update count with actual response from server
      setEngagementCounts(prev => ({
        ...prev,
        [listing.listing_id]: {
          ...prev[listing.listing_id],
          likes: response.like_count || 0
        }
      }));
      
      toast.success(isCurrentlyLiked ? 'Removed like' : 'Liked!');
    } catch (error) {
      console.error('Error toggling like:', error);
      
      // Revert optimistic update on error
      setLikedItems(prev => ({
        ...prev,
        [listing.listing_id]: !prev[listing.listing_id]
      }));
      
      if (error.response?.status === 401) {
        toast.error('Please log in to like products');
      } else {
        toast.error('Failed to update like');
      }
    }
  };

  const handleLove = async (e, listing) => {
    e.stopPropagation();
    if (!isAuthenticated) {
      toast.error('Please log in to love products');
      return;
    }
    
    if (!listing?.listing_id) return;
    
    try {
      const isCurrentlyLoved = lovedItems[listing.listing_id];
      
      // Optimistically update UI
      setLovedItems(prev => ({
        ...prev,
        [listing.listing_id]: !isCurrentlyLoved
      }));
      
      // Call the appropriate API endpoint
      let response;
      if (isCurrentlyLoved) {
        response = await listingAPI.unlove(listing.listing_id);
      } else {
        response = await listingAPI.love(listing.listing_id);
      }
      
      // Update count with actual response from server
      setEngagementCounts(prev => ({
        ...prev,
        [listing.listing_id]: {
          ...prev[listing.listing_id],
          loves: response.love_count || 0
        }
      }));
      
      toast.success(isCurrentlyLoved ? 'Removed love' : 'Loved!');
    } catch (error) {
      console.error('Error toggling love:', error);
      
      // Revert optimistic update on error
      setLovedItems(prev => ({
        ...prev,
        [listing.listing_id]: !prev[listing.listing_id]
      }));
      
      if (error.response?.status === 401) {
        toast.error('Please log in to love products');
      } else {
        toast.error('Failed to update love');
      }
    }
  };

  const handleClick = async (e, listing) => {
    e.stopPropagation();
    if (!listing?.listing_id) return;
    
    try {
      const response = await listingAPI.click(listing.listing_id);
      
      // Update click count
      setEngagementCounts(prev => ({
        ...prev,
        [listing.listing_id]: {
          ...prev[listing.listing_id],
          clicks: response.click_count || 0
        }
      }));
      
      // Redirect to product link
      if (response.link) {
        window.open(response.link, '_blank', 'noopener,noreferrer');
      } else {
        toast.error('Product link not available');
      }
    } catch (error) {
      console.error('Error handling click:', error);
      toast.error('Failed to open product link');
    }
  };

  const handleShare = async (e, listing) => {
    e.stopPropagation();
    if (!listing?.listing_id) return;
    
    const shareUrl = `${window.location.origin}/watch?video=${listing.listing_id}`;
    
    try {
      if (navigator.share) {
        await navigator.share({
          title: listing.product_name || 'Product',
          text: `Check out ${listing.product_name || 'this product'}`,
          url: shareUrl
        });
      } else {
        // Fallback: copy to clipboard
        await navigator.clipboard.writeText(shareUrl);
        toast.success('Link copied to clipboard!');
      }
    } catch (error) {
      if (error.name !== 'AbortError') {
        // Fallback: copy to clipboard
        try {
          await navigator.clipboard.writeText(shareUrl);
          toast.success('Link copied to clipboard!');
        } catch (clipboardError) {
          toast.error('Failed to share');
        }
      }
    }
  };

  return (
    <div className="min-h-screen bg-white">
      {/* Header Section */}
      <header className="sticky top-0 z-50 bg-white border-b border-gray-200">
        <div className="px-4 py-4 flex items-center gap-3">
          <button
            onClick={() => navigate('/home')}
            className="bg-white border border-gray-200 rounded-full p-2 hover:bg-gray-50 transition-colors"
            aria-label="Go back"
          >
            <ArrowLeft className="w-5 h-5 text-gray-700" />
          </button>
          <h1 
            className="font-bold font-poppins"
            style={{ 
              fontSize: '18px',
              color: '#111827'
            }}
          >
            {categoryName}
          </h1>
        </div>
      </header>

      {/* Category Products Grid */}
      {loadingCategory ? (
        <div className="grid grid-cols-2 gap-4 p-4">
          {[...Array(4)].map((_, i) => (
            <div 
              key={i}
              className="bg-gray-200 animate-pulse rounded-xl"
              style={{ height: '280px' }}
            />
          ))}
        </div>
      ) : categoryListings.length > 0 ? (
        <div className="grid grid-cols-2 gap-4 p-4 pb-32">
          {categoryListings.map((listing) => (
            <div 
              key={`category-${listing.listing_id}`} 
              className="relative group cursor-pointer transition-transform duration-150"
              onClick={(e) => handleCardTap(e, listing)}
              onTouchStart={(e) => handleCardTap(e, listing)}
              role="button"
              tabIndex={0}
              aria-label={`${listing.product_name} - ${formatPrice(listing.price)}`}
            >
              <div 
                className="bg-white rounded-xl overflow-hidden"
                style={{
                  borderRadius: '18px',
                  boxShadow: '0 8px 20px rgba(16, 24, 40, 0.06)'
                }}
              >
                <div 
                  className="relative w-full overflow-hidden bg-gray-800"
                  style={{ height: '180px' }}
                >
                  {listing.video_url ? (
                    <div className="relative w-full h-full bg-gray-800">
                      <video
                        src={listing.video_url}
                        poster={listing.thumbnail_url}
                        className="w-full h-full object-cover"
                        preload="metadata"
                        muted
                        playsInline
                        onError={(e) => {
                          console.error('Video error:', e);
                          e.target.style.display = 'none';
                        }}
                      />
                      {/* Play Button Overlay */}
                      <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                        <div 
                          className="bg-gray-100 rounded-full p-2.5 shadow-xl border-2 border-[#4B2BD6]"
                          style={{
                            width: '48px',
                            height: '48px',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                          }}
                        >
                          <Play className="w-5 h-5 text-[#4B2BD6]" strokeWidth={2.5} />
                        </div>
                      </div>
                    </div>
                  ) : listing.thumbnail_url ? (
                    <img 
                      src={listing.thumbnail_url} 
                      alt={listing.product_name}
                      className="w-full h-full object-cover"
                      onError={(e) => {
                        e.target.src = 'https://via.placeholder.com/280x180?text=No+Image';
                      }}
                    />
                  ) : (
                    <div className="w-full h-full flex items-center justify-center bg-gray-800">
                      <span className="text-gray-400 text-sm">No Image</span>
                    </div>
                  )}
                  
                  {/* Dark Gradient Overlay */}
                  <div 
                    className="absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t from-black/70 to-transparent pointer-events-none"
                  ></div>
                  
                  {/* View Count Badge */}
                  {(engagementCounts[listing.listing_id]?.views || listing.view_count || 0) > 0 && (
                    <div 
                      className="absolute top-2 left-2 flex items-center gap-1 px-1.5 py-1 rounded z-10"
                      style={{
                        background: '#1E3A8A',
                        boxShadow: '0 2px 4px rgba(0, 0, 0, 0.3)'
                      }}
                    >
                      <Eye className="w-2.5 h-2.5 text-white" />
                      <span 
                        className="text-white font-poppins font-medium"
                        style={{ fontSize: '10px' }}
                      >
                        {formatViewCount(engagementCounts[listing.listing_id]?.views || listing.view_count || 0)}
                      </span>
                    </div>
                  )}
                  
                  {/* Wishlist Button */}
                  <button
                    onClick={(e) => handleWishlistToggle(e, listing)}
                    className="absolute top-2 right-2 w-7 h-7 rounded-lg bg-white flex items-center justify-center shadow-md hover:bg-gray-50 transition-all z-10"
                    style={{ minWidth: '28px', minHeight: '28px' }}
                    aria-label={wishlistIds.includes(listing.listing_id) ? 'Remove from wishlist' : 'Add to wishlist'}
                  >
                    <Heart 
                      className={`w-4 h-4 ${wishlistIds.includes(listing.listing_id) ? 'fill-red-500 text-red-500' : 'text-gray-700'}`}
                    />
                  </button>

                  {/* Interactive Buttons Bar */}
                  <div className="absolute bottom-2 right-2 flex items-center gap-1 z-10">
                    <button
                      onClick={(e) => handleLike(e, listing)}
                      className="bg-white/90 backdrop-blur-sm rounded-full p-1.5 shadow-md hover:bg-white transition-all flex items-center justify-center"
                      style={{ width: '28px', height: '28px' }}
                      aria-label="Like"
                    >
                      <ThumbsUp 
                        className={`w-3.5 h-3.5 transition-colors duration-300 ${
                          likedItems[listing.listing_id] 
                            ? 'text-blue-500 fill-current' 
                            : 'text-blue-400'
                        }`}
                      />
                    </button>

                    <button
                      onClick={(e) => handleClick(e, listing)}
                      className="bg-white/90 backdrop-blur-sm rounded-full p-1.5 shadow-md hover:bg-white transition-all flex items-center justify-center"
                      style={{ width: '28px', height: '28px' }}
                      aria-label="Open product link"
                    >
                      <LinkIcon className="w-3.5 h-3.5 text-gray-700" />
                    </button>

                    <button
                      onClick={(e) => handleLove(e, listing)}
                      className="bg-white/90 backdrop-blur-sm rounded-full p-1.5 shadow-md hover:bg-white transition-all flex items-center justify-center"
                      style={{ width: '28px', height: '28px' }}
                      aria-label="Love"
                    >
                      <Heart 
                        className={`w-3.5 h-3.5 transition-colors duration-300 ${
                          lovedItems[listing.listing_id] 
                            ? 'text-pink-500 fill-current' 
                            : 'text-pink-400'
                        }`}
                      />
                    </button>

                    <button
                      onClick={(e) => handleShare(e, listing)}
                      className="bg-white/90 backdrop-blur-sm rounded-full p-1.5 shadow-md hover:bg-white transition-all flex items-center justify-center"
                      style={{ width: '28px', height: '28px' }}
                      aria-label="Share"
                    >
                      <Share2 className="w-3.5 h-3.5 text-gray-700" />
                    </button>
                  </div>
                  
                  {/* Product Info */}
                  <div className="absolute bottom-0 left-0 right-0 p-2.5 z-10" style={{ paddingBottom: '42px' }}>
                    <h3 
                      className="font-poppins font-semibold mb-0.5 line-clamp-2 text-white"
                      style={{ 
                        fontSize: '12px',
                        lineHeight: '1.3',
                        fontWeight: '600',
                        textShadow: '0 1px 3px rgba(0, 0, 0, 0.5)'
                      }}
                    >
                      {listing.product_name}
                    </h3>
                    <div 
                      className="inline-block px-2 py-1 rounded mt-0.5 mb-0.5"
                      style={{
                        background: '#FFFFFF',
                        boxShadow: '0 2px 8px rgba(0, 0, 0, 0.15)',
                        borderRadius: '6px'
                      }}
                    >
                      <span 
                        className="font-poppins font-semibold"
                        style={{ 
                          color: '#111827',
                          fontSize: '11px'
                        }}
                      >
                        {formatPrice(listing.price)}
                      </span>
                    </div>
                    <p 
                      className="font-poppins text-white/90 line-clamp-3"
                      style={{ 
                        fontSize: '10px',
                        textShadow: '0 1px 2px rgba(0, 0, 0, 0.5)',
                        marginTop: '2px'
                      }}
                    >
                      {listing.description || 'No description available'}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="flex flex-col items-center justify-center py-16 px-4">
          <div className="w-24 h-24 bg-gray-100 rounded-full flex items-center justify-center mb-4">
            <PackageSearch className="w-12 h-12 text-gray-400" />
          </div>
          <h3 className="text-xl font-bold text-gray-900 mb-2 font-poppins">
            No Products Found
          </h3>
          <p className="text-gray-600 text-center font-poppins">
            There are no products available in this category yet.
          </p>
        </div>
      )}
    </div>
  );
};

export default CategoryProducts;

