import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';

const InfluencerManagement = () => {
  const [activeTab, setActiveTab] = useState('pending');
  const [influencers, setInfluencers] = useState([]);
  const [pendingInfluencers, setPendingInfluencers] = useState([]);
  const [approvedInfluencers, setApprovedInfluencers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedInfluencer, setSelectedInfluencer] = useState(null);
  const [showInfluencerModal, setShowInfluencerModal] = useState(false);
  const [actionLoading, setActionLoading] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    fetchInfluencers();
  }, []);

  const fetchInfluencers = async () => {
    try {
      setLoading(true);
      const response = await adminAPI.getInfluencers();
      
      if (response.ok) {
        const data = response.data;
        setInfluencers(data.influencers || []);
        setPendingInfluencers(data.pending || []);
        setApprovedInfluencers(data.approved || []);
      } else {
        setError(response.message || 'Failed to load influencers');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleApproval = async (influencerId, action, reason = '', requestId = null) => {
    try {
      setActionLoading(influencerId);
      const requestData = { action, reason };
      if (requestId) {
        requestData.request_id = requestId;
      }
      const response = await adminAPI.approveInfluencer(influencerId, action, reason, requestId);
      
      if (response.ok) {
        await fetchInfluencers();
        if (selectedInfluencer && selectedInfluencer.id === influencerId) {
          setShowInfluencerModal(false);
          setSelectedInfluencer(null);
        }
      } else {
        setError(response.message || 'Failed to update influencer status');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setActionLoading(null);
    }
  };

  const handleSubscriptionUpdate = async (influencerId, subscriptionData) => {
    try {
      setActionLoading(influencerId);
      const response = await adminAPI.updateInfluencerSubscription(influencerId, subscriptionData);
      
      if (response.ok) {
        await fetchInfluencers();
      } else {
        setError(response.message || 'Failed to update subscription');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setActionLoading(null);
    }
  };

  const openInfluencerModal = async (influencer) => {
    try {
      const response = await adminAPI.getInfluencerDetails(influencer.id);
      if (response.ok) {
        setSelectedInfluencer(response.data);
        setShowInfluencerModal(true);
      } else {
        setError(response.message || 'Failed to load influencer details');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'APPROVED': return 'bg-green-100 text-green-800';
      case 'PENDING': return 'bg-yellow-100 text-yellow-800';
      case 'REJECTED': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getSubscriptionBadgeColor = (subscription) => {
    if (subscription && subscription.is_active) {
      return 'bg-green-100 text-green-800';
    }
    return 'bg-gray-100 text-gray-800';
  };

  const filteredInfluencers = (list) => {
    if (!searchTerm) return list;
    return list.filter(influencer => 
      influencer.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      influencer.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      influencer.business_name?.toLowerCase().includes(searchTerm.toLowerCase())
    );
  };

  const InfluencerCard = ({ influencer, showActions = true }) => (
    <div className="bg-white rounded-xl sm:rounded-2xl p-4 sm:p-6 shadow-lg border border-gray-100 hover:shadow-xl transition-all duration-300">
      <div className="flex flex-col sm:flex-row sm:items-start gap-4">
        <div className="flex items-start space-x-3 sm:space-x-4 flex-1">
          <div className="w-12 h-12 sm:w-16 sm:h-16 bg-purple-600 rounded-xl sm:rounded-2xl flex items-center justify-center text-white text-sm sm:text-lg font-bold shadow-md flex-shrink-0">
            {influencer.name?.charAt(0) || 'I'}
          </div>
          <div className="flex-1 min-w-0">
            <h3 className="text-base sm:text-xl font-bold text-gray-900 mb-0.5 sm:mb-1 truncate">{influencer.name || 'No Name'}</h3>
            <p className="text-gray-600 text-sm mb-1 sm:mb-2 truncate">{influencer.email}</p>
            {influencer.business_name && (
              <p className="text-gray-500 text-xs sm:text-sm mb-2 sm:mb-3 truncate">{influencer.business_name}</p>
            )}
            <div className="flex flex-wrap items-center gap-2 mb-3">
              <span className={`px-2 sm:px-3 py-1 sm:py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(influencer.status)}`}>
                {influencer.status || 'PENDING'}
              </span>
              <span className={`px-2 sm:px-3 py-1 sm:py-1.5 rounded-full text-xs font-semibold ${getSubscriptionBadgeColor(influencer.subscription)}`}>
                {influencer.subscription?.is_active ? 'Subscribed' : 'No Sub'}
              </span>
            </div>
            <div className="grid grid-cols-3 gap-2 sm:gap-4 text-center">
              <div className="bg-purple-50 rounded-lg p-2">
                <p className="text-lg sm:text-2xl font-bold text-purple-600">{influencer.total_videos || 0}</p>
                <p className="text-xs text-gray-500">Videos</p>
              </div>
              <div className="bg-blue-50 rounded-lg p-2">
                <p className="text-lg sm:text-2xl font-bold text-blue-600">{influencer.total_engagement || 0}</p>
                <p className="text-xs text-gray-500">Engmnt</p>
              </div>
              <div className="bg-green-50 rounded-lg p-2">
                <p className="text-lg sm:text-2xl font-bold text-green-600">${influencer.total_earnings || 0}</p>
                <p className="text-xs text-gray-500">Earnings</p>
              </div>
            </div>
          </div>
        </div>
        <div className="flex sm:flex-col gap-2 sm:space-y-2">
          <button
            onClick={() => openInfluencerModal(influencer)}
            className="flex-1 sm:flex-none bg-blue-600 text-white px-3 sm:px-4 py-2 rounded-lg sm:rounded-xl hover:bg-blue-700 transition-colors text-xs sm:text-sm font-medium"
          >
            View Details
          </button>
                            {showActions && influencer.status === 'PENDING' && (
                    <div className="flex space-x-2">
                      <button
                        onClick={() => handleApproval(influencer.id, 'approve', '', influencer.request_id || null)}
                        disabled={actionLoading === influencer.id}
                        className="bg-green-600 text-white px-3 py-1.5 rounded-lg hover:bg-green-700 transition-colors text-xs font-medium disabled:opacity-50"
                      >
                        {actionLoading === influencer.id ? '...' : 'Approve'}
                      </button>
                      <button
                        onClick={() => {
                          const reason = prompt('Rejection reason (optional):');
                          if (reason !== null) {
                            handleApproval(influencer.id, 'reject', reason, influencer.request_id || null);
                          }
                        }}
                        disabled={actionLoading === influencer.id}
                        className="bg-red-600 text-white px-3 py-1.5 rounded-lg hover:bg-red-700 transition-colors text-xs font-medium disabled:opacity-50"
                      >
                        {actionLoading === influencer.id ? '...' : 'Reject'}
                      </button>
                    </div>
                  )}
        </div>
      </div>
    </div>
  );

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-purple-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Influencers</h2>
          <p className="text-lg text-gray-600">Fetching influencer data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-4 sm:px-6 py-4 sm:py-6 lg:py-8">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3 sm:gap-4">
            <div>
              <h1 className="text-xl sm:text-2xl lg:text-4xl font-bold text-gray-900 mb-1 sm:mb-2">Influencer Management</h1>
              <p className="text-gray-600 text-sm sm:text-base lg:text-lg">Manage influencer approvals, subscriptions, and performance</p>
            </div>
            <div className="flex items-center">
              <button 
                onClick={fetchInfluencers}
                className="bg-purple-600 text-white px-4 sm:px-6 py-2.5 sm:py-3 rounded-lg sm:rounded-xl hover:bg-purple-700 transition-all duration-200 flex items-center space-x-2 sm:space-x-3 shadow-lg text-sm sm:text-base"
              >
                <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                <span className="font-semibold">Refresh</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="px-4 sm:px-6 py-4 sm:py-6 lg:py-8">
        {/* Search */}
        <div className="bg-white rounded-xl sm:rounded-2xl p-4 sm:p-6 shadow-lg border border-gray-100 mb-6 sm:mb-8">
          <div className="relative">
            <input
              type="text"
              placeholder="Search influencers..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-9 sm:pl-10 pr-3 sm:pr-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
            />
            <svg className="absolute left-3 top-3 sm:top-3.5 w-4 h-4 sm:w-5 sm:h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-xl p-4 mb-6">
            <div className="flex items-center">
              <svg className="w-5 h-5 text-red-600 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-red-800 font-medium">{error}</p>
            </div>
          </div>
        )}

        {/* Tabs */}
        <div className="bg-white rounded-xl sm:rounded-2xl shadow-lg border border-gray-100 overflow-hidden mb-6 sm:mb-8">
          <div className="border-b border-gray-200 overflow-x-auto">
            <nav className="flex min-w-max">
              <button
                onClick={() => setActiveTab('pending')}
                className={`px-3 sm:px-6 py-3 sm:py-4 text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                  activeTab === 'pending'
                    ? 'bg-purple-600 text-white border-b-2 border-purple-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Pending ({filteredInfluencers(pendingInfluencers).length})
              </button>
              <button
                onClick={() => setActiveTab('approved')}
                className={`px-3 sm:px-6 py-3 sm:py-4 text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                  activeTab === 'approved'
                    ? 'bg-purple-600 text-white border-b-2 border-purple-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Approved ({filteredInfluencers(approvedInfluencers).length})
              </button>
              <button
                onClick={() => setActiveTab('all')}
                className={`px-3 sm:px-6 py-3 sm:py-4 text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                  activeTab === 'all'
                    ? 'bg-purple-600 text-white border-b-2 border-purple-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                All ({filteredInfluencers(influencers).length})
              </button>
            </nav>
          </div>

          <div className="p-6">
            {activeTab === 'pending' && (
              <div className="space-y-6">
                {filteredInfluencers(pendingInfluencers).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No pending influencers</p>
                    <p className="text-sm text-gray-500 mt-2">All influencer applications have been processed</p>
                  </div>
                ) : (
                  filteredInfluencers(pendingInfluencers).map((influencer) => (
                    <InfluencerCard key={influencer.id} influencer={influencer} showActions={true} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'approved' && (
              <div className="space-y-6">
                {filteredInfluencers(approvedInfluencers).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No approved influencers</p>
                    <p className="text-sm text-gray-500 mt-2">Approved influencers will appear here</p>
                  </div>
                ) : (
                  filteredInfluencers(approvedInfluencers).map((influencer) => (
                    <InfluencerCard key={influencer.id} influencer={influencer} showActions={false} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'all' && (
              <div className="space-y-6">
                {filteredInfluencers(influencers).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No influencers found</p>
                    <p className="text-sm text-gray-500 mt-2">Try adjusting your search criteria</p>
                  </div>
                ) : (
                  filteredInfluencers(influencers).map((influencer) => (
                    <InfluencerCard key={influencer.id} influencer={influencer} showActions={false} />
                  ))
                )}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Influencer Details Modal */}
      {showInfluencerModal && selectedInfluencer && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowInfluencerModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-4xl sm:w-full">
              <div className="bg-white px-6 py-8">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-2xl font-bold text-gray-900">Influencer Details</h3>
                  <button
                    onClick={() => setShowInfluencerModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                  {/* Profile Information */}
                  <div className="space-y-6">
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Profile Information</h4>
                      <div className="space-y-3">
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Name</label>
                          <p className="text-gray-900">{selectedInfluencer.name || 'Not provided'}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Email</label>
                          <p className="text-gray-900">{selectedInfluencer.email}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Business Name</label>
                          <p className="text-gray-900">{selectedInfluencer.business_name || 'Not provided'}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Status</label>
                          <span className={`inline-block px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(selectedInfluencer.status)}`}>
                            {selectedInfluencer.status || 'PENDING'}
                          </span>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Applied</label>
                          <p className="text-gray-900">{selectedInfluencer.created_at ? new Date(selectedInfluencer.created_at).toLocaleString() : 'N/A'}</p>
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* Performance Statistics */}
                  <div className="space-y-6">
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Performance Statistics</h4>
                      <div className="grid grid-cols-2 gap-4">
                        <div className="bg-purple-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-purple-600">{selectedInfluencer.total_videos || 0}</p>
                          <p className="text-purple-700 text-sm font-medium">Total Videos</p>
                        </div>
                        <div className="bg-blue-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-blue-600">{selectedInfluencer.total_engagement || 0}</p>
                          <p className="text-blue-700 text-sm font-medium">Total Engagement</p>
                        </div>
                        <div className="bg-green-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-green-600">${selectedInfluencer.total_earnings || 0}</p>
                          <p className="text-green-700 text-sm font-medium">Total Earnings</p>
                        </div>
                        <div className="bg-yellow-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-yellow-600">{selectedInfluencer.avg_engagement_rate || 0}%</p>
                          <p className="text-yellow-700 text-sm font-medium">Avg Engagement Rate</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Action Buttons */}
                <div className="flex items-center justify-end space-x-4 mt-8 pt-6 border-t border-gray-200">
                  {selectedInfluencer.status === 'PENDING' && (
                    <>
                      <button
                        onClick={() => handleApproval(selectedInfluencer.id, 'approve', '', selectedInfluencer.request_id || null)}
                        disabled={actionLoading === selectedInfluencer.id}
                        className="bg-green-600 text-white px-6 py-3 rounded-xl hover:bg-green-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedInfluencer.id ? 'Processing...' : 'Approve Influencer'}
                      </button>
                      <button
                        onClick={() => {
                          const reason = prompt('Rejection reason (optional):');
                          if (reason !== null) {
                            handleApproval(selectedInfluencer.id, 'reject', reason, selectedInfluencer.request_id || null);
                          }
                        }}
                        disabled={actionLoading === selectedInfluencer.id}
                        className="bg-red-600 text-white px-6 py-3 rounded-xl hover:bg-red-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedInfluencer.id ? 'Processing...' : 'Reject Influencer'}
                      </button>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default InfluencerManagement;
