import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';

const PromotionManagement = () => {
  const [activeTab, setActiveTab] = useState('pending');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  // Submissions state
  const [submissions, setSubmissions] = useState([]);
  const [showReviewModal, setShowReviewModal] = useState(false);
  const [selectedSubmission, setSelectedSubmission] = useState(null);
  const [reviewForm, setReviewForm] = useState({
    status: 'APPROVED',
    admin_remarks: '',
    concession_amount: '',
    concession_type: 'DISCOUNT'
  });

  // Analytics state
  const [analytics, setAnalytics] = useState({
    total_submissions: 0,
    pending_count: 0,
    approved_count: 0,
    rejected_count: 0,
    total_concessions: 0
  });

  useEffect(() => {
    fetchAllData();
  }, []);

  const fetchAllData = async () => {
    try {
      setLoading(true);
      await Promise.all([
        fetchSubmissions(),
        fetchAnalytics()
      ]);
    } catch (err) {
      setError('Failed to load promotion data');
    } finally {
      setLoading(false);
    }
  };

  const fetchSubmissions = async () => {
    try {
      const response = await adminAPI.getPromotionSubmissions();
      if (response.ok) {
        setSubmissions(response.data.submissions || []);
      }
    } catch (err) {
      console.error('Failed to fetch submissions:', err);
    }
  };

  const fetchAnalytics = async () => {
    try {
      const response = await adminAPI.getPromotionAnalytics();
      if (response.ok) {
        setAnalytics(response.data.analytics || analytics);
      }
    } catch (err) {
      console.error('Failed to fetch analytics:', err);
    }
  };

  const handleReviewSubmission = (submission) => {
    setSelectedSubmission(submission);
    setReviewForm({
      status: 'APPROVED',
      admin_remarks: '',
      concession_amount: '',
      concession_type: 'DISCOUNT'
    });
    setShowReviewModal(true);
  };

  const handleReviewSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await adminAPI.reviewPromotionSubmission(
        selectedSubmission.submission_id,
        reviewForm
      );
      
      if (response.ok) {
        await fetchSubmissions();
        await fetchAnalytics();
        setShowReviewModal(false);
        setSelectedSubmission(null);
        setReviewForm({
          status: 'APPROVED',
          admin_remarks: '',
          concession_amount: '',
          concession_type: 'DISCOUNT'
        });
      } else {
        setError(response.message || 'Failed to review submission');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'APPROVED': return 'bg-green-100 text-green-800';
      case 'PENDING': return 'bg-yellow-100 text-yellow-800';
      case 'REJECTED': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getPlatformIcon = (platform) => {
    const icons = {
      'INSTAGRAM': '',
      'FACEBOOK': '',
      'TWITTER': '',
      'TIKTOK': '',
      'YOUTUBE': '',
      'LINKEDIN': '',
      'OTHER': ''
    };
    return icons[platform] || '';
  };

  const getSubmissionTypeIcon = (type) => {
    const icons = {
      'POST': '',
      'VIDEO': '',
      'STORY': '',
      'REEL': ''
    };
    return icons[type] || '';
  };

  const filteredSubmissions = submissions.filter(submission => {
    if (activeTab === 'all') return true;
    return submission.status === activeTab.toUpperCase();
  });

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-blue-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Promotion Data</h2>
          <p className="text-lg text-gray-600">Fetching promotion submissions...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-6 py-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-4xl font-bold text-gray-900 mb-2">Promotion Approval System</h1>
              <p className="text-gray-600 text-lg">Review and approve influencer promotion submissions</p>
            </div>
            <button 
              onClick={fetchAllData}
              className="bg-blue-600 text-white px-6 py-3 rounded-xl hover:bg-blue-700 transition-all duration-200 flex items-center space-x-3 shadow-lg hover:shadow-xl transform hover:scale-105"
            >
              <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
              <span className="font-semibold">Refresh</span>
            </button>
          </div>
        </div>
      </div>

      <div className="px-6 py-8">
        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-xl p-4 mb-6">
            <div className="flex items-center">
              <svg className="w-5 h-5 text-red-600 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-red-800 font-medium">{error}</p>
            </div>
          </div>
        )}

        {/* Analytics Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6 mb-8">
          <div className="bg-white rounded-xl p-6 shadow-lg border border-gray-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Submissions</p>
                <p className="text-2xl font-bold text-gray-900">{analytics.total_submissions}</p>
              </div>
              <div className="w-12 h-12 bg-blue-100 rounded-xl flex items-center justify-center">
                <svg className="w-6 h-6 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl p-6 shadow-lg border border-gray-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Pending Review</p>
                <p className="text-2xl font-bold text-yellow-600">{analytics.pending_count}</p>
              </div>
              <div className="w-12 h-12 bg-yellow-100 rounded-xl flex items-center justify-center">
                <svg className="w-6 h-6 text-yellow-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl p-6 shadow-lg border border-gray-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Approved</p>
                <p className="text-2xl font-bold text-green-600">{analytics.approved_count}</p>
              </div>
              <div className="w-12 h-12 bg-green-100 rounded-xl flex items-center justify-center">
                <svg className="w-6 h-6 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl p-6 shadow-lg border border-gray-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Rejected</p>
                <p className="text-2xl font-bold text-red-600">{analytics.rejected_count}</p>
              </div>
              <div className="w-12 h-12 bg-red-100 rounded-xl flex items-center justify-center">
                <svg className="w-6 h-6 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-xl p-6 shadow-lg border border-gray-100">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Concessions</p>
                <p className="text-2xl font-bold text-purple-600">{analytics.total_concessions}</p>
              </div>
              <div className="w-12 h-12 bg-purple-100 rounded-xl flex items-center justify-center">
                <svg className="w-6 h-6 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1" />
                </svg>
              </div>
            </div>
          </div>
        </div>

        {/* Main Content */}
        <div className="bg-white rounded-2xl shadow-lg border border-gray-100 overflow-hidden">
          {/* Tabs */}
          <div className="border-b border-gray-200">
            <nav className="flex">
              <button
                onClick={() => setActiveTab('pending')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'pending'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Pending Review ({analytics.pending_count})
              </button>
              <button
                onClick={() => setActiveTab('approved')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'approved'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Approved ({analytics.approved_count})
              </button>
              <button
                onClick={() => setActiveTab('rejected')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'rejected'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Rejected ({analytics.rejected_count})
              </button>
              <button
                onClick={() => setActiveTab('all')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'all'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                All Submissions ({analytics.total_submissions})
              </button>
            </nav>
          </div>

          <div className="p-6">
            {/* Submissions List */}
            <div className="space-y-4">
              {filteredSubmissions.length === 0 ? (
                <div className="text-center py-12">
                  <svg className="w-16 h-16 text-gray-400 mx-auto mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                  </svg>
                  <h3 className="text-lg font-medium text-gray-900 mb-2">No submissions found</h3>
                  <p className="text-gray-600">No promotion submissions match the current filter.</p>
                </div>
              ) : (
                filteredSubmissions.map((submission) => (
                  <div key={submission.submission_id} className="bg-gray-50 rounded-xl p-6 border border-gray-200">
                    <div className="flex items-start justify-between">
                      <div className="flex-1">
                        <div className="flex items-center space-x-3 mb-3">
                          <span className="text-2xl">{getPlatformIcon(submission.platform)}</span>
                          <span className="text-lg">{getSubmissionTypeIcon(submission.submission_type)}</span>
                          <h3 className="text-lg font-semibold text-gray-900">{submission.user_name}</h3>
                          <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusBadgeColor(submission.status)}`}>
                            {submission.status}
                          </span>
                        </div>
                        
                        <p className="text-gray-600 mb-3">{submission.content_description}</p>
                        
                        <div className="flex items-center space-x-6 text-sm text-gray-500 mb-3">
                          <span>Platform: {submission.platform}</span>
                          <span>Type: {submission.submission_type}</span>
                          <span>Submitted: {new Date(submission.submission_date).toLocaleDateString()}</span>
                        </div>
                        
                        {submission.engagement_metrics && (
                          <div className="flex items-center space-x-4 text-sm">
                            <span className="text-gray-600">Engagement:</span>
                            {submission.engagement_metrics.likes && (
                              <span className="text-red-600"> {submission.engagement_metrics.likes}</span>
                            )}
                            {submission.engagement_metrics.comments && (
                              <span className="text-blue-600"> {submission.engagement_metrics.comments}</span>
                            )}
                            {submission.engagement_metrics.shares && (
                              <span className="text-green-600"> {submission.engagement_metrics.shares}</span>
                            )}
                            {submission.engagement_metrics.reach && (
                              <span className="text-purple-600"> {submission.engagement_metrics.reach}</span>
                            )}
                          </div>
                        )}
                        
                        {submission.admin_remarks && (
                          <div className="mt-3 p-3 bg-blue-50 rounded-lg">
                            <p className="text-sm text-blue-800">
                              <strong>Admin Remarks:</strong> {submission.admin_remarks}
                            </p>
                          </div>
                        )}
                        
                        {submission.concession_amount > 0 && (
                          <div className="mt-3 p-3 bg-green-50 rounded-lg">
                            <p className="text-sm text-green-800">
                              <strong>Concession:</strong> {submission.concession_amount} ({submission.concession_type})
                            </p>
                          </div>
                        )}
                      </div>
                      
                      <div className="flex flex-col space-y-2 ml-4">
                        <a
                          href={submission.content_url}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="text-blue-600 hover:text-blue-800 text-sm font-medium"
                        >
                          View Content
                        </a>
                        {submission.screenshot_url && (
                          <a
                            href={submission.screenshot_url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="text-green-600 hover:text-green-800 text-sm font-medium"
                          >
                            View Screenshot
                          </a>
                        )}
                        {submission.status === 'PENDING' && (
                          <button
                            onClick={() => handleReviewSubmission(submission)}
                            className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium"
                          >
                            Review
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Review Modal */}
      {showReviewModal && selectedSubmission && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowReviewModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl sm:w-full">
              <div className="bg-white px-6 py-8">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-2xl font-bold text-gray-900">Review Promotion Submission</h3>
                  <button
                    onClick={() => setShowReviewModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <div className="mb-6 p-4 bg-gray-50 rounded-xl">
                  <h4 className="font-semibold text-gray-900 mb-2">Submission Details</h4>
                  <p className="text-gray-600 mb-2">{selectedSubmission.content_description}</p>
                  <div className="flex items-center space-x-4 text-sm text-gray-500">
                    <span>{getPlatformIcon(selectedSubmission.platform)} {selectedSubmission.platform}</span>
                    <span>{getSubmissionTypeIcon(selectedSubmission.submission_type)} {selectedSubmission.submission_type}</span>
                    <span>By: {selectedSubmission.user_name}</span>
                  </div>
                </div>

                <form onSubmit={handleReviewSubmit} className="space-y-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Review Decision *</label>
                    <select
                      required
                      value={reviewForm.status}
                      onChange={(e) => setReviewForm({...reviewForm, status: e.target.value})}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="APPROVED">Approve</option>
                      <option value="REJECTED">Reject</option>
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Admin Remarks *</label>
                    <textarea
                      required
                      rows={4}
                      value={reviewForm.admin_remarks}
                      onChange={(e) => setReviewForm({...reviewForm, admin_remarks: e.target.value})}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      placeholder="Add your review comments..."
                    />
                  </div>

                  {reviewForm.status === 'APPROVED' && (
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Concession Type</label>
                        <select
                          value={reviewForm.concession_type}
                          onChange={(e) => setReviewForm({...reviewForm, concession_type: e.target.value})}
                          className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        >
                          <option value="DISCOUNT">Discount</option>
                          <option value="CREDIT">Credit</option>
                          <option value="FREE_MONTHS">Free Months</option>
                          <option value="UPGRADE">Upgrade</option>
                        </select>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Concession Amount ()</label>
                        <input
                          type="number"
                          min="0"
                          step="0.01"
                          value={reviewForm.concession_amount}
                          onChange={(e) => setReviewForm({...reviewForm, concession_amount: e.target.value})}
                          className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                          placeholder="Enter concession amount"
                        />
                      </div>
                    </div>
                  )}

                  <div className="flex items-center justify-end space-x-4 pt-6 border-t border-gray-200">
                    <button
                      type="button"
                      onClick={() => setShowReviewModal(false)}
                      className="px-6 py-3 border border-gray-300 rounded-xl text-gray-700 hover:bg-gray-50 transition-colors font-semibold"
                    >
                      Cancel
                    </button>
                    <button
                      type="submit"
                      className="bg-blue-600 text-white px-6 py-3 rounded-xl hover:bg-blue-700 transition-colors font-semibold"
                    >
                      Submit Review
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default PromotionManagement;
