import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';

const BrandManagement = () => {
  const [activeTab, setActiveTab] = useState('pending');
  const [brands, setBrands] = useState([]);
  const [pendingBrands, setPendingBrands] = useState([]);
  const [approvedBrands, setApprovedBrands] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedBrand, setSelectedBrand] = useState(null);
  const [showBrandModal, setShowBrandModal] = useState(false);
  const [actionLoading, setActionLoading] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    fetchBrands();
  }, []);

  const fetchBrands = async () => {
    try {
      setLoading(true);
      const response = await adminAPI.getBrands();
      
      if (response.ok) {
        const data = response.data;
        setBrands(data.brands || []);
        setPendingBrands(data.pending || []);
        setApprovedBrands(data.approved || []);
      } else {
        setError(response.message || 'Failed to load brands');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleApproval = async (brandId, action, reason = '', requestId = null) => {
    try {
      setActionLoading(brandId);
      const response = await adminAPI.approveBrand(brandId, action, reason, requestId);
      
      if (response.ok) {
        await fetchBrands();
        if (selectedBrand && selectedBrand.id === brandId) {
          setShowBrandModal(false);
          setSelectedBrand(null);
        }
      } else {
        setError(response.message || 'Failed to update brand status');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setActionLoading(null);
    }
  };

  const handleVideoAction = async (brandId, videoId, action, reason = '') => {
    try {
      setActionLoading(`${brandId}-${videoId}`);
      const response = await adminAPI.approveVideo(videoId, action, reason);
      
      if (response.ok) {
        await fetchBrands();
      } else {
        setError(response.message || 'Failed to update video status');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setActionLoading(null);
    }
  };

  const openBrandModal = async (brand) => {
    try {
      const response = await adminAPI.getBrandDetails(brand.id);
      if (response.ok) {
        setSelectedBrand(response.data);
        setShowBrandModal(true);
      } else {
        setError(response.message || 'Failed to load brand details');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'APPROVED': return 'bg-green-100 text-green-800';
      case 'PENDING': return 'bg-yellow-100 text-yellow-800';
      case 'REJECTED': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getVideoStatusBadgeColor = (status) => {
    switch (status) {
      case 'APPROVED': return 'bg-green-100 text-green-800';
      case 'PENDING': return 'bg-yellow-100 text-yellow-800';
      case 'FLAGGED': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const filteredBrands = (list) => {
    if (!searchTerm) return list;
    return list.filter(brand => 
      brand.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      brand.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      brand.business_name?.toLowerCase().includes(searchTerm.toLowerCase())
    );
  };

  const BrandCard = ({ brand, showActions = true }) => (
    <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-100 hover:shadow-xl transition-all duration-300">
      <div className="flex items-start justify-between">
        <div className="flex items-start space-x-4">
          <div className="w-16 h-16 bg-blue-600 rounded-2xl flex items-center justify-center text-white text-lg font-bold shadow-md">
            {brand.name?.charAt(0) || 'B'}
          </div>
          <div className="flex-1">
            <h3 className="text-xl font-bold text-gray-900 mb-1">{brand.name || 'No Name'}</h3>
            <p className="text-gray-600 mb-2">{brand.email}</p>
            {brand.business_name && (
              <p className="text-gray-500 text-sm mb-3">{brand.business_name}</p>
            )}
            <div className="flex items-center space-x-3 mb-3">
              <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(brand.status)}`}>
                {brand.status || 'PENDING'}
              </span>
              <span className="px-3 py-1.5 rounded-full text-xs font-semibold bg-blue-100 text-blue-800">
                {brand.upload_limit || 3} Uploads Remaining
              </span>
            </div>
            <div className="grid grid-cols-4 gap-4 text-center">
              <div>
                <p className="text-2xl font-bold text-blue-600">{brand.total_videos || 0}</p>
                <p className="text-xs text-gray-500">Total Videos</p>
              </div>
              <div>
                <p className="text-2xl font-bold text-green-600">{brand.approved_videos || 0}</p>
                <p className="text-xs text-gray-500">Approved</p>
              </div>
              <div>
                <p className="text-2xl font-bold text-red-600">{brand.flagged_videos || 0}</p>
                <p className="text-xs text-gray-500">Flagged</p>
              </div>
              <div>
                <p className="text-2xl font-bold text-purple-600">${brand.total_payments || 0}</p>
                <p className="text-xs text-gray-500">Payments</p>
              </div>
            </div>
          </div>
        </div>
        <div className="flex flex-col space-y-2">
          <button
            onClick={() => openBrandModal(brand)}
            className="bg-blue-600 text-white px-4 py-2 rounded-xl hover:bg-blue-700 transition-colors text-sm font-medium"
          >
            View Details
          </button>
          {showActions && brand.status === 'PENDING' && (
            <div className="flex space-x-2">
              <button
                onClick={() => handleApproval(brand.id, 'approve', '', brand.request_id || null)}
                disabled={actionLoading === brand.id}
                className="bg-green-600 text-white px-3 py-1.5 rounded-lg hover:bg-green-700 transition-colors text-xs font-medium disabled:opacity-50"
              >
                {actionLoading === brand.id ? '...' : 'Approve'}
              </button>
              <button
                onClick={() => {
                  const reason = prompt('Rejection reason (optional):');
                  if (reason !== null) {
                    handleApproval(brand.id, 'reject', reason, brand.request_id || null);
                  }
                }}
                disabled={actionLoading === brand.id}
                className="bg-red-600 text-white px-3 py-1.5 rounded-lg hover:bg-red-700 transition-colors text-xs font-medium disabled:opacity-50"
              >
                {actionLoading === brand.id ? '...' : 'Reject'}
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-blue-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Brands</h2>
          <p className="text-lg text-gray-600">Fetching brand data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-6 py-8">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-4xl font-bold text-gray-900 mb-2">Brand Management</h1>
              <p className="text-gray-600 text-lg">Manage brand approvals, video uploads, and payment tracking</p>
            </div>
            <div className="flex items-center space-x-4">
              <button 
                onClick={fetchBrands}
                className="bg-blue-600 text-white px-6 py-3 rounded-xl hover:bg-blue-700 transition-all duration-200 flex items-center space-x-3 shadow-lg hover:shadow-xl transform hover:scale-105"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                <span className="font-semibold">Refresh</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="px-6 py-8">
        {/* Search */}
        <div className="bg-white rounded-2xl p-6 shadow-lg border border-gray-100 mb-8">
          <div className="relative">
            <input
              type="text"
              placeholder="Search brands by name, email, or business name..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900"
            />
            <svg className="absolute left-3 top-3.5 w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-xl p-4 mb-6">
            <div className="flex items-center">
              <svg className="w-5 h-5 text-red-600 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-red-800 font-medium">{error}</p>
            </div>
          </div>
        )}

        {/* Tabs */}
        <div className="bg-white rounded-2xl shadow-lg border border-gray-100 overflow-hidden mb-8">
          <div className="border-b border-gray-200">
            <nav className="flex">
              <button
                onClick={() => setActiveTab('pending')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'pending'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Pending Approval ({filteredBrands(pendingBrands).length})
              </button>
              <button
                onClick={() => setActiveTab('approved')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'approved'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Approved Brands ({filteredBrands(approvedBrands).length})
              </button>
              <button
                onClick={() => setActiveTab('all')}
                className={`px-6 py-4 text-sm font-medium transition-colors ${
                  activeTab === 'all'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                All Brands ({filteredBrands(brands).length})
              </button>
            </nav>
          </div>

          <div className="p-6">
            {activeTab === 'pending' && (
              <div className="space-y-6">
                {filteredBrands(pendingBrands).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No pending brands</p>
                    <p className="text-sm text-gray-500 mt-2">All brand applications have been processed</p>
                  </div>
                ) : (
                  filteredBrands(pendingBrands).map((brand) => (
                    <BrandCard key={brand.id} brand={brand} showActions={true} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'approved' && (
              <div className="space-y-6">
                {filteredBrands(approvedBrands).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No approved brands</p>
                    <p className="text-sm text-gray-500 mt-2">Approved brands will appear here</p>
                  </div>
                ) : (
                  filteredBrands(approvedBrands).map((brand) => (
                    <BrandCard key={brand.id} brand={brand} showActions={false} />
                  ))
                )}
              </div>
            )}

            {activeTab === 'all' && (
              <div className="space-y-6">
                {filteredBrands(brands).length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 mx-auto mb-6 bg-gray-100 rounded-2xl flex items-center justify-center">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                      </svg>
                    </div>
                    <p className="text-lg font-medium text-gray-600">No brands found</p>
                    <p className="text-sm text-gray-500 mt-2">Try adjusting your search criteria</p>
                  </div>
                ) : (
                  filteredBrands(brands).map((brand) => (
                    <BrandCard key={brand.id} brand={brand} showActions={false} />
                  ))
                )}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Brand Details Modal */}
      {showBrandModal && selectedBrand && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowBrandModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-6xl sm:w-full">
              <div className="bg-white px-6 py-8">
                <div className="flex items-center justify-between mb-6">
                  <h3 className="text-2xl font-bold text-gray-900">Brand Details</h3>
                  <button
                    onClick={() => setShowBrandModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                  {/* Brand Information */}
                  <div className="space-y-6">
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Brand Information</h4>
                      <div className="space-y-3">
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Name</label>
                          <p className="text-gray-900">{selectedBrand.name || 'Not provided'}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Email</label>
                          <p className="text-gray-900">{selectedBrand.email}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Business Name</label>
                          <p className="text-gray-900">{selectedBrand.business_name || 'Not provided'}</p>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Status</label>
                          <span className={`inline-block px-3 py-1.5 rounded-full text-xs font-semibold ${getStatusBadgeColor(selectedBrand.status)}`}>
                            {selectedBrand.status || 'PENDING'}
                          </span>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700">Upload Limit</label>
                          <p className="text-gray-900">{selectedBrand.upload_limit || 3} videos remaining</p>
                        </div>
                      </div>
                    </div>
                  </div>

                  {/* Statistics */}
                  <div className="space-y-6">
                    <div>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4">Brand Statistics</h4>
                      <div className="grid grid-cols-2 gap-4">
                        <div className="bg-blue-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-blue-600">{selectedBrand.total_videos || 0}</p>
                          <p className="text-blue-700 text-sm font-medium">Total Videos</p>
                        </div>
                        <div className="bg-green-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-green-600">{selectedBrand.approved_videos || 0}</p>
                          <p className="text-green-700 text-sm font-medium">Approved</p>
                        </div>
                        <div className="bg-red-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-red-600">{selectedBrand.flagged_videos || 0}</p>
                          <p className="text-red-700 text-sm font-medium">Flagged</p>
                        </div>
                        <div className="bg-purple-50 rounded-xl p-4">
                          <p className="text-2xl font-bold text-purple-600">${selectedBrand.total_payments || 0}</p>
                          <p className="text-purple-700 text-sm font-medium">Total Payments</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Video Uploads Section */}
                {selectedBrand.videos && selectedBrand.videos.length > 0 && (
                  <div className="mt-8 pt-6 border-t border-gray-200">
                    <h4 className="text-lg font-semibold text-gray-900 mb-4">Video Uploads</h4>
                    <div className="space-y-4">
                      {selectedBrand.videos.map((video) => (
                        <div key={video.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                          <div className="flex items-center space-x-4">
                            <div className="w-12 h-12 bg-gray-200 rounded-lg flex items-center justify-center">
                              <svg className="w-6 h-6 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                              </svg>
                            </div>
                            <div>
                              <p className="font-medium text-gray-900">{video.product_name}</p>
                              <p className="text-sm text-gray-500">{video.price}  {video.created_at}</p>
                            </div>
                          </div>
                          <div className="flex items-center space-x-2">
                            <span className={`px-3 py-1.5 rounded-full text-xs font-semibold ${getVideoStatusBadgeColor(video.status)}`}>
                              {video.status || 'PENDING'}
                            </span>
                            {video.status === 'PENDING' && (
                              <div className="flex space-x-2">
                                <button
                                  onClick={() => handleVideoAction(selectedBrand.id, video.id, 'approve')}
                                  disabled={actionLoading === `${selectedBrand.id}-${video.id}`}
                                  className="bg-green-600 text-white px-3 py-1.5 rounded-lg hover:bg-green-700 transition-colors text-xs font-medium disabled:opacity-50"
                                >
                                  Approve
                                </button>
                                <button
                                  onClick={() => {
                                    const reason = prompt('Flag reason:');
                                    if (reason) {
                                      handleVideoAction(selectedBrand.id, video.id, 'flag', reason);
                                    }
                                  }}
                                  disabled={actionLoading === `${selectedBrand.id}-${video.id}`}
                                  className="bg-red-600 text-white px-3 py-1.5 rounded-lg hover:bg-red-700 transition-colors text-xs font-medium disabled:opacity-50"
                                >
                                  Flag
                                </button>
                              </div>
                            )}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Action Buttons */}
                <div className="flex items-center justify-end space-x-4 mt-8 pt-6 border-t border-gray-200">
                  {selectedBrand.status === 'PENDING' && (
                    <>
                      <button
                        onClick={() => handleApproval(selectedBrand.id, 'approve', '', selectedBrand.request_id || null)}
                        disabled={actionLoading === selectedBrand.id}
                        className="bg-green-600 text-white px-6 py-3 rounded-xl hover:bg-green-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedBrand.id ? 'Processing...' : 'Approve Brand'}
                      </button>
                      <button
                        onClick={() => {
                          const reason = prompt('Rejection reason (optional):');
                          if (reason !== null) {
                            handleApproval(selectedBrand.id, 'reject', reason, selectedBrand.request_id || null);
                          }
                        }}
                        disabled={actionLoading === selectedBrand.id}
                        className="bg-red-600 text-white px-6 py-3 rounded-xl hover:bg-red-700 transition-colors font-semibold disabled:opacity-50"
                      >
                        {actionLoading === selectedBrand.id ? 'Processing...' : 'Reject Brand'}
                      </button>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default BrandManagement;
