import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Clock, CheckCircle, XCircle, RefreshCw, ArrowLeft, Loader, Bell, MessageCircle } from 'lucide-react';
import { providerAPI } from '../utils/api';
import { toast } from 'react-hot-toast';

const statusVisuals = {
  APPROVED: {
    heading: "You're all set to go live!",
    message: 'Your provider profile is ready. Head over to Listings and start showcasing your products.',
    iconColor: 'text-emerald-500',
    bgColor: 'bg-emerald-50',
    borderColor: 'border-emerald-200',
    textColor: 'text-emerald-700'
  },
  REJECTED: {
    heading: 'We need a quick follow-up',
    message: 'Your submission needs a bit more info. Revisit your details or message our support team to continue.',
    iconColor: 'text-red-500',
    bgColor: 'bg-red-50',
    borderColor: 'border-red-200',
    textColor: 'text-red-700'
  },
  PENDING: {
    heading: "We're reviewing your brand",
    message: 'Thanks for submitting your provider request. Our team is carefully reviewing everythingkeep an eye out for updates.',
    iconColor: 'text-[#6C4DFF]',
    bgColor: 'bg-purple-50',
    borderColor: 'border-purple-200',
    textColor: 'text-[#6C4DFF]'
  }
};

const ProviderRequestWaiting = () => {
  const navigate = useNavigate();
  const [requestStatus, setRequestStatus] = useState(null);
  const [loading, setLoading] = useState(true);
  const [autoRefresh, setAutoRefresh] = useState(true);

  useEffect(() => {
    checkStatus();
    const interval = setInterval(() => {
      if (autoRefresh) {
        checkStatus();
      }
    }, 5000);

    return () => clearInterval(interval);
  }, [autoRefresh]);

  const checkStatus = async () => {
    try {
      const response = await providerAPI.getRequestStatus();
      if (response.ok) {
        if (response.has_provider) {
          navigate('/listing');
          return;
        }
        setRequestStatus(response.request_status);
        if (response.request_status === 'APPROVED') {
          setAutoRefresh(false);
          toast.success('Your provider request has been approved!');
          setTimeout(() => navigate('/listing'), 2000);
        }
      }
    } catch (error) {
      console.error('Error checking request status:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSupport = () => {
    window.open('mailto:support@getnayi.com', '_blank');
  };

  const getStatusIcon = (size = 64, statusInfo) => {
    const style = { width: size, height: size };
    switch (requestStatus) {
      case 'APPROVED':
        return <CheckCircle className={statusInfo.iconColor} style={style} />;
      case 'REJECTED':
        return <XCircle className={statusInfo.iconColor} style={style} />;
      default:
        return <Clock className={`${statusInfo.iconColor} ${requestStatus === 'PENDING' ? 'animate-pulse' : ''}`} style={style} />;
    }
  };

  const timelineSteps = [
    { label: 'Request submitted', state: 'done' },
    { label: 'Under review', state: requestStatus === 'REJECTED' ? 'error' : requestStatus === 'APPROVED' ? 'done' : 'active' },
    { 
      label: requestStatus === 'REJECTED' ? 'Needs updates' : 'Ready to go', 
      state: requestStatus === 'APPROVED' ? 'done' : requestStatus === 'REJECTED' ? 'error' : 'pending' 
    }
  ];

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
      </div>
    );
  }

  const statusInfo = statusVisuals[requestStatus] || statusVisuals.PENDING;

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="w-full bg-white border-b border-gray-200 sticky top-0 z-50">
        <div className="px-4 sm:px-5 py-3.5 sm:py-4 flex items-center justify-between">
          <div className="flex items-center">
            <button
              onClick={() => navigate('/')}
              className="inline-flex items-center gap-2 text-gray-600 hover:text-gray-900 transition-colors mr-4"
            >
              <ArrowLeft className="w-5 h-5" />
            </button>
            <span className="text-2xl sm:text-[24px] font-poppins font-bold">
              <span className="text-black">Get</span>
              <span className="text-[#6C4DFF]">nayi</span>
            </span>
          </div>
          <div className="flex items-center gap-3 sm:gap-4">
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <Bell className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <MessageCircle className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
          </div>
        </div>
      </header>

      <div className="px-4 sm:px-5 py-6 sm:py-8 pb-32 sm:pb-12 max-w-4xl mx-auto">
        {/* Status Card */}
        <div className={`${statusInfo.bgColor} border-2 ${statusInfo.borderColor} rounded-2xl p-6 sm:p-8 mb-6`}>
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-6">
            <div className="flex-1">
              <p className="text-xs uppercase tracking-wider text-gray-500 mb-2 font-poppins">
                Current Status
              </p>
              <h1 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-3 font-poppins">
                {statusInfo.heading}
              </h1>
              <p className="text-base text-gray-600 leading-relaxed font-poppins">
                {statusInfo.message}
              </p>
            </div>
            <div className={`w-20 h-20 sm:w-24 sm:h-24 ${statusInfo.bgColor} border-2 ${statusInfo.borderColor} rounded-2xl flex items-center justify-center flex-shrink-0`}>
              {getStatusIcon(48, statusInfo)}
            </div>
          </div>
        </div>

        {/* Timeline Steps */}
        <div className="mb-6">
          <h2 className="text-lg font-bold text-gray-900 mb-4 font-poppins">Request Progress</h2>
          <div className="grid gap-4 sm:grid-cols-3">
            {timelineSteps.map((step, index) => {
              const isActive = step.state === 'active';
              const isDone = step.state === 'done';
              const isError = step.state === 'error';

              return (
                <div
                  key={step.label}
                  className={`rounded-xl border-2 px-4 py-4 transition-all duration-200 ${
                    isDone
                      ? 'border-emerald-200 bg-emerald-50'
                      : isError
                      ? 'border-red-200 bg-red-50'
                      : isActive
                      ? 'border-[#6C4DFF] bg-purple-50'
                      : 'border-gray-200 bg-gray-50'
                  } ${isActive ? 'animate-pulse' : ''}`}
                >
                  <div className="flex items-center justify-between mb-2">
                    <span className="text-xs font-semibold text-gray-500 font-poppins">
                      Step {index + 1}
                    </span>
                    <div
                      className={`w-2.5 h-2.5 rounded-full ${
                        isDone
                          ? 'bg-emerald-500'
                          : isError
                          ? 'bg-red-500'
                          : isActive
                          ? 'bg-[#6C4DFF]'
                          : 'bg-gray-300'
                      }`}
                    />
                  </div>
                  <p className="text-sm font-semibold text-gray-900 font-poppins">
                    {step.label}
                  </p>
                </div>
              );
            })}
          </div>
        </div>

        {requestStatus === 'PENDING' && (
          <div className="grid gap-5 lg:grid-cols-3 mb-6">
            <div className="lg:col-span-2 border-2 border-gray-200 rounded-xl p-5 bg-gray-50">
              <div className="flex items-center justify-between mb-3">
                <div>
                  <p className="text-xs uppercase tracking-wider text-gray-500 font-poppins">Live Updates</p>
                  <p className="text-base text-gray-900 font-semibold mt-1 font-poppins">Status Monitor</p>
                </div>
                <RefreshCw className={`w-5 h-5 ${autoRefresh ? 'animate-spin text-[#6C4DFF]' : 'text-gray-400'}`} />
              </div>
              <p className="text-sm text-gray-600 mb-4 font-poppins">
                {autoRefresh
                  ? "We'll refresh this status every few seconds so you don't have to."
                  : 'Auto-refresh is paused. Tap below if you want us to keep checking for you.'}
              </p>
              <div className="flex flex-wrap gap-3">
                <button
                  onClick={checkStatus}
                  className="bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-5 py-2.5 rounded-full font-medium transition-colors font-poppins shadow-md hover:shadow-lg active:scale-[0.96]"
                >
                  Refresh now
                </button>
                <button
                  onClick={() => setAutoRefresh(!autoRefresh)}
                  className="border-2 border-gray-300 text-gray-700 hover:bg-gray-100 px-5 py-2.5 rounded-full font-medium transition-colors font-poppins"
                >
                  {autoRefresh ? 'Pause auto-refresh' : 'Resume auto-refresh'}
                </button>
              </div>
            </div>
            <div className="border-2 border-gray-200 rounded-xl p-5 bg-gray-50">
              <p className="text-xs uppercase tracking-wider text-gray-500 mb-2 font-poppins">Need help?</p>
              <p className="text-sm text-gray-600 mb-4 font-poppins">
                Our partner team can answer any question about your onboarding.
              </p>
              <button
                onClick={handleSupport}
                className="w-full border-2 border-gray-300 text-gray-700 hover:bg-gray-100 px-4 py-2.5 rounded-full font-medium transition-colors font-poppins"
              >
                Message support
              </button>
            </div>
          </div>
        )}

        {/* Action Buttons */}
        {requestStatus === 'REJECTED' && (
          <div className="flex flex-col sm:flex-row gap-4">
            <button
              onClick={() => navigate('/provider/register')}
              className="flex-1 bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-6 py-3 rounded-full font-semibold transition-colors font-poppins shadow-md hover:shadow-lg active:scale-[0.96]"
            >
              Submit new request
            </button>
            <button
              onClick={() => navigate('/')}
              className="flex-1 border-2 border-gray-300 text-gray-700 hover:bg-gray-100 px-6 py-3 rounded-full font-semibold transition-colors font-poppins"
            >
              Return home
            </button>
          </div>
        )}

        {requestStatus === 'APPROVED' && (
          <div className="flex flex-col sm:flex-row gap-4">
            <button
              onClick={() => navigate('/listing')}
              className="flex-1 bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-6 py-3 rounded-full font-semibold transition-colors font-poppins shadow-md hover:shadow-lg active:scale-[0.96]"
            >
              Go to listings
            </button>
            <button
              onClick={() => navigate('/')}
              className="flex-1 border-2 border-gray-300 text-gray-700 hover:bg-gray-100 px-6 py-3 rounded-full font-semibold transition-colors font-poppins"
            >
              Explore home
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default ProviderRequestWaiting;
