import { initializeApp } from 'firebase/app';
import { getAuth, GoogleAuthProvider } from 'firebase/auth';
import { getAnalytics } from 'firebase/analytics';

// Firebase configuration
// Using environment variables if available, otherwise using direct config
const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY || "AIzaSyAeSHXfPlQhh8IY-Xh5xZ52Y0KC3iKlOwA",
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN || "getnayi.firebaseapp.com",
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID || "getnayi",
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET || "getnayi.firebasestorage.app",
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID || "862344895994",
  appId: process.env.REACT_APP_FIREBASE_APP_ID || "1:862344895994:web:e3ccd073df36edf556d843",
  measurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID || "G-5JV4K4GGY0"
};

// Verify Firebase config is loaded
if (!firebaseConfig.apiKey || !firebaseConfig.projectId) {
  console.error('Firebase configuration is missing required fields');
}

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Analytics (only in browser environment)
let analytics = null;
if (typeof window !== 'undefined') {
  try {
    analytics = getAnalytics(app);
  } catch (error) {
    console.log('Analytics initialization error:', error);
  }
}

// Initialize Firebase Authentication and get a reference to the service
export const auth = getAuth(app);

// Initialize Google Auth Provider
export const googleProvider = new GoogleAuthProvider();
googleProvider.setCustomParameters({
  prompt: 'select_account',
});

export { analytics };
export default app;

