import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { ArrowLeft, User, UserMinus } from 'lucide-react';
import { userAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const Following = () => {
  const [searchParams] = useSearchParams();
  const userId = searchParams.get('user_id');
  const navigate = useNavigate();
  const { user: currentUser } = useAuth();
  const [following, setFollowing] = useState([]);
  const [loading, setLoading] = useState(true);
  const [unfollowingIds, setUnfollowingIds] = useState(new Set());

  useEffect(() => {
    if (userId) {
      loadFollowing();
    } else {
      toast.error('User ID required');
      navigate('/');
    }
  }, [userId]);

  const loadFollowing = async () => {
    try {
      setLoading(true);
      const data = await userAPI.getFollowing(parseInt(userId));
      if (data.ok) {
        setFollowing(data.following || []);
      }
    } catch (error) {
      console.error('Error loading following:', error);
      toast.error('Failed to load following');
    } finally {
      setLoading(false);
    }
  };

  const handleUnfollow = async (targetUserId, e) => {
    if (e) {
      e.stopPropagation();
    }

    if (!currentUser) {
      toast.error('Please log in to unfollow users');
      return;
    }

    // Only allow unfollowing if viewing your own following list
    if (!userId || Number(userId) !== Number(currentUser.user_id)) {
      return;
    }

    setUnfollowingIds(prev => new Set(prev).add(targetUserId));

    try {
      await userAPI.unfollow(targetUserId);
      setFollowing(prev => prev.filter(u => u.user_id !== targetUserId));
      toast.success('Unfollowed');
    } catch (error) {
      console.error('Error unfollowing:', error);
      toast.error(error?.response?.data?.error || 'Failed to unfollow');
    } finally {
      setUnfollowingIds(prev => {
        const newSet = new Set(prev);
        newSet.delete(targetUserId);
        return newSet;
      });
    }
  };

  const isOwnFollowingList = currentUser && userId && Number(userId) === Number(currentUser.user_id);

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-4 py-4 sticky top-0 z-20">
        <div className="flex items-center gap-4 max-w-4xl mx-auto">
          <button
            onClick={() => navigate(-1)}
            className="p-2 hover:bg-gray-100 rounded-full transition-colors"
          >
            <ArrowLeft className="w-5 h-5 text-gray-900" />
          </button>
          <h1 className="text-xl font-bold text-gray-900 font-poppins">Following</h1>
        </div>
      </div>

      {/* Following List */}
      <div className="max-w-4xl mx-auto px-4 py-4">
        {loading ? (
          <div className="text-center py-8 text-gray-500 text-sm font-poppins">
            Loading following...
          </div>
        ) : following.length > 0 ? (
          <div className="space-y-1">
            {following.map((user) => (
              <div
                key={user.user_id}
                className="flex items-center gap-3 px-3 py-2 border-b border-gray-100 hover:bg-gray-50 transition-colors"
              >
                <div 
                  className="w-10 h-10 rounded-full bg-gradient-to-br from-[#6A00FF] to-purple-600 flex items-center justify-center border border-gray-200 flex-shrink-0 cursor-pointer"
                  onClick={() => {
                    if (user.username) {
                      navigate(`/profile/${user.username}`);
                    } else {
                      navigate(`/profile?user_id=${user.user_id}`);
                    }
                  }}
                >
                  {user.profile_image_url ? (
                    <img
                      src={user.profile_image_url}
                      alt={user.full_name || user.username}
                      className="w-full h-full rounded-full object-cover"
                    />
                  ) : user.business_name ? (
                    <span className="text-white text-sm font-poppins font-bold">
                      {user.business_name.charAt(0).toUpperCase()}
                    </span>
                  ) : (
                    <User className="w-5 h-5 text-white" />
                  )}
                </div>
                <div 
                  className="flex-1 min-w-0 cursor-pointer"
                  onClick={() => {
                    if (user.username) {
                      navigate(`/profile/${user.username}`);
                    } else {
                      navigate(`/profile?user_id=${user.user_id}`);
                    }
                  }}
                >
                  <h3 className="font-semibold text-sm text-gray-900 font-poppins truncate">
                    {user.business_name || user.full_name || user.username || 'User'}
                  </h3>
                  {user.username && (
                    <p className="text-xs text-gray-500 font-poppins truncate">@{user.username}</p>
                  )}
                  {user.provider_type && !user.username && (
                    <p className="text-xs text-gray-500 font-poppins capitalize truncate">
                      {user.provider_type.toLowerCase()}
                    </p>
                  )}
                </div>
                {/* Unfollow Button - only show on own following list */}
                {isOwnFollowingList && (
                  <button
                    onClick={(e) => handleUnfollow(user.user_id, e)}
                    disabled={unfollowingIds.has(user.user_id)}
                    className="px-2.5 py-1.5 text-xs font-poppins rounded-lg bg-gray-100 hover:bg-gray-200 text-gray-700 transition-colors flex items-center gap-1.5 flex-shrink-0"
                  >
                    <UserMinus className="w-3.5 h-3.5" />
                    <span className="hidden sm:inline">Unfollow</span>
                  </button>
                )}
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <User className="w-16 h-16 text-gray-300 mx-auto mb-4" />
            <p className="text-gray-500 font-poppins">Not following anyone yet</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Following;

