import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { authAPI, categoriesAPI } from '../utils/api';
import { useAuth } from '../context/AuthContext';
import { toast } from 'react-hot-toast';

const Signup = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { login } = useAuth();
  
  const onboardingToken = location.state?.onboardingToken;
  const isGoogleAuth = location.state?.isGoogleAuth || false;

  const [step, setStep] = useState('info'); // 'info' or 'preferences'
  const [username, setUsername] = useState('');
  const [fullName, setFullName] = useState('');
  const [categories, setCategories] = useState([]);
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    if (!onboardingToken) {
      navigate('/login');
      return;
    }
    fetchCategories();
  }, [onboardingToken, navigate]);

  const fetchCategories = async () => {
    try {
      const data = await categoriesAPI.getAll();
      setCategories(data.categories || []);
    } catch (err) {
      const errorMsg = 'Failed to load categories. Please refresh the page.';
      setError(errorMsg);
      toast.error(errorMsg);
    }
  };

  const handleInfoSubmit = (e) => {
    e.preventDefault();
    
    if (!username.trim() || !fullName.trim()) {
      const errorMsg = 'Username and full name are required';
      setError(errorMsg);
      toast.error(errorMsg);
      return;
    }

    if (username.length < 3) {
      const errorMsg = 'Username must be at least 3 characters';
      setError(errorMsg);
      toast.error(errorMsg);
      return;
    }

    setError('');
    setStep('preferences');
  };

  const toggleCategory = (slug) => {
    setSelectedCategories(prev => {
      if (prev.includes(slug)) {
        return prev.filter(s => s !== slug);
      } else {
        return [...prev, slug];
      }
    });
  };

  const handleSignupComplete = async () => {
    if (selectedCategories.length < 1) {
      const errorMsg = 'Please select at least 1 category';
      setError(errorMsg);
      toast.error(errorMsg);
      return;
    }

    setLoading(true);
    setError('');

    try {
      let data;
      
      if (isGoogleAuth) {
        // Get the stored Google ID token
        const idToken = sessionStorage.getItem('google_id_token');
        if (!idToken) {
          throw new Error('Google authentication token not found. Please sign in again.');
        }
        
        console.log('=== GOOGLE SIGNUP - SIGNUP DATA ===');
        console.log('Signup Data:', {
          username: username.trim(),
          fullName: fullName.trim(),
          selectedCategories: selectedCategories,
          idTokenLength: idToken.length
        });
        console.log('===================================');
        
        // Use Google signup endpoint
        data = await authAPI.signUpWithGoogle(
          idToken,
          username.trim(),
          fullName.trim(),
          selectedCategories
        );
        
        // Clear the stored token
        sessionStorage.removeItem('google_id_token');
      } else {
        // Use regular phone OTP signup
        data = await authAPI.signup(
        onboardingToken,
        username.trim(),
        fullName.trim(),
        selectedCategories
      );
      }

      // Check if backend returned an error
      if (!data.ok) {
        const errorMsg = data.error || 'Signup failed. Please try again.';
        console.error('Signup error:', errorMsg);
        setError(errorMsg);
        toast.error(errorMsg);
        setLoading(false);
        return;
      }

      // Print backend response for signup
      console.log('=== GOOGLE SIGNUP - BACKEND RESPONSE ===');
      console.log('Backend Response:', {
        ok: data.ok,
        hasToken: !!data.token,
        hasUser: !!data.user,
        user: data.user
      });
      console.log('========================================');

      // Store token in localStorage for Authorization header
      if (data.token) {
        localStorage.setItem('auth_token', data.token);
        console.log('Auth token stored in localStorage');
      }

      if (data.user) {
        console.log('=== GOOGLE SIGNUP - FINAL USER DATA ===');
        console.log('User Data:', {
          user_id: data.user.user_id,
          username: data.user.username,
          full_name: data.user.full_name,
          email: data.user.email,
          phone: data.user.phone,
          profile_picture: data.user.profile_picture,
          is_provider: data.user.is_provider,
          is_influencer: data.user.is_influencer,
          is_admin: data.user.is_admin,
          capsules: data.user.capsules,
          type: data.user.type
        });
        console.log('=======================================');
        login(data.user);
      }
      
      toast.success('Account created successfully!');
      navigate('/home', { replace: true });
    } catch (err) {
      const errorMsg = err.response?.data?.error || err.response?.data?.details || err.message || 'Signup failed. Please try again.';
      setError(errorMsg);
      toast.error(errorMsg);
    } finally {
      setLoading(false);
    }
  };

  if (step === 'info') {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center p-4 sm:p-6">
        <div className="bg-white border border-gray-200 rounded-2xl shadow-xl p-6 sm:p-8 w-full max-w-md relative z-10">
          <div className="text-center mb-6 sm:mb-8">
            <h1 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-2 font-poppins">Create Your Account</h1>
            <p className="text-sm sm:text-base text-gray-600 font-poppins">Tell us about yourself</p>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-600 rounded-lg text-xs sm:text-sm font-poppins">
              {error}
            </div>
          )}

          <form onSubmit={handleInfoSubmit}>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-2 font-poppins">
                Username
              </label>
              <input
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value.toLowerCase().replace(/[^a-z0-9_]/g, ''))}
                placeholder="john_doe"
                className="w-full px-4 py-4 text-base border border-gray-300 rounded-xl focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent bg-white text-gray-900 placeholder-gray-500"
                required
              />
            </div>

            <div className="mb-6">
              <label className="block text-sm font-medium text-gray-700 mb-2 font-poppins">
                Full Name
              </label>
              <input
                type="text"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                placeholder="John Doe"
                className="w-full px-4 py-4 text-base border border-gray-300 rounded-xl focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent bg-white text-gray-900 placeholder-gray-500"
                required
              />
            </div>

            <button
              type="submit"
              className="w-full bg-[#6A00FF] text-white py-4 rounded-xl text-base font-medium active:bg-purple-700 transition-colors touch-manipulation font-poppins"
            >
              Continue
            </button>
          </form>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white p-4 sm:p-6">
      <div className="max-w-4xl mx-auto py-4 sm:py-8">
        <div className="bg-white border border-gray-200 rounded-2xl shadow-xl p-4 sm:p-8 relative z-10">
          <div className="text-center mb-6 sm:mb-8">
            <h1 className="text-xl sm:text-3xl font-bold text-gray-900 mb-2 font-poppins">Choose Your Interests</h1>
            <p className="text-sm sm:text-base text-gray-600 font-poppins">Select at least 1 category you love</p>
            <div className="mt-2 inline-block px-4 py-2 bg-[#6A00FF] text-white rounded-full text-sm font-medium font-poppins">
              {selectedCategories.length} selected
            </div>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-600 rounded-lg text-xs sm:text-sm font-poppins">
              {error}
            </div>
          )}

          <div className="grid grid-cols-2 sm:grid-cols-3 gap-3 sm:gap-4 mb-6 sm:mb-8">
            {categories.map((category) => (
              <button
                key={category.category_id}
                onClick={() => toggleCategory(category.slug)}
                className={`p-4 sm:p-6 rounded-xl border-2 transition-all touch-manipulation ${
                  selectedCategories.includes(category.slug)
                    ? 'border-[#6A00FF] bg-[#6A00FF]/10 scale-95'
                    : 'border-gray-300 active:border-[#6A00FF] bg-white hover:bg-gray-50'
                }`}
              >
                <div className="w-12 h-12 sm:w-16 sm:h-16 mx-auto mb-2 sm:mb-3 rounded-lg overflow-hidden flex items-center justify-center bg-gradient-to-br from-[#6A00FF] to-purple-400">
                  {category.image_url ? (
                    <img 
                      src={category.image_url} 
                      alt={category.category_name}
                      className="w-full h-full object-cover"
                      onError={(e) => {
                        e.target.style.display = 'none';
                        const parent = e.target.parentElement;
                        if (parent && !parent.querySelector('span')) {
                          const span = document.createElement('span');
                          span.className = 'text-xl sm:text-2xl text-white font-bold font-poppins';
                          span.textContent = category.category_name?.charAt(0)?.toUpperCase() || '?';
                          parent.appendChild(span);
                        }
                      }}
                    />
                  ) : (
                    <span className="text-xl sm:text-2xl text-white font-bold font-poppins">
                      {category.category_name?.charAt(0)?.toUpperCase() || '?'}
                    </span>
                  )}
                </div>
                <h3 className="font-semibold text-gray-900 text-center text-sm sm:text-base font-poppins">
                  {category.category_name}
                </h3>
                {selectedCategories.includes(category.slug) && (
                  <div className="mt-2 text-center">
                    <span className="inline-block w-6 h-6 bg-[#6A00FF] text-white rounded-full text-sm leading-6 font-poppins">
                      
                    </span>
                  </div>
                )}
              </button>
            ))}
          </div>

          <div className="space-y-3">
            <button
              onClick={handleSignupComplete}
              disabled={loading || selectedCategories.length < 1}
              className="w-full px-8 py-4 bg-[#6A00FF] text-white rounded-xl text-base font-medium active:bg-purple-700 disabled:bg-gray-400 transition-colors touch-manipulation font-poppins"
            >
              {loading ? 'Creating Account...' : 'Get Started'}
            </button>

            <button
              onClick={() => setStep('info')}
              className="w-full px-6 py-3 text-gray-600 active:text-gray-800 font-medium touch-manipulation font-poppins"
              disabled={loading}
            >
               Back
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Signup;

