import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { Search as SearchIcon, X, ArrowLeft, Eye, Heart, Play, User, TrendingUp, Clock, Star, Users } from 'lucide-react';
import { searchAPI, listingAPI, providerAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const Search = () => {
  const navigate = useNavigate();
  const { isAuthenticated, user } = useAuth();
  const [searchQuery, setSearchQuery] = useState('');
  const [recentSearches, setRecentSearches] = useState([]);
  const [trendingSearches, setTrendingSearches] = useState([]);
  const [searchResults, setSearchResults] = useState(null);
  const [loading, setLoading] = useState(false);
  const [recentlyUploaded, setRecentlyUploaded] = useState([]);
  const [mostLiked, setMostLiked] = useState([]);
  const [mostWatched, setMostWatched] = useState([]);
  const [influencers, setInfluencers] = useState([]);
  const [brands, setBrands] = useState([]);
  const [loadingSections, setLoadingSections] = useState({
    recentlyUploaded: true,
    mostLiked: true,
    mostWatched: true,
    influencers: true,
    brands: true
  });

  // Load recent searches from localStorage
  useEffect(() => {
    const saved = localStorage.getItem('recentSearches');
    if (saved) {
      try {
        setRecentSearches(JSON.parse(saved));
      } catch (e) {
        console.error('Error loading recent searches:', e);
      }
    }
  }, []);

  // Generate trending searches (random from all products)
  useEffect(() => {
    const generateTrendingSearches = async () => {
      try {
        const response = await listingAPI.getAll({ limit: 50 });
        if (response?.listings && Array.isArray(response.listings)) {
          const productNames = response.listings
            .filter(item => item.product_name)
            .map(item => item.product_name)
            .slice(0, 10);
          setTrendingSearches(productNames);
        }
      } catch (error) {
        console.error('Error generating trending searches:', error);
      }
    };
    generateTrendingSearches();
  }, []);

  // Load all sections
  useEffect(() => {
    loadAllSections();
  }, []);

  const loadAllSections = async () => {
    try {
      // Fetch all listings
      const listingsResponse = await listingAPI.getAll({ limit: 1000 });
      const allListings = listingsResponse?.listings || [];

      // Filter recently uploaded (less than 5 views)
      const recentlyUploadedList = allListings
        .filter(item => (item.view_count || 0) < 5)
        .sort((a, b) => new Date(b.created_at) - new Date(a.created_at))
        .slice(0, 10);
      setRecentlyUploaded(recentlyUploadedList);
      setLoadingSections(prev => ({ ...prev, recentlyUploaded: false }));

      // Filter most liked (> 100 likes)
      const mostLikedList = allListings
        .filter(item => (item.like_count || 0) > 100)
        .sort((a, b) => (b.like_count || 0) - (a.like_count || 0))
        .slice(0, 10);
      setMostLiked(mostLikedList);
      setLoadingSections(prev => ({ ...prev, mostLiked: false }));

      // Filter most watched (> 100 views)
      const mostWatchedList = allListings
        .filter(item => (item.view_count || 0) > 100)
        .sort((a, b) => (b.view_count || 0) - (a.view_count || 0))
        .slice(0, 10);
      setMostWatched(mostWatchedList);
      setLoadingSections(prev => ({ ...prev, mostWatched: false }));

      // Fetch influencers (use a common query to get all)
      try {
        const searchResponse = await searchAPI.search('a', 50, 'influencer');
        if (searchResponse?.ok && searchResponse?.results?.influencers) {
          setInfluencers(searchResponse.results.influencers.slice(0, 10));
        }
      } catch (error) {
        console.error('Error fetching influencers:', error);
      }
      setLoadingSections(prev => ({ ...prev, influencers: false }));

      // Fetch brands (use a common query to get all)
      try {
        const brandSearchResponse = await searchAPI.search('a', 50, 'brand');
        if (brandSearchResponse?.ok && brandSearchResponse?.results?.brands) {
          setBrands(brandSearchResponse.results.brands.slice(0, 10));
        }
      } catch (error) {
        console.error('Error fetching brands:', error);
      }
      setLoadingSections(prev => ({ ...prev, brands: false }));
    } catch (error) {
      console.error('Error loading sections:', error);
      Object.keys(loadingSections).forEach(key => {
        setLoadingSections(prev => ({ ...prev, [key]: false }));
      });
    }
  };

  const saveRecentSearch = (query) => {
    if (!query || query.trim().length === 0) return;
    const trimmed = query.trim();
    const updated = [trimmed, ...recentSearches.filter(s => s !== trimmed)].slice(0, 10);
    setRecentSearches(updated);
    localStorage.setItem('recentSearches', JSON.stringify(updated));
  };

  const handleSearch = async (query) => {
    if (!query || query.length < 2) {
      setSearchResults(null);
      return;
    }

    try {
      setLoading(true);
      setSearchResults(null);
      saveRecentSearch(query);
      const response = await searchAPI.getSuggestions(query, 20);
      console.log('Search response:', response);
      if (response && response.ok) {
        setSearchResults(response.suggestions);
      } else {
        console.error('Search response not ok:', response);
        toast.error(response?.error || 'Search failed');
      }
    } catch (error) {
      console.error('Error searching:', error);
      console.error('Error response:', error.response);
      console.error('Error data:', error.response?.data);
      
      // Handle 500 error response - backend returns structured error data
      // Backend returns { ok: false, error: str, suggestions: {categories: [], brands: [], influencers: [], products: []} }
      if (error.response?.status === 500 && error.response?.data) {
        const errorData = error.response.data;
        console.log('500 error data:', errorData);
        // Always try to use suggestions even if there's an error (backend returns empty arrays on SQL error)
        if (errorData.suggestions) {
          setSearchResults(errorData.suggestions);
          console.log('Search: Using suggestions from error response:', errorData.suggestions);
          // Don't show error toast - silently handle backend SQL errors
          // Results will show empty arrays which will display "No results found"
        } else {
          // No suggestions in error response, set empty results
          setSearchResults({
            categories: [],
            brands: [],
            influencers: [],
            products: []
          });
        }
      } else {
        // Other errors - show toast and set empty results
        toast.error(error.response?.data?.error || 'Search failed');
        setSearchResults({
          categories: [],
          brands: [],
          influencers: [],
          products: []
        });
      }
    } finally {
      setLoading(false);
    }
  };

  // Auto-search on typing with debounce
  useEffect(() => {
    if (searchQuery.length < 2) {
      setSearchResults(null);
      setLoading(false);
      return;
    }

    const timeout = setTimeout(async () => {
      try {
        setLoading(true);
        setSearchResults(null);
        saveRecentSearch(searchQuery);
        const response = await searchAPI.getSuggestions(searchQuery, 20);
        console.log('Auto-search response:', response);
        if (response && response.ok !== false) {
          setSearchResults(response.suggestions || response);
        } else if (response && response.error) {
          console.error('Auto-search response error:', response.error);
          // Even if there's an error, try to show empty results
          setSearchResults({
            categories: [],
            brands: [],
            influencers: [],
            products: []
          });
        }
      } catch (error) {
        console.error('Error in auto-search:', error);
        console.error('Error response:', error.response);
        console.error('Error data:', error.response?.data);
        
        // Handle 500 error response - backend returns structured error data
        // Backend returns { ok: false, error: str, suggestions: {categories: [], brands: [], influencers: [], products: []} }
        if (error.response?.status === 500 && error.response?.data) {
          const errorData = error.response.data;
          // Always try to use suggestions even if there's an error (backend returns empty arrays on SQL error)
          if (errorData.suggestions) {
            setSearchResults(errorData.suggestions);
            console.log('Auto-search: Using suggestions from error response:', errorData.suggestions);
          } else {
            // If no results in error, set empty results
            setSearchResults({
              categories: [],
              brands: [],
              influencers: [],
              products: []
            });
          }
        } else {
          // Other errors - set empty results
          setSearchResults({
            categories: [],
            brands: [],
            influencers: [],
            products: []
          });
        }
      } finally {
        setLoading(false);
      }
    }, 500);

    return () => clearTimeout(timeout);
  }, [searchQuery]);

  const handleSuggestionClick = (query) => {
    setSearchQuery(query);
    handleSearch(query);
  };

  const handleProductClick = (listingId) => {
    navigate(`/watch?video=${listingId}`);
  };

  const handleProfileClick = async (providerData) => {
    try {
      if (providerData.user_id) {
        navigate(`/profile?user_id=${providerData.user_id}`);
      } else if (providerData.provider_id) {
        const providerResponse = await providerAPI.getById(providerData.provider_id);
        if (providerResponse?.provider?.user_id) {
          navigate(`/profile?user_id=${providerResponse.provider.user_id}`);
        } else {
          toast.error('Profile not available');
        }
      } else {
        toast.error('Profile not available');
      }
    } catch (error) {
      console.error('Error fetching profile:', error);
      toast.error('Failed to load profile');
    }
  };

  const formatPrice = (price) => {
    if (typeof price === 'string') {
      return price;
    }
    return `${parseFloat(price || 0).toFixed(0)}`;
  };

  const formatCount = (count) => {
    if (count >= 1000000) {
      return `${(count / 1000000).toFixed(1)}M`;
    } else if (count >= 1000) {
      return `${(count / 1000).toFixed(1)}k`;
    }
    return count.toString();
  };

  return (
    <div className="min-h-screen bg-gray-50 pb-20">
      {/* Header */}
      <header className="sticky top-0 z-50 bg-white border-b border-gray-200">
        <div className="px-4 py-3 flex items-center gap-3">
          <button
            onClick={() => navigate(-1)}
            className="p-2 hover:bg-gray-100 rounded-full transition-colors"
          >
            <ArrowLeft className="w-5 h-5 text-gray-700" />
          </button>
          <div className="flex-1 relative">
            <SearchIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  handleSearch(searchQuery);
                }
              }}
              placeholder="Search for products, brands, categories..."
              className="w-full pl-10 pr-4 py-2.5 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-[#6A00FF] outline-none font-poppins bg-white"
              autoFocus
            />
          </div>
          {searchQuery && (
            <button
              onClick={() => {
                setSearchQuery('');
                setSearchResults(null);
                setLoading(false);
              }}
              className="p-2 hover:bg-gray-100 rounded-full transition-colors"
            >
              <X className="w-5 h-5 text-gray-500" />
            </button>
          )}
        </div>
      </header>

      <div className="px-4 py-4">
        {/* Search Results */}
        {loading && searchQuery.trim().length >= 2 && (
          <div className="flex justify-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-[#6A00FF]"></div>
          </div>
        )}

        {searchResults && !loading && (
          <div className="mb-4 sm:mb-6">
            <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins mb-3 sm:mb-4">Search Results</h2>
            
            {/* Products */}
            {searchResults.products && searchResults.products.length > 0 && (
              <div className="mb-6">
                <h3 className="text-sm font-semibold text-gray-700 font-poppins mb-3">Products</h3>
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-3">
                  {searchResults.products.map((product) => (
                    <div
                      key={product.listing_id}
                      onClick={() => handleProductClick(product.listing_id)}
                      className="bg-white rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-shadow cursor-pointer"
                    >
                      {product.thumbnail_url && (
                        <div className="relative w-full aspect-square bg-gray-100">
                          <img
                            src={product.thumbnail_url}
                            alt={product.product_name}
                            className="w-full h-full object-cover"
                          />
                          <div className="absolute bottom-2 right-2 bg-black/50 text-white text-xs px-2 py-1 rounded flex items-center gap-1">
                            <Play className="w-3 h-3" />
                            <span>Watch</span>
                          </div>
                        </div>
                      )}
                      <div className="p-3">
                        <h4 className="text-sm font-semibold text-gray-900 font-poppins truncate mb-1">
                          {product.product_name}
                        </h4>
                        {product.price && (
                          <p className="text-sm font-bold text-[#6A00FF] font-poppins">
                            {formatPrice(product.price)}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Influencers */}
            {searchResults.influencers && searchResults.influencers.length > 0 && (
              <div className="mb-6">
                <h3 className="text-sm font-semibold text-gray-700 font-poppins mb-3">Influencers</h3>
                <div className="flex gap-4 overflow-x-auto pb-2 scrollbar-hide">
                  {searchResults.influencers.map((influencer) => (
                    <div
                      key={influencer.provider_id || influencer.user_id}
                      onClick={() => handleProfileClick(influencer)}
                      className="flex flex-col items-center gap-2 flex-shrink-0 cursor-pointer group"
                    >
                      <div className="relative w-20 h-20 rounded-full overflow-hidden border-2 border-[#6A00FF] group-hover:border-purple-600 transition-colors">
                        {influencer.profile_image_url ? (
                          <img
                            src={influencer.profile_image_url}
                            alt={influencer.business_name || influencer.full_name}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full bg-gray-200 flex items-center justify-center">
                            <User className="w-10 h-10 text-gray-400" />
                          </div>
                        )}
                      </div>
                      <div className="text-center max-w-[80px]">
                        <p className="text-xs font-semibold text-gray-900 font-poppins truncate">
                          {influencer.business_name || influencer.full_name || 'Influencer'}
                        </p>
                        {influencer.handle_address && (
                          <p className="text-xs text-gray-500 font-poppins truncate">
                            @{influencer.handle_address}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Brands */}
            {searchResults.brands && searchResults.brands.length > 0 && (
              <div className="mb-6">
                <h3 className="text-sm font-semibold text-gray-700 font-poppins mb-3">Brands</h3>
                <div className="flex gap-4 overflow-x-auto pb-2 scrollbar-hide">
                  {searchResults.brands.map((brand) => (
                    <div
                      key={brand.provider_id || brand.user_id}
                      onClick={() => handleProfileClick(brand)}
                      className="flex flex-col items-center gap-2 flex-shrink-0 cursor-pointer group"
                    >
                      <div className="relative w-20 h-20 rounded-full overflow-hidden border-2 border-[#6A00FF] group-hover:border-purple-600 transition-colors">
                        {brand.profile_image_url ? (
                          <img
                            src={brand.profile_image_url}
                            alt={brand.business_name || brand.full_name}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full bg-gray-200 flex items-center justify-center">
                            <User className="w-10 h-10 text-gray-400" />
                          </div>
                        )}
                      </div>
                      <div className="text-center max-w-[80px]">
                        <p className="text-xs font-semibold text-gray-900 font-poppins truncate">
                          {brand.business_name || brand.full_name || 'Brand'}
                        </p>
                        {brand.handle_address && (
                          <p className="text-xs text-gray-500 font-poppins truncate">
                            @{brand.handle_address}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Categories */}
            {searchResults.categories && searchResults.categories.length > 0 && (
              <div className="mb-6">
                <h3 className="text-sm font-semibold text-gray-700 font-poppins mb-3">Categories</h3>
                <div className="grid grid-cols-2 sm:grid-cols-4 gap-3">
                  {searchResults.categories.map((category) => (
                    <div
                      key={category.category_id}
                      onClick={() => navigate(`/category?category_id=${category.category_id}&category_name=${encodeURIComponent(category.category_name || 'Category')}`)}
                      className="bg-white rounded-lg p-4 shadow-sm hover:shadow-md transition-shadow cursor-pointer text-center"
                    >
                      {category.image_url && (
                        <img
                          src={category.image_url}
                          alt={category.category_name}
                          className="w-16 h-16 mx-auto mb-2 object-cover rounded-lg"
                        />
                      )}
                      <h4 className="text-sm font-semibold text-gray-900 font-poppins">
                        {category.category_name}
                      </h4>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* No results */}
            {(!searchResults.products || searchResults.products.length === 0) &&
             (!searchResults.brands || searchResults.brands.length === 0) &&
             (!searchResults.influencers || searchResults.influencers.length === 0) &&
             (!searchResults.categories || searchResults.categories.length === 0) && (
              <div className="text-center py-8">
                <p className="text-gray-500 font-poppins">No results found</p>
              </div>
            )}
          </div>
        )}

        {!searchResults && !loading && (
          <>
            {/* Recent Searches */}
            {recentSearches.length > 0 && (
              <div className="mb-4 sm:mb-6">
                <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins mb-2 sm:mb-3">Recent Searches</h2>
                <div className="flex flex-wrap gap-2">
                  {recentSearches.map((search, index) => (
                    <button
                      key={index}
                      onClick={() => handleSuggestionClick(search)}
                      className="px-4 py-2 bg-white border border-gray-300 rounded-full text-sm text-gray-700 font-poppins hover:bg-gray-50 hover:border-[#6A00FF] transition-colors"
                    >
                      {search}
                    </button>
                  ))}
                </div>
              </div>
            )}

            {/* Trending Searches */}
            {trendingSearches.length > 0 && (
              <div className="mb-4 sm:mb-6">
                <div className="flex items-center gap-2 mb-2 sm:mb-3">
                  <TrendingUp className="w-4 h-4 sm:w-5 sm:h-5 text-[#6A00FF]" />
                  <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins">Trending Searches</h2>
                </div>
                <div className="flex flex-wrap gap-2">
                  {trendingSearches.map((search, index) => (
                    <button
                      key={index}
                      onClick={() => handleSuggestionClick(search)}
                      className="px-4 py-2 bg-white border border-gray-300 rounded-full text-sm text-gray-700 font-poppins hover:bg-gray-50 hover:border-[#6A00FF] transition-colors"
                    >
                      {search}
                    </button>
                  ))}
                </div>
              </div>
            )}

            {/* Recently Uploaded Products */}
            <div className="mb-4 sm:mb-6">
              <div className="flex items-center gap-2 mb-2 sm:mb-3">
                <Clock className="w-4 h-4 sm:w-5 sm:h-5 text-[#6A00FF]" />
                <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins">Recently Uploaded Products</h2>
              </div>
              {loadingSections.recentlyUploaded ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-[#6A00FF]"></div>
                </div>
              ) : recentlyUploaded.length > 0 ? (
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-3">
                  {recentlyUploaded.map((product) => (
                    <div
                      key={product.listing_id}
                      onClick={() => handleProductClick(product.listing_id)}
                      className="bg-white rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-shadow cursor-pointer"
                    >
                      {product.thumbnail_url && (
                        <div className="relative w-full aspect-square bg-gray-100">
                          <img
                            src={product.thumbnail_url}
                            alt={product.product_name}
                            className="w-full h-full object-cover"
                          />
                          <div className="absolute top-2 left-2 bg-red-500 text-white text-xs px-2 py-1 rounded">
                            New
                          </div>
                        </div>
                      )}
                      <div className="p-3">
                        <h4 className="text-sm font-semibold text-gray-900 font-poppins truncate mb-1">
                          {product.product_name}
                        </h4>
                        {product.price && (
                          <p className="text-sm font-bold text-[#6A00FF] font-poppins">
                            {formatPrice(product.price)}
                          </p>
                        )}
                        <div className="flex items-center gap-2 mt-1 text-xs text-gray-500">
                          <Eye className="w-3 h-3" />
                          <span>{product.view_count || 0} views</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 text-sm font-poppins">No recently uploaded products</p>
              )}
            </div>

            {/* Most Liked Videos */}
            <div className="mb-4 sm:mb-6">
              <div className="flex items-center gap-2 mb-2 sm:mb-3">
                <Star className="w-4 h-4 sm:w-5 sm:h-5 text-[#6A00FF]" />
                <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins">Most Liked Videos</h2>
              </div>
              {loadingSections.mostLiked ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-[#6A00FF]"></div>
                </div>
              ) : mostLiked.length > 0 ? (
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-3">
                  {mostLiked.map((product) => (
                    <div
                      key={product.listing_id}
                      onClick={() => handleProductClick(product.listing_id)}
                      className="bg-white rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-shadow cursor-pointer"
                    >
                      {product.thumbnail_url && (
                        <div className="relative w-full aspect-square bg-gray-100">
                          <img
                            src={product.thumbnail_url}
                            alt={product.product_name}
                            className="w-full h-full object-cover"
                          />
                          <div className="absolute bottom-2 right-2 bg-black/50 text-white text-xs px-2 py-1 rounded flex items-center gap-1">
                            <Heart className="w-3 h-3 fill-pink-400 text-pink-400" />
                            <span>{formatCount(product.like_count || 0)}</span>
                          </div>
                        </div>
                      )}
                      <div className="p-3">
                        <h4 className="text-sm font-semibold text-gray-900 font-poppins truncate mb-1">
                          {product.product_name}
                        </h4>
                        {product.price && (
                          <p className="text-sm font-bold text-[#6A00FF] font-poppins">
                            {formatPrice(product.price)}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 text-sm font-poppins">No products found</p>
              )}
            </div>

            {/* Most Watched Products */}
            <div className="mb-4 sm:mb-6">
              <div className="flex items-center gap-2 mb-2 sm:mb-3">
                <Eye className="w-4 h-4 sm:w-5 sm:h-5 text-[#6A00FF]" />
                <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins">Most Watched Products</h2>
              </div>
              {loadingSections.mostWatched ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-[#6A00FF]"></div>
                </div>
              ) : mostWatched.length > 0 ? (
                <div className="grid grid-cols-2 sm:grid-cols-3 gap-3">
                  {mostWatched.map((product) => (
                    <div
                      key={product.listing_id}
                      onClick={() => handleProductClick(product.listing_id)}
                      className="bg-white rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-shadow cursor-pointer"
                    >
                      {product.thumbnail_url && (
                        <div className="relative w-full aspect-square bg-gray-100">
                          <img
                            src={product.thumbnail_url}
                            alt={product.product_name}
                            className="w-full h-full object-cover"
                          />
                          <div className="absolute bottom-2 right-2 bg-black/50 text-white text-xs px-2 py-1 rounded flex items-center gap-1">
                            <Eye className="w-3 h-3" />
                            <span>{formatCount(product.view_count || 0)}</span>
                          </div>
                        </div>
                      )}
                      <div className="p-3">
                        <h4 className="text-sm font-semibold text-gray-900 font-poppins truncate mb-1">
                          {product.product_name}
                        </h4>
                        {product.price && (
                          <p className="text-sm font-bold text-[#6A00FF] font-poppins">
                            {formatPrice(product.price)}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 text-sm font-poppins">No products found</p>
              )}
            </div>

            {/* Influencers Profiles */}
            <div className="mb-4 sm:mb-6">
              <div className="flex items-center gap-2 mb-2 sm:mb-3">
                <Users className="w-4 h-4 sm:w-5 sm:h-5 text-[#6A00FF]" />
                <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins">Influencers to Follow</h2>
              </div>
              {loadingSections.influencers ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-[#6A00FF]"></div>
                </div>
              ) : influencers.length > 0 ? (
                <div className="flex gap-4 overflow-x-auto pb-2 scrollbar-hide">
                  {influencers.map((influencer) => (
                    <div
                      key={influencer.provider_id || influencer.user_id}
                      onClick={() => handleProfileClick(influencer)}
                      className="flex flex-col items-center gap-2 flex-shrink-0 cursor-pointer group"
                    >
                      <div className="relative w-20 h-20 rounded-full overflow-hidden border-2 border-[#6A00FF] group-hover:border-purple-600 transition-colors">
                        {influencer.profile_image_url ? (
                          <img
                            src={influencer.profile_image_url}
                            alt={influencer.business_name || influencer.full_name}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full bg-gray-200 flex items-center justify-center">
                            <User className="w-10 h-10 text-gray-400" />
                          </div>
                        )}
                      </div>
                      <div className="text-center max-w-[80px]">
                        <p className="text-xs font-semibold text-gray-900 font-poppins truncate">
                          {influencer.business_name || influencer.full_name || 'Influencer'}
                        </p>
                        {influencer.handle_address && (
                          <p className="text-xs text-gray-500 font-poppins truncate">
                            @{influencer.handle_address}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 text-sm font-poppins">No influencers found</p>
              )}
            </div>

            {/* Brands Profiles */}
            <div className="mb-4 sm:mb-6">
              <div className="flex items-center gap-2 mb-2 sm:mb-3">
                <Users className="w-4 h-4 sm:w-5 sm:h-5 text-[#6A00FF]" />
                <h2 className="text-base sm:text-lg font-semibold text-gray-900 font-poppins">Brands to Follow</h2>
              </div>
              {loadingSections.brands ? (
                <div className="flex justify-center py-8">
                  <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-[#6A00FF]"></div>
                </div>
              ) : brands.length > 0 ? (
                <div className="flex gap-4 overflow-x-auto pb-2 scrollbar-hide">
                  {brands.map((brand) => (
                    <div
                      key={brand.provider_id || brand.user_id}
                      onClick={() => handleProfileClick(brand)}
                      className="flex flex-col items-center gap-2 flex-shrink-0 cursor-pointer group"
                    >
                      <div className="relative w-20 h-20 rounded-full overflow-hidden border-2 border-[#6A00FF] group-hover:border-purple-600 transition-colors">
                        {brand.profile_image_url ? (
                          <img
                            src={brand.profile_image_url}
                            alt={brand.business_name || brand.full_name}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full bg-gray-200 flex items-center justify-center">
                            <User className="w-10 h-10 text-gray-400" />
                          </div>
                        )}
                      </div>
                      <div className="text-center max-w-[80px]">
                        <p className="text-xs font-semibold text-gray-900 font-poppins truncate">
                          {brand.business_name || brand.full_name || 'Brand'}
                        </p>
                        {brand.handle_address && (
                          <p className="text-xs text-gray-500 font-poppins truncate">
                            @{brand.handle_address}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 text-sm font-poppins">No brands found</p>
              )}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Search;
