import React, { useState, useEffect, useRef } from 'react';
import { ArrowLeft, Save, Loader, Upload, X } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { userAPI, providerAPI, uploadAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const EditProfile = () => {
  const navigate = useNavigate();
  const { user: currentUser } = useAuth();
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [activeTab, setActiveTab] = useState('user');
  const [user, setUser] = useState(null);
  const [provider, setProvider] = useState(null);
  const [uploadingBanner, setUploadingBanner] = useState(false);
  const [uploadingProfile, setUploadingProfile] = useState(false);
  const bannerFileInputRef = useRef(null);
  const profileFileInputRef = useRef(null);
  const [formData, setFormData] = useState({
    // User fields
    name: '',
    email: '',
    phone: '',
    profile_picture: '',
    banner: '',
    
    // Provider fields
    name_provider: '',
    phone_provider: '',
    business_name: '',
    type: 'brand',
    location: '',
    gst_number: '',
    business_address: '',
    handle_address: ''
  });

  useEffect(() => {
    loadProfileData();
  }, []);

  const loadProfileData = async () => {
    try {
      setLoading(true);
      const [userData, providerData] = await Promise.all([
        userAPI.getProfile().catch(() => null),
        providerAPI.getMe().catch(() => null)
      ]);

      const userInfo = userData?.user || userData || null;
      const providerInfo = providerData?.provider || providerData || null;

      setUser(userInfo);
      setProvider(providerInfo);

      if (userInfo) {
        setFormData(prev => ({
          ...prev,
          name: userInfo.full_name || userInfo.name || '',
          email: userInfo.email || '',
          phone: userInfo.phone || '',
          profile_picture: userInfo.profile_picture || userInfo.profile_image_url || '',
          banner: userInfo.banner || ''
        }));
      }

      if (providerInfo) {
        setFormData(prev => ({
          ...prev,
          name_provider: providerInfo.name || '',
          phone_provider: providerInfo.phone || '',
          business_name: providerInfo.business_name || '',
          type: (providerInfo.provider_type || providerInfo.type || 'BRAND').toString().toLowerCase(),
          location: providerInfo.location || '',
          gst_number: providerInfo.gst || providerInfo.gst_number || '',
          business_address: providerInfo.business_address || '',
          handle_address: providerInfo.handle_address || ''
        }));
      }
    } catch (error) {
      console.error('Error loading profile:', error);
      toast.error('Failed to load profile data');
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleBannerUpload = async (e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    
    setUploadingBanner(true);
    try {
      const response = await uploadAPI.uploadImage(file);
      if (response.ok && response.url) {
        setFormData(prev => ({ ...prev, banner: response.url }));
        toast.success('Banner uploaded successfully!');
      } else {
        toast.error(response.message || 'Failed to upload banner');
      }
    } catch (error) {
      console.error('Error uploading banner:', error);
      toast.error('Failed to upload banner');
    } finally {
      setUploadingBanner(false);
      if (bannerFileInputRef.current) bannerFileInputRef.current.value = '';
    }
  };

  const handleProfileUpload = async (e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    
    setUploadingProfile(true);
    try {
      const response = await uploadAPI.uploadImage(file);
      if (response.ok && response.url) {
        setFormData(prev => ({ ...prev, profile_picture: response.url }));
        toast.success('Profile picture uploaded successfully!');
      } else {
        toast.error(response.message || 'Failed to upload profile picture');
      }
    } catch (error) {
      console.error('Error uploading profile picture:', error);
      toast.error('Failed to upload profile picture');
    } finally {
      setUploadingProfile(false);
      if (profileFileInputRef.current) profileFileInputRef.current.value = '';
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);

    try {
      if (activeTab === 'user') {
        const payload = {
          name: formData.name,
          email: formData.email,
          phone: formData.phone,
          profile_picture: formData.profile_picture === '' ? null : formData.profile_picture,
          banner: formData.banner === '' ? null : formData.banner
        };
        await userAPI.updateProfile(payload);
        toast.success('Profile updated successfully!');
      } else if (activeTab === 'provider' && provider) {
        await providerAPI.updateProfile({
          name: formData.name_provider,
          phone: formData.phone_provider,
          business_name: formData.business_name,
          // type is intentionally excluded - users cannot change their provider type
          // Backend will use existing provider type from database
          location: formData.location,
          gst_number: formData.gst_number,
          business_address: formData.business_address,
          handle_address: formData.handle_address
        });
        toast.success('Provider profile updated successfully!');
      }
      
      // Redirect to profile after save
      navigate('/profile');
    } catch (error) {
      console.error('Error updating profile:', error);
      toast.error(error.response?.data?.error || 'Failed to update profile');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-[#6A00FF]" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-3 sm:px-4 md:px-6 py-3 sm:py-4 sticky top-0 z-20">
        <div className="flex items-center gap-2 sm:gap-4 max-w-4xl mx-auto">
          <button
            onClick={() => navigate('/profile')}
            className="p-1.5 sm:p-2 hover:bg-gray-100 rounded-full transition-colors active:scale-95"
            aria-label="Go back"
          >
            <ArrowLeft className="w-5 h-5 sm:w-6 sm:h-6 text-gray-900" />
          </button>
          <h1 className="text-lg sm:text-xl font-bold text-gray-900 font-poppins">Edit Profile</h1>
        </div>
      </div>

      <div className="max-w-4xl mx-auto px-3 sm:px-4 md:px-6 py-4 sm:py-6 pb-32 sm:pb-40">
        {/* Tabs */}
        <div className="flex gap-1 sm:gap-2 mb-4 sm:mb-6 border-b border-gray-200 overflow-x-auto">
          <button
            onClick={() => setActiveTab('user')}
            className={`px-3 sm:px-4 py-2 font-poppins text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
              activeTab === 'user'
                ? 'text-[#6A00FF] border-b-2 border-[#6A00FF]'
                : 'text-gray-600 active:text-gray-900'
            }`}
          >
            User Profile
          </button>
          {provider && (
            <button
              onClick={() => setActiveTab('provider')}
              className={`px-3 sm:px-4 py-2 font-poppins text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                activeTab === 'provider'
                  ? 'text-[#6A00FF] border-b-2 border-[#6A00FF]'
                  : 'text-gray-600 active:text-gray-900'
              }`}
            >
              Business Profile
            </button>
          )}
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="space-y-4 sm:space-y-6">
          {activeTab === 'user' ? (
            <div className="space-y-4 sm:space-y-5">
              {/* Banner */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Profile Banner
                </label>
                <div className="space-y-2 sm:space-y-3">
                  <div className="flex flex-col sm:flex-row items-stretch sm:items-center gap-2 sm:gap-3">
                    <input
                      type="url"
                      name="banner"
                      value={formData.banner}
                      onChange={handleInputChange}
                      placeholder="Paste URL or upload"
                      className="flex-1 px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                    />
                    <button
                      type="button"
                      onClick={() => bannerFileInputRef.current?.click()}
                      disabled={uploadingBanner}
                      className="px-4 py-2.5 sm:py-2 bg-[#6A00FF] hover:bg-purple-700 active:bg-purple-800 text-white rounded-lg transition-colors font-poppins text-sm disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2 min-w-[100px] sm:min-w-[120px]"
                    >
                      {uploadingBanner ? (
                        <>
                          <Loader className="w-4 h-4 animate-spin" />
                          <span className="hidden sm:inline">Uploading...</span>
                        </>
                      ) : (
                        <>
                          <Upload className="w-4 h-4" />
                          <span>Upload</span>
                        </>
                      )}
                    </button>
                    <input
                      ref={bannerFileInputRef}
                      type="file"
                      accept="image/*"
                      onChange={handleBannerUpload}
                      className="hidden"
                    />
                  </div>
                  {formData.banner && (
                    <div className="mt-2 w-full h-24 sm:h-32 rounded-lg overflow-hidden border border-gray-200 relative">
                      <img src={formData.banner} alt="Banner preview" className="w-full h-full object-cover" />
                      <button
                        type="button"
                        onClick={() => setFormData(prev => ({ ...prev, banner: '' }))}
                        className="absolute top-2 right-2 bg-red-500 hover:bg-red-600 active:bg-red-700 text-white rounded-full p-1.5 sm:p-2 transition-colors"
                        aria-label="Remove banner"
                      >
                        <X className="w-3.5 h-3.5 sm:w-4 sm:h-4" />
                      </button>
                    </div>
                  )}
                </div>
              </div>

              {/* Profile Picture */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Profile Picture
                </label>
                <div className="space-y-2 sm:space-y-3">
                  {formData.profile_picture && (
                    <div className="flex justify-center sm:justify-start mb-2">
                      <div className="w-16 h-16 sm:w-20 sm:h-20 rounded-full overflow-hidden border-2 border-gray-200 flex-shrink-0">
                        <img src={formData.profile_picture} alt="Profile preview" className="w-full h-full object-cover" />
                      </div>
                    </div>
                  )}
                  <div className="flex flex-col sm:flex-row items-stretch sm:items-center gap-2 sm:gap-3">
                    <input
                      type="url"
                      name="profile_picture"
                      value={formData.profile_picture}
                      onChange={handleInputChange}
                      placeholder="Paste URL or upload"
                      className="flex-1 px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                    />
                    <div className="flex gap-2 sm:gap-3">
                      <button
                        type="button"
                        onClick={() => profileFileInputRef.current?.click()}
                        disabled={uploadingProfile}
                        className="flex-1 sm:flex-none px-4 py-2.5 sm:py-2 bg-[#6A00FF] hover:bg-purple-700 active:bg-purple-800 text-white rounded-lg transition-colors font-poppins text-sm disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-2 min-w-[100px] sm:min-w-[120px]"
                      >
                        {uploadingProfile ? (
                          <>
                            <Loader className="w-4 h-4 animate-spin" />
                            <span className="hidden sm:inline">Uploading...</span>
                          </>
                        ) : (
                          <>
                            <Upload className="w-4 h-4" />
                            <span>Upload</span>
                          </>
                        )}
                      </button>
                      {formData.profile_picture && (
                        <button
                          type="button"
                          onClick={() => setFormData(prev => ({ ...prev, profile_picture: '' }))}
                          className="px-3 sm:px-4 py-2.5 sm:py-2 bg-red-500 hover:bg-red-600 active:bg-red-700 text-white rounded-lg transition-colors font-poppins text-sm whitespace-nowrap"
                        >
                          Remove
                        </button>
                      )}
                    </div>
                    <input
                      ref={profileFileInputRef}
                      type="file"
                      accept="image/*"
                      onChange={handleProfileUpload}
                      className="hidden"
                    />
                  </div>
                </div>
              </div>

              {/* Full Name */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Full Name
                </label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  required
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Email */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Email
                </label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  required
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Phone */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Phone
                </label>
                <input
                  type="tel"
                  name="phone"
                  value={formData.phone}
                  onChange={handleInputChange}
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>
            </div>
          ) : (
            <div className="space-y-4 sm:space-y-5">
              {/* Business Name */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Business Name
                </label>
                <input
                  type="text"
                  name="business_name"
                  value={formData.business_name}
                  onChange={handleInputChange}
                  required
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Provider Type */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Business Type
                </label>
                <select
                  name="type"
                  value={formData.type}
                  onChange={handleInputChange}
                  disabled
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins bg-gray-100 cursor-not-allowed"
                >
                  <option value="brand">Brand</option>
                  <option value="influencer">Influencer</option>
                </select>
                <p className="text-xs text-gray-500 mt-1 font-poppins">Business type cannot be changed</p>
              </div>

              {/* Location */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Location
                </label>
                <input
                  type="text"
                  name="location"
                  value={formData.location}
                  onChange={handleInputChange}
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Handle/Website */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Handle/Website
                </label>
                <input
                  type="text"
                  name="handle_address"
                  value={formData.handle_address}
                  onChange={handleInputChange}
                  placeholder="https://example.com or @handle"
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* GST Number */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  GST Number (Optional)
                </label>
                <input
                  type="text"
                  name="gst_number"
                  value={formData.gst_number}
                  onChange={handleInputChange}
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Business Address */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Business Address (Optional)
                </label>
                <textarea
                  name="business_address"
                  value={formData.business_address}
                  onChange={handleInputChange}
                  rows={3}
                  className="w-full px-3 sm:px-4 py-2.5 sm:py-2 text-sm sm:text-base border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins resize-none"
                />
              </div>
            </div>
          )}

          {/* Save Button */}
          <div className="flex flex-col sm:flex-row gap-2 sm:gap-3 pt-4 sm:pt-6">
            <button
              type="button"
              onClick={() => navigate('/profile')}
              className="w-full sm:w-auto px-6 py-3 sm:py-2 border border-gray-300 rounded-lg hover:bg-gray-50 active:bg-gray-100 transition-colors font-poppins text-sm sm:text-base"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={saving}
              className="w-full sm:flex-1 flex items-center justify-center gap-2 bg-[#6A00FF] hover:bg-purple-700 active:bg-purple-800 text-white px-6 py-3 sm:py-2 rounded-lg transition-colors font-poppins text-sm sm:text-base disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {saving ? (
                <>
                  <Loader className="w-4 h-4 animate-spin" />
                  <span>Saving...</span>
                </>
              ) : (
                <>
                  <Save className="w-4 h-4" />
                  <span>Save Changes</span>
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default EditProfile;


