import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Bell, MessageCircle, Plus, Package, Briefcase, Eye, Heart, MousePointer, Loader } from 'lucide-react';
import { listingAPI } from '../utils/api';
import { toast } from 'react-hot-toast';

const MyListings = () => {
  const navigate = useNavigate();
  const [listings, setListings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('ALL');

  useEffect(() => {
    loadListings();
  }, []);

  const loadListings = async () => {
    try {
      setLoading(true);
      const data = await listingAPI.getMy();
      const approvedListings = data.listings || [];
      const pendingRequests = data.pending_requests || [];
      // Combine approved listings and pending requests
      setListings([...pendingRequests, ...approvedListings]);
    } catch (error) {
      console.error('Error loading listings:', error);
      toast.error('Failed to load listings');
    } finally {
      setLoading(false);
    }
  };

  const filteredListings = listings.filter(listing => 
    filter === 'ALL' || listing.type === filter
  );

  const stats = {
    total: listings.length,
    products: listings.filter(l => l.type === 'PRODUCT').length,
    services: listings.filter(l => l.type === 'SERVICE').length,
    totalViews: listings.reduce((sum, l) => sum + (l.view_count || 0), 0),
    totalLoves: listings.reduce((sum, l) => sum + (l.love_count || 0), 0),
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="w-full bg-white border-b border-gray-200 sticky top-0 z-50">
        <div className="px-4 sm:px-5 py-3.5 sm:py-4 flex items-center justify-between">
          <div className="flex items-center">
            <span className="text-2xl sm:text-[24px] font-poppins font-bold">
              <span className="text-black">Get</span>
              <span className="text-[#6C4DFF]">nayi</span>
            </span>
          </div>
          <div className="flex items-center gap-3 sm:gap-4">
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <Bell className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <MessageCircle className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
          </div>
        </div>
      </header>

      <div className="px-4 sm:px-5 py-6 sm:py-8 pb-32 sm:pb-12 max-w-7xl mx-auto">
        {/* Header Section */}
        <div className="flex items-center justify-between mb-6">
          <div>
            <h1 className="text-2xl sm:text-3xl font-bold text-gray-900 font-poppins">My Listings</h1>
            <p className="text-gray-600 text-xs sm:text-sm mt-1 font-poppins">
              {stats.total} total listing{stats.total !== 1 ? 's' : ''}
            </p>
          </div>
          <button
            onClick={() => navigate('/listing')}
            className="bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-4 sm:px-6 py-2 sm:py-3 rounded-full text-sm sm:text-base font-semibold transition-all flex items-center gap-2 shadow-md hover:shadow-lg active:scale-[0.96] font-poppins"
          >
            <Plus className="w-4 h-4 sm:w-5 sm:h-5" />
            <span className="hidden sm:inline">New Listing</span>
            <span className="sm:hidden">New</span>
          </button>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-2 md:grid-cols-4 gap-3 sm:gap-4 mb-6">
          <div className="bg-white border border-gray-200 rounded-xl p-3 sm:p-4 shadow-sm">
            <div className="text-xl sm:text-2xl font-bold text-gray-900 font-poppins">{stats.total}</div>
            <div className="text-xs sm:text-sm text-gray-600 font-poppins">Total Listings</div>
          </div>
          <div className="bg-white border border-gray-200 rounded-xl p-3 sm:p-4 shadow-sm">
            <div className="text-xl sm:text-2xl font-bold text-[#6C4DFF] font-poppins">{stats.products}</div>
            <div className="text-xs sm:text-sm text-gray-600 font-poppins">Products</div>
          </div>
          <div className="bg-white border border-gray-200 rounded-xl p-3 sm:p-4 shadow-sm">
            <div className="text-xl sm:text-2xl font-bold text-gray-600 font-poppins">{stats.services}</div>
            <div className="text-xs sm:text-sm text-gray-600 font-poppins">Services</div>
          </div>
          <div className="bg-white border border-gray-200 rounded-xl p-3 sm:p-4 shadow-sm">
            <div className="text-xl sm:text-2xl font-bold text-[#6C4DFF] font-poppins">{stats.totalViews}</div>
            <div className="text-xs sm:text-sm text-gray-600 font-poppins">Total Views</div>
          </div>
        </div>

        {/* Filter Tabs */}
        <div className="bg-gray-50 border border-gray-200 rounded-xl shadow-sm p-2 mb-6 flex gap-2">
          {['ALL', 'PRODUCT', 'SERVICE'].map(tab => (
            <button
              key={tab}
              onClick={() => setFilter(tab)}
              className={`flex-1 py-2 px-3 sm:px-4 rounded-lg text-sm sm:text-base font-medium transition-all font-poppins ${
                filter === tab
                  ? 'bg-[#6C4DFF] text-white shadow-sm'
                  : 'text-gray-600 hover:bg-gray-100'
              }`}
            >
              {tab === 'ALL' ? 'All' : tab === 'PRODUCT' ? 'Products' : 'Services'}
            </button>
          ))}
        </div>

        {/* Listings Grid */}
        {filteredListings.length === 0 ? (
          <div className="bg-white border border-gray-200 rounded-2xl shadow-sm p-8 sm:p-12 text-center">
            <div className="w-16 h-16 sm:w-20 sm:h-20 bg-[#6C4DFF] rounded-full flex items-center justify-center mx-auto mb-4">
              <Package className="w-8 h-8 sm:w-10 sm:h-10 text-white" />
            </div>
            <h3 className="text-lg sm:text-xl font-semibold text-gray-900 mb-2 font-poppins">No listings yet</h3>
            <p className="text-sm sm:text-base text-gray-600 mb-6 font-poppins">Start by creating your first listing</p>
            <button
              onClick={() => navigate('/listing')}
              className="bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-6 sm:px-8 py-2 sm:py-3 rounded-full text-sm sm:text-base font-semibold transition-all inline-flex items-center gap-2 shadow-md hover:shadow-lg active:scale-[0.96] font-poppins"
            >
              <Plus className="w-4 h-4 sm:w-5 sm:h-5" />
              Create Listing
            </button>
          </div>
        ) : (
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredListings.map(listing => (
              <div
                key={listing.listing_id || listing.request_id}
                className="bg-white border border-gray-200 rounded-2xl shadow-sm hover:shadow-lg transition-all overflow-hidden group cursor-pointer active:scale-[0.98]"
                onClick={() => !listing.is_request && listing.listing_id && navigate(`/listing/${listing.listing_id}`)}
              >
                {/* Thumbnail */}
                <div className="relative h-48 bg-gray-100">
                  {listing.thumbnail_url ? (
                    <img
                      src={listing.thumbnail_url}
                      alt={listing.product_name}
                      className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                    />
                  ) : (
                    <div className="w-full h-full flex items-center justify-center bg-[#E8D5FF]">
                      {listing.type === 'PRODUCT' ? (
                        <Package className="w-16 h-16 text-[#6C4DFF]" />
                      ) : (
                        <Briefcase className="w-16 h-16 text-[#6C4DFF]" />
                      )}
                    </div>
                  )}
                  <div className="absolute top-2 right-2 sm:top-3 sm:right-3 flex flex-col gap-1.5 sm:gap-2 items-end">
                    {listing.is_request && listing.status === 'PENDING' && (
                      <div className="bg-yellow-500 text-white px-2 sm:px-3 py-0.5 sm:py-1 rounded-full text-xs font-medium font-poppins">
                        Pending Approval
                      </div>
                    )}
                    <div className="bg-white/90 backdrop-blur-sm px-2 sm:px-3 py-0.5 sm:py-1 rounded-full text-xs sm:text-sm font-medium font-poppins text-gray-900 border border-gray-200">
                      {listing.type === 'PRODUCT' ? ' Product' : ' Service'}
                    </div>
                  </div>
                </div>

                {/* Content */}
                <div className="p-3 sm:p-4">
                  <h3 className="font-bold text-base sm:text-lg text-gray-900 mb-1 line-clamp-1 font-poppins">
                    {listing.product_name}
                  </h3>
                  <p className="text-[#6C4DFF] font-bold text-lg sm:text-xl mb-2 font-poppins">
                    {Number(listing.price).toLocaleString()}
                  </p>
                  <p className="text-gray-600 text-xs sm:text-sm line-clamp-2 mb-3 font-poppins">
                    {listing.description}
                  </p>

                  {/* Stats - Only show for approved listings */}
                  {!listing.is_request && (
                    <div className="flex items-center gap-3 sm:gap-4 text-xs sm:text-sm text-gray-600 pt-3 border-t border-gray-100">
                      <div className="flex items-center gap-1">
                        <Eye className="w-3 h-3 sm:w-4 sm:h-4" />
                        <span className="font-poppins">{listing.view_count || 0}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <Heart className="w-3 h-3 sm:w-4 sm:h-4" />
                        <span className="font-poppins">{listing.love_count || 0}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <MousePointer className="w-3 h-3 sm:w-4 sm:h-4" />
                        <span className="font-poppins">{listing.click_count || 0}</span>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default MyListings;
