import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Heart, Package, Eye, ThumbsUp, Loader, Trash2 } from 'lucide-react';
import { wishlistAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const Wishlist = () => {
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();
  const [wishlist, setWishlist] = useState([]);
  const [grouped, setGrouped] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    if (isAuthenticated) {
      loadWishlist();
    } else {
      setLoading(false);
    }
  }, [isAuthenticated]);

  const loadWishlist = async () => {
    try {
      setLoading(true);
      setError(false);
      const data = await wishlistAPI.getAll();
      if (data && data.wishlist && Array.isArray(data.wishlist)) {
        setWishlist(data.wishlist);
        // Group by video_id (fallback to listing_id if no video)
        const groups = {};
        data.wishlist.forEach(item => {
          const key = item.video_id || `listing-${item.listing_id}`;
          if (!groups[key]) {
            groups[key] = {
              video_id: item.video_id,
              provider_id: item.provider_id,
              video_url: item.video_url,
              video_thumbnail_url: item.video_thumbnail_url,
              stats: {
                views: item.view_count || 0,
                likes: item.like_count || 0,
                loves: item.love_count || 0
              },
              products: []
            };
          }
          groups[key].products.push({
            listing_id: item.listing_id,
            product_name: item.product_name,
            price: item.price,
            thumbnail: item.thumbnail,
            link: item.link,
            provider_name: item.provider_name || item.business_name
          });
        });
        setGrouped(Object.values(groups));
      } else {
        setWishlist([]);
        setGrouped([]);
      }
    } catch (error) {
      console.error('Error loading wishlist:', error);
      setWishlist([]);
      setGrouped([]);
      setError(true);
      toast.error('Could not fetch wishlist');
    } finally {
      setLoading(false);
    }
  };

  const handleRemove = async (listingId, e) => {
    e.stopPropagation();
    try {
      await wishlistAPI.remove(listingId);
      setWishlist(wishlist.filter(item => item.listing_id !== listingId));
      toast.success('Removed from wishlist');
    } catch (error) {
      console.error('Error removing from wishlist:', error);
      toast.error('Failed to remove');
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-white">
        <Loader className="w-8 h-8 animate-spin text-[#6A00FF]" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Simple Header */}
      <header className="sticky top-0 z-[100] bg-white border-b border-gray-200 shadow-sm">
        <div className="px-4 py-3 flex items-center justify-center">
          <div className="flex items-center gap-2">
            <span className="text-lg sm:text-xl font-poppins font-bold">
              <span className="text-gray-900">Get</span>
              <span className="text-[#6A00FF]">Nayi</span>
            </span>
          </div>
        </div>
      </header>
      
      <div className="pt-4 pb-32 sm:pb-24 px-4 relative z-10">
        <div className="max-w-7xl mx-auto">
          <div className="mb-4 sm:mb-6">
            <h1 className="text-xl sm:text-3xl font-bold text-gray-900 font-poppins">My Wishlist</h1>
            <p className="text-gray-500 sm:text-gray-600 text-xs sm:text-sm mt-0.5 sm:mt-1 font-poppins">
              {wishlist.length} saved item{wishlist.length !== 1 ? 's' : ''}
            </p>
          </div>

          {error ? (
            <div className="bg-gray-100 border border-gray-200 rounded-2xl shadow-sm p-6 sm:p-12 text-center">
              <div className="w-14 h-14 sm:w-20 sm:h-20 bg-red-500 rounded-full flex items-center justify-center mx-auto mb-3 sm:mb-4">
                <Heart className="w-7 h-7 sm:w-10 sm:h-10 text-white" />
              </div>
              <h3 className="text-lg sm:text-xl font-semibold text-gray-900 mb-1 sm:mb-2 font-poppins">Could not fetch wishlist</h3>
              <p className="text-gray-600 text-sm sm:text-base mb-4 sm:mb-6 font-poppins">Please try again later</p>
              <button
                onClick={loadWishlist}
                className="bg-[#6A00FF] hover:bg-purple-700 text-white px-6 sm:px-8 py-2.5 sm:py-3 rounded-xl font-semibold text-sm sm:text-base transition-all inline-flex items-center gap-2 font-poppins"
              >
                Retry
              </button>
            </div>
          ) : wishlist.length === 0 ? (
            <div className="bg-gray-100 border border-gray-200 rounded-2xl shadow-sm p-6 sm:p-12 text-center">
              <div className="w-14 h-14 sm:w-20 sm:h-20 bg-[#6A00FF] rounded-full flex items-center justify-center mx-auto mb-3 sm:mb-4">
                <Heart className="w-7 h-7 sm:w-10 sm:h-10 text-white" />
              </div>
              <h3 className="text-lg sm:text-xl font-semibold text-gray-900 mb-1 sm:mb-2 font-poppins">Your wishlist is empty</h3>
              <p className="text-gray-600 text-sm sm:text-base mb-4 sm:mb-6 font-poppins">Start saving products and services you love</p>
              <button
                onClick={() => navigate('/home')}
                className="bg-[#6A00FF] hover:bg-purple-700 text-white px-6 sm:px-8 py-2.5 sm:py-3 rounded-xl font-semibold text-sm sm:text-base transition-all inline-flex items-center gap-2 font-poppins"
              >
                Browse Listings
              </button>
            </div>
          ) : (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
              {grouped.map(group => (
                <div
                  key={group.video_id || `group-${group.products[0]?.listing_id}`}
                  className="bg-white border border-gray-200 rounded-2xl shadow-sm hover:shadow-lg transition-all overflow-hidden relative"
                >
                  {/* Remove first product removes the whole group (video) */}
                  {group.products[0] && (
                    <button
                      onClick={(e) => handleRemove(group.products[0].listing_id, e)}
                      className="absolute top-3 right-3 z-10 bg-red-500 hover:bg-red-600 text-white p-2 rounded-full shadow-lg transition-all"
                    >
                      <Trash2 className="w-4 h-4" />
                    </button>
                  )}

                  {/* Small video/thumbnail header */}
                  <div
                    className="relative h-40 bg-gray-100 cursor-pointer"
                    onClick={() => {
                      const id = group.video_id || group.products[0]?.listing_id;
                      if (id) navigate(`/watch?video=${id}`);
                    }}
                  >
                    {group.video_thumbnail_url ? (
                      <img
                        src={group.video_thumbnail_url}
                        alt="Video thumbnail"
                        className="w-full h-full object-cover"
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center bg-gradient-to-br from-[#6A00FF] to-purple-200">
                        <Package className="w-12 h-12 text-white" />
                      </div>
                    )}
                    <div className="absolute bottom-2 left-2 flex items-center gap-3 bg-black/60 text-white px-2 py-1 rounded-md">
                      <div className="flex items-center gap-1">
                        <Eye className="w-4 h-4" />
                        <span className="text-xs font-poppins">{group.stats.views}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <ThumbsUp className="w-4 h-4" />
                        <span className="text-xs font-poppins">{group.stats.likes}</span>
                      </div>
                      <div className="flex items-center gap-1">
                        <Heart className="w-4 h-4" />
                        <span className="text-xs font-poppins">{group.stats.loves}</span>
                      </div>
                    </div>
                  </div>

                  {/* Bottom: small carousel of linked products */}
                  <div className="p-3">
                    <div className="flex gap-3 overflow-x-auto scrollbar-hide pb-1">
                      {group.products.map((prod, idx) => (
                        <div
                          key={prod.listing_id}
                          className="flex-shrink-0 w-28 cursor-pointer"
                          onClick={(e) => {
                            e.stopPropagation();
                            if (prod.link) {
                              window.open(prod.link, '_blank', 'noopener,noreferrer');
                            } else {
                              toast.error('Product link not available');
                            }
                          }}
                        >
                          <div className="w-full h-20 rounded-lg overflow-hidden border border-gray-200 bg-gray-100">
                            {prod.thumbnail ? (
                              <img
                                src={prod.thumbnail}
                                alt={prod.product_name}
                                className="w-full h-full object-cover"
                              />
                            ) : (
                              <div className="w-full h-full flex items-center justify-center bg-gray-200">
                                <Package className="w-6 h-6 text-gray-500" />
                              </div>
                            )}
                          </div>
                          <div className="mt-1">
                            <p className="text-xs font-semibold text-gray-900 font-poppins line-clamp-1">{prod.product_name}</p>
                            <p className="text-[11px] text-gray-600 font-poppins line-clamp-1">{prod.provider_name}</p>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

    </div>
  );
};

export default Wishlist;

