import React, { useEffect, useMemo, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { ArrowLeft, Check, CheckCircle2, Clock, Crown, Shield, Sparkles, Tag, X } from 'lucide-react';
import { subscriptionAPI } from '../utils/api';
import { toast } from 'react-hot-toast';

// Load Razorpay script dynamically
const loadRazorpayScript = () => {
  return new Promise((resolve) => {
    if (window.Razorpay) {
      resolve(window.Razorpay);
      return;
    }
    const script = document.createElement('script');
    script.src = 'https://checkout.razorpay.com/v1/checkout.js';
    script.onload = () => resolve(window.Razorpay);
    script.onerror = () => {
      console.error('Failed to load Razorpay script');
      resolve(null);
    };
    document.body.appendChild(script);
  });
};

const toNumber = (value, fallback = 0) => {
  if (typeof value === 'number') return value;
  if (typeof value === 'string' && value.trim() !== '') {
    const parsed = Number(value);
    return Number.isNaN(parsed) ? fallback : parsed;
  }
  return fallback;
};

const formatIndianPrice = (value) => {
  const numeric = toNumber(value);
  return new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR',
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  }).format(numeric);
};

const parseFeatures = (plan) => {
  if (!plan?.features) return {};
  try {
    if (typeof plan.features === 'string') {
      return JSON.parse(plan.features);
    }
    return plan.features;
  } catch (err) {
    console.warn('Failed to parse plan features', err);
    return {};
  }
};

const buildBenefitList = (plan) => {
  const features = parseFeatures(plan);
  const benefits = [];

  if (features.videos_per_month) {
    benefits.push(`${features.videos_per_month}+ AI-powered video listings each month`);
  }
  if (features.storage_gb) {
    benefits.push(`${features.storage_gb}GB hosted storage for your media`);
  }
  if (features.support) {
    benefits.push(`${String(features.support).charAt(0).toUpperCase()}${String(features.support).slice(1)} support channel`);
  }
  if (features.team_members) {
    benefits.push(`Seat access for ${features.team_members} team member${features.team_members > 1 ? 's' : ''}`);
  }

  return benefits;
};

const getDurationCopy = (plan) => {
  if (!plan?.duration_days) return 'Flexible duration';
  if (plan.duration_days === 365 || plan.plan_type === 'YEARLY') {
    return 'Yearly';
  }
  if (plan.duration_days % 30 === 0 || plan.plan_type === 'MONTHLY') {
    const months = plan.duration_days / 30;
    return months <= 1 ? 'Monthly' : `${months} months`;
  }
  return `${plan.duration_days} days`;
};

const SubscriptionUpgrade = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const [plans, setPlans] = useState([]);
  const [loadingPlans, setLoadingPlans] = useState(true);
  const [subscription, setSubscription] = useState(null);
  const [selectedPlan, setSelectedPlan] = useState(null);
  const [showDetailModal, setShowDetailModal] = useState(false);
  const [couponCode, setCouponCode] = useState('');
  const [couponLoading, setCouponLoading] = useState(false);
  const [couponState, setCouponState] = useState({
    applied: false,
    discountAmount: 0,
    finalPrice: null,
    message: '',
    coupon: null,
    planId: null
  });
  const [processingUpgrade, setProcessingUpgrade] = useState(false);

  useEffect(() => {
    loadPlans();
    loadSubscription();
  }, []);

  const loadPlans = async () => {
    try {
      setLoadingPlans(true);
      const response = await subscriptionAPI.getPlans();
      if (response.ok) {
        setPlans(response.data?.plans || []);
      } else {
        setPlans([]);
      }
    } catch (error) {
      console.error('Failed to fetch subscription plans:', error);
      toast.error('Unable to load subscription plans right now');
    } finally {
      setLoadingPlans(false);
    }
  };

  const loadSubscription = async () => {
    try {
      const data = await subscriptionAPI.getStatus();
      setSubscription(data?.subscription || data || null);
    } catch (error) {
      console.error('Failed to fetch subscription status:', error);
    }
  };

  const currentPlanLabel = useMemo(() => {
    if (!subscription?.is_subscribed) return 'Free Creator Plan';
    const name = subscription?.plan_name || 'Premium Plan';
    return name;
  }, [subscription]);

  const getPlanPrice = (plan) => {
    if (!plan) return 0;
    const basePrice = toNumber(plan.price);
    if (!couponState.applied || couponState.planId !== plan.plan_id) {
      return basePrice;
    }
    return toNumber(couponState.finalPrice ?? basePrice);
  };

  const resetCouponState = () => {
    setCouponCode('');
    setCouponState({
      applied: false,
      discountAmount: 0,
      finalPrice: null,
      message: '',
      coupon: null,
      planId: null
    });
  };

  const handlePlanClick = (plan) => {
    setSelectedPlan(plan);
    resetCouponState();
    setShowDetailModal(true);
  };

  const expectedActivationEnd = useMemo(() => {
    if (!selectedPlan?.duration_days) return null;
    const days = selectedPlan.duration_days;
    const startDate = new Date();
    startDate.setDate(startDate.getDate() + days);
    return startDate.toLocaleDateString('en-US', { day: 'numeric', month: 'long', year: 'numeric' });
  }, [selectedPlan]);

  const selectedPlanBenefits = useMemo(
    () => (selectedPlan ? buildBenefitList(selectedPlan) : []),
    [selectedPlan]
  );

  const handleCouponApply = async () => {
    if (!selectedPlan) {
      toast.error('Select a plan to apply coupon');
      return;
    }

    const normalized = couponCode.trim().toUpperCase();
    if (!normalized) {
      setCouponState((prev) => ({ ...prev, applied: false, message: 'Enter a coupon code to apply discounts' }));
      return;
    }

    setCouponLoading(true);
    try {
      const response = await subscriptionAPI.validateCoupon({
        coupon_code: normalized,
        plan_id: selectedPlan.plan_id,
        plan_price: toNumber(selectedPlan.price)
      });

      setCouponCode(normalized);
      setCouponState({
        applied: true,
        discountAmount: response.discount_amount,
        finalPrice: response.final_price,
        message: response.message || `${normalized} applied`,
        coupon: response.coupon,
        planId: selectedPlan.plan_id
      });

      toast.success(response.message || 'Coupon applied successfully');
    } catch (error) {
      const message = error.response?.data?.message || 'Coupon code is not valid';
      setCouponState({
        applied: false,
        discountAmount: 0,
        finalPrice: null,
        message,
        coupon: null,
        planId: null
      });
      toast.error(message);
    } finally {
      setCouponLoading(false);
    }
  };

  const handleUpgrade = async () => {
    if (!selectedPlan) return;
    
    try {
      setProcessingUpgrade(true);

      // Load Razorpay script
      const Razorpay = await loadRazorpayScript();
      if (!Razorpay) {
        toast.error('Payment gateway failed to load. Please refresh and try again.');
        setProcessingUpgrade(false);
        return;
      }

      // Prepare order data
      const orderData = {
        plan_id: selectedPlan.plan_id,
        coupon_code: couponState.applied && couponState.planId === selectedPlan.plan_id 
          ? couponCode 
          : null
      };

      // Create Razorpay order
      const orderResponse = await subscriptionAPI.createPaymentOrder(orderData);
      
      if (!orderResponse.ok) {
        throw new Error(orderResponse.message || 'Failed to create payment order');
      }

      const { order_id, amount, currency, key_id, transaction_id, plan } = orderResponse.data;

      // Validate key_id is not a placeholder
      if (!key_id || key_id === 'your_razorpay_key_id' || key_id.includes('your_razorpay')) {
        throw new Error('Payment gateway is not properly configured. Please contact support.');
      }

      // Get user details for Razorpay checkout
      const userEmail = localStorage.getItem('userEmail') || '';
      const userName = localStorage.getItem('userName') || 'User';

      // Configure Razorpay options
      const options = {
        key: key_id,
        amount: amount,
        currency: currency,
        name: 'GetNayi',
        description: `Subscription: ${plan.plan_name}`,
        order_id: order_id,
        handler: async function (response) {
          try {
            setProcessingUpgrade(true);
            
            // Verify payment
            const verifyResponse = await subscriptionAPI.verifyPayment({
              razorpay_order_id: response.razorpay_order_id,
              razorpay_payment_id: response.razorpay_payment_id,
              razorpay_signature: response.razorpay_signature,
              transaction_id: transaction_id
            });

            if (verifyResponse.ok) {
              toast.success('Payment successful! Subscription activated.');
              setShowDetailModal(false);
              loadSubscription();
              navigate('/settings', { replace: true });
            } else {
              throw new Error(verifyResponse.message || 'Payment verification failed');
            }
          } catch (error) {
            console.error('Payment verification error:', error);
            toast.error(error.response?.data?.message || error.message || 'Payment verification failed. Please contact support.');
          } finally {
            setProcessingUpgrade(false);
          }
        },
        prefill: {
          name: userName,
          email: userEmail,
        },
        theme: {
          color: '#6C4DFF'
        },
        modal: {
          ondismiss: function() {
            setProcessingUpgrade(false);
            toast.info('Payment cancelled');
          }
        }
      };

      // Open Razorpay checkout
      const razorpayInstance = new Razorpay(options);
      razorpayInstance.on('payment.failed', function (response) {
        console.error('Payment failed:', response);
        toast.error(response.error.description || 'Payment failed. Please try again.');
        setProcessingUpgrade(false);
      });
      
      razorpayInstance.open();

    } catch (error) {
      console.error('Subscription error:', error);
      const errorMessage = error.response?.data?.message || error.message || 'Failed to process payment';
      toast.error(errorMessage);
      setProcessingUpgrade(false);
    }
  };

  return (
    <div className="min-h-screen bg-[#F8F7FD] text-gray-900">
      <header className="sticky top-0 z-20 bg-white border-b border-gray-100">
        <div className="max-w-xl mx-auto flex items-center gap-3 px-4 py-3">
          <button
            onClick={() => navigate(-1)}
            className="p-2 rounded-full border border-gray-200 text-gray-700 hover:bg-gray-50 transition"
            aria-label="Go back"
          >
            <ArrowLeft className="w-5 h-5" />
          </button>
          <div>
            <p className="text-[11px] uppercase tracking-[0.3em] text-gray-400" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
              Subscription
            </p>
            <h1 className="text-lg font-semibold text-gray-900" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
              Upgrade plan
            </h1>
          </div>
          <div className="ml-auto flex items-center gap-2 text-xs text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
            <Shield className="w-4 h-4 text-[#6C4DFF]" />
            Secure
          </div>
        </div>
      </header>

      <main className="max-w-xl mx-auto px-4 py-5 pb-28 space-y-6">
        <section className="rounded-2xl bg-gradient-to-r from-[#F7ECFF] to-[#E4E4FF] border border-white shadow-sm p-4">
          <div className="flex items-start gap-3">
            <div className="p-2 rounded-xl bg-white shadow-sm">
              <Sparkles className="w-5 h-5 text-[#6C4DFF]" />
            </div>
            <div className="flex-1 space-y-1">
              <p className="text-xs uppercase tracking-wide text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                Current plan
              </p>
              <h2 className="text-xl font-semibold text-gray-900" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
                {currentPlanLabel}
              </h2>
              <p className="text-sm text-gray-600 leading-5" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                Unlock more uploads, insights, and placement boosts by choosing a plan that fits your cadence.
              </p>
              <div className="flex items-center gap-2 text-sm text-emerald-600 font-medium pt-1" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                <CheckCircle2 className="w-4 h-4" />
                {subscription?.is_subscribed ? 'Premium active' : 'Free plan active'}
              </div>
              {subscription?.expires_at && (
                <p className="text-xs text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                  Renews on{' '}
                  <span className="font-medium text-gray-700">
                    {new Date(subscription.expires_at).toLocaleDateString('en-US', { day: 'numeric', month: 'short', year: 'numeric' })}
                  </span>
                </p>
              )}
            </div>
          </div>
        </section>

        <section className="space-y-3">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-[11px] uppercase tracking-wide text-gray-400" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                Plans for every creator
              </p>
              <h3 className="text-lg font-semibold text-gray-900" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
                Pick your growth pace
              </h3>
            </div>
            {location.state?.from && (
              <span className="text-xs text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                From {location.state.from.replace('-', ' ')}
              </span>
            )}
          </div>

          {loadingPlans ? (
            <div className="space-y-3">
              {[1, 2, 3].map((skeleton) => (
                <div key={skeleton} className="h-28 rounded-2xl bg-white shadow-sm animate-pulse border border-gray-100" />
              ))}
            </div>
          ) : (
            <div className="space-y-3">
              {plans.map((plan) => {
                const benefitList = buildBenefitList(plan);
                const benefits = benefitList.slice(0, 2);
                return (
                  <button
                    key={plan.plan_id}
                    onClick={() => handlePlanClick(plan)}
                    className="w-full text-left rounded-2xl bg-white border border-gray-100 shadow-sm px-4 py-4 active:scale-[0.99] transition"
                  >
                  <div className="flex items-start justify-between gap-3">
                    <div className="flex-1">
                      <p className="text-[11px] uppercase tracking-wide text-gray-400" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                        {plan.plan_type?.toLowerCase() === 'influencer' ? 'Influencer' : getDurationCopy(plan)}
                      </p>
                      <h4 className="text-lg font-semibold text-gray-900" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
                        {plan.plan_name}
                      </h4>
                      {plan.plan_description && (
                        <p className="text-xs text-gray-500 mt-1 leading-4" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                          {plan.plan_description}
                        </p>
                      )}
                    </div>
                    <Crown className="w-5 h-5 text-[#FFC361]" />
                  </div>
                    <p className="text-2xl font-semibold text-gray-900 mt-3" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
                      {formatIndianPrice(plan.price)}
                      <span className="text-sm font-normal text-gray-500 ml-1" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                        / {getDurationCopy(plan).toLowerCase()}
                      </span>
                    </p>
                    {benefits.length > 0 && (
                      <ul className="mt-3 space-y-1.5">
                        {benefits.map((benefit) => (
                          <li key={benefit} className="flex items-center gap-2 text-sm text-gray-600" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                            <Check className="w-4 h-4 text-emerald-500" />
                            {benefit}
                          </li>
                        ))}
                      </ul>
                    )}
                    <div className="flex items-center justify-between mt-3 text-xs text-[#6C4DFF]" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                      <span>Tap to view details</span>
                      <Tag className="w-4 h-4" />
                    </div>
                  </button>
                );
              })}

              {plans.length === 0 && (
                <div className="rounded-2xl bg-white border border-dashed border-gray-200 p-6 text-center text-sm text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                  No active plans available right now.
                </div>
              )}
            </div>
          )}
        </section>
      </main>

      {showDetailModal && selectedPlan && (
        <>
          <div
            className="fixed inset-0 bg-black/60 z-40"
            onClick={() => setShowDetailModal(false)}
          />
          <div className="fixed inset-0 z-50 flex items-end md:items-center justify-center px-4 pt-10 pb-8">
            <div className="w-full max-w-md rounded-3xl bg-white shadow-2xl border border-gray-100 px-5 pt-4 pb-10 space-y-4 max-h-[85vh] overflow-y-auto scrollbar-hide translate-y-[-16px] safe-bottom">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-[11px] uppercase tracking-wide text-gray-400" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                    Plan detail
                  </p>
                  <h4 className="text-xl font-semibold text-gray-900" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
                    {selectedPlan.plan_name}
                  </h4>
                  {selectedPlan.plan_description && (
                    <p className="text-xs text-gray-500 mt-1 leading-4" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                      {selectedPlan.plan_description}
                    </p>
                  )}
                  <div className="flex flex-wrap gap-2 text-[11px] uppercase tracking-wide text-gray-400 mt-2" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                    <span className="px-2 py-1 rounded-full bg-gray-100 text-gray-600">
                      {selectedPlan.plan_type || 'Custom'}
                    </span>
                    <span className="px-2 py-1 rounded-full bg-gray-100 text-gray-600">
                      {selectedPlan.duration_days} days
                    </span>
                  </div>
                </div>
                <button
                  onClick={() => setShowDetailModal(false)}
                  className="p-2 rounded-full border border-gray-200 text-gray-600"
                  aria-label="Close"
                >
                  <X className="w-4 h-4" />
                </button>
              </div>

              <div className="space-y-2 border border-gray-100 rounded-2xl p-4">
                <p className="text-xs uppercase text-gray-400" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                  Price
                </p>
                <p className="text-2xl font-semibold text-gray-900" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
                  {formatIndianPrice(getPlanPrice(selectedPlan))}
                </p>
                <p className="text-xs text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                  Billed {selectedPlan.plan_type?.toLowerCase() === 'yearly' ? 'yearly' : `every ${getDurationCopy(selectedPlan).toLowerCase()}`}
                </p>
                {couponState.applied && couponState.planId === selectedPlan.plan_id && (
                  <div className="space-y-1">
                    <p className="text-xs text-emerald-600" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                      {couponState.message || `You save ${formatIndianPrice(couponState.discountAmount || 0)}`}
                    </p>
                    <p className="text-xs text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                      Saved {formatIndianPrice(couponState.discountAmount || 0)} on this plan
                    </p>
                  </div>
                )}
              </div>

              <div className="space-y-2 border border-gray-100 rounded-2xl p-4">
                <div className="flex items-center gap-3">
                  <Clock className="w-4 h-4 text-gray-500" />
                  <div>
                    <p className="text-xs uppercase text-gray-400" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                      Deactivation date
                    </p>
                    <p className="text-sm font-medium text-gray-800" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                      {expectedActivationEnd || 'Calculated automatically'}
                    </p>
                  </div>
                </div>
                <p className="text-xs text-gray-500" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                  Renewals are handled instantly with support ready to help within 7 days.
                </p>
              </div>

              {selectedPlanBenefits.length > 0 && (
                <div className="border border-gray-100 rounded-2xl p-4 space-y-3 bg-gray-50/60">
                  <p className="text-xs uppercase tracking-wide text-gray-400" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                    Benefits
                  </p>
                  <div className="grid gap-2">
                    {selectedPlanBenefits.map((benefit) => (
                      <div key={benefit} className="flex items-center gap-2 text-sm text-gray-700" style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                        <Check className="w-4 h-4 text-emerald-500" />
                        <span className="flex-1 leading-tight">{benefit}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              <div className="space-y-3 border border-gray-100 rounded-2xl p-4">
                <div className="flex items-center justify-between">
                  <p className="text-sm font-medium text-gray-900" style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}>
                    Apply coupon
                  </p>
                  {couponState.applied && (
                    <button
                      onClick={resetCouponState}
                      className="text-xs text-[#6C4DFF]"
                      type="button"
                      style={{ fontFamily: 'Inter, Poppins, sans-serif' }}
                    >
                      Remove
                    </button>
                  )}
                </div>
                <div className="flex gap-2">
                  <input
                    type="text"
                    value={couponCode}
                    onChange={(event) => setCouponCode(event.target.value)}
                    placeholder="Creator code"
                    className="flex-1 rounded-xl border border-gray-200 px-4 py-2.5 text-sm focus:outline-none focus:ring-2 focus:ring-[#6C4DFF]"
                    style={{ fontFamily: 'Inter, Poppins, sans-serif' }}
                  />
                  <button
                    type="button"
                    onClick={handleCouponApply}
                    disabled={couponLoading}
                    className="px-4 py-2.5 rounded-xl bg-[#6C4DFF] text-white text-sm font-semibold disabled:opacity-50 disabled:cursor-not-allowed"
                    style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}
                  >
                    {couponLoading ? 'Checking' : 'Apply'}
                  </button>
                </div>
                {couponState.message && (
                  <p className={`text-xs ${couponState.applied ? 'text-emerald-600' : 'text-red-500'}`} style={{ fontFamily: 'Inter, Poppins, sans-serif' }}>
                    {couponState.message}
                  </p>
                )}
              </div>

              <div className="pt-2 pb-3">
                <button
                  onClick={handleUpgrade}
                  disabled={processingUpgrade}
                  className="w-full rounded-2xl bg-[#6C4DFF] text-white py-3 text-base font-semibold disabled:opacity-50"
                  style={{ fontFamily: 'Gilroy, Poppins, sans-serif' }}
                >
                  {processingUpgrade ? 'Processing' : 'Upgrade now'}
                </button>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default SubscriptionUpgrade;

