// BottomNav.jsx - Fixed bottom navigation bar
import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const BottomNav = ({ activeTab }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const { user } = useAuth();

  const baseNavItems = [
    { id: 'home', label: 'Home', path: '/home', icon: 'home' },
    { id: 'watch', label: 'Watch', path: '/watch', icon: 'play' },
    { id: 'add', label: 'List', path: '/listing', icon: 'plus' },
    { id: 'wishlist', label: 'Wishlist', path: '/wishlist', icon: 'heart' },
    { id: 'profile', label: 'Profile', path: '/profile', icon: 'user' },
  ];

  // Add admin button if user is admin
  const navItems = user?.is_admin 
    ? [...baseNavItems, { id: 'admin', label: 'Admin', path: '/admin/dashboard', icon: 'admin' }]
    : baseNavItems;

  const getIcon = (iconName, isActive) => {
    const iconColor = isActive ? '#FFFFFF' : '#6B7280';
    const className = 'w-5 h-5';
    
    switch (iconName) {
      case 'home':
        return (
          <svg className={className} fill={isActive ? iconColor : 'none'} stroke={iconColor} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
          </svg>
        );
      case 'play':
        return (
          <svg className={className} fill={isActive ? iconColor : 'none'} stroke={iconColor} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z" />
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
        );
      case 'plus':
        return (
          <svg className={className} fill="none" stroke={iconColor} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M12 4v16m8-8H4" />
          </svg>
        );
      case 'heart':
        return (
          <svg className={className} fill={isActive ? iconColor : 'none'} stroke={iconColor} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
          </svg>
        );
      case 'user':
        return (
          <svg className={className} fill={isActive ? iconColor : 'none'} stroke={iconColor} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
          </svg>
        );
      case 'admin':
        return (
          <svg className={className} fill={isActive ? iconColor : 'none'} stroke={iconColor} viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
          </svg>
        );
      default:
        return null;
    }
  };

  const handleNavClick = (item) => {
    if (item.path) {
      navigate(item.path);
    }
  };

  const isActive = (item) => {
    // Use activeTab prop if provided, otherwise use location-based detection
    if (activeTab) {
      return item.id === activeTab;
    }
    
    if (item.path === '/home') {
      return location.pathname === '/home';
    }
    
    // For List tab, also check provider registration path
    if (item.id === 'add') {
      return location.pathname.startsWith('/listing') || location.pathname.startsWith('/provider/register');
    }
    
    return item.path && location.pathname.startsWith(item.path);
  };

  return (
    <nav 
      className="fixed bottom-0 left-0 right-0 z-[9999] safe-bottom"
      style={{
        paddingBottom: 'env(safe-area-inset-bottom, 0)'
      }}
    >
      {/* Floating Elevated Container */}
      <div 
        className="bg-white mx-auto relative"
        style={{
          maxWidth: 'calc(100% - 16px)',
          width: '100%',
          margin: '0 8px 8px',
          borderRadius: '20px',
          boxShadow: '0 12px 30px rgba(16, 24, 40, 0.12)',
          backdropFilter: 'blur(10px)',
          WebkitBackdropFilter: 'blur(10px)'
        }}
      >
        <div className="flex justify-around items-center py-2 sm:py-3 px-2 sm:px-4">
          {navItems.map((item) => (
            <button
              key={item.id}
              onClick={() => handleNavClick(item)}
              className="flex flex-col items-center justify-center flex-1 py-0.5 sm:py-1 relative"
              style={{
                minWidth: '40px',
                minHeight: '40px'
              }}
              aria-label={item.label}
              disabled={!item.path}
            >
              <div 
                className="p-1.5 sm:p-2 rounded-full transition-all duration-200"
                style={{
                  ...(isActive(item) 
                    ? {
                        background: '#4B2BD6',
                        width: '36px',
                        height: '36px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                      }
                    : {
                        background: 'transparent'
                      })
                }}
              >
                {getIcon(item.icon, isActive(item))}
              </div>
              <span 
                className="text-[10px] sm:text-xs font-medium mt-0.5 sm:mt-1 font-poppins"
                style={{
                  ...(isActive(item) 
                    ? { color: '#4B2BD6' }
                    : { color: '#6B7280' })
                }}
              >
                {item.label}
              </span>
              {item.badge && (
                <span 
                  className="absolute top-0 right-2 sm:right-4 bg-[#4B2BD6] text-white text-[9px] sm:text-[10px] px-1 sm:px-1.5 py-0.5 rounded-full font-medium"
                >
                  {item.badge}
                </span>
              )}
            </button>
          ))}
        </div>
      </div>
    </nav>
  );
};

export default BottomNav;

