import React, { useEffect, useState, useMemo } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { ArrowLeft, AlertCircle, ChevronDown } from 'lucide-react';
import { cmsAPI } from '../utils/api';

const StaticPage = () => {
  const { slug } = useParams();
  const navigate = useNavigate();
  const [page, setPage] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [banners, setBanners] = useState([]);
  const [faqs, setFaqs] = useState([]);
  const [expandedFaqId, setExpandedFaqId] = useState(null);

  useEffect(() => {
    const fetchPage = async () => {
      if (!slug) {
        setError('Missing page identifier');
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        const response = await cmsAPI.getPageBySlug(slug);
        if (response?.ok) {
          setPage(response.page);
          setFaqs(response.page?.faqs || []);
          setBanners(response.page?.banners || []);
          setError('');
        } else {
          setError(response?.message || 'Page not found');
        }
      } catch (err) {
        console.error('Static page load error:', err);
        setError(err?.response?.data?.message || 'Page not found');
      } finally {
        setLoading(false);
      }
    };

    fetchPage();
  }, [slug]);

  useEffect(() => {
    if (page?.title) {
      document.title = `${page.title} - GetNayi`;
      return () => {
        document.title = 'GetNayi';
      };
    }
  }, [page]);

  const hasBanners = useMemo(() => Array.isArray(banners) && banners.length > 0, [banners]);
  const hasFaqs = useMemo(() => Array.isArray(faqs) && faqs.length > 0, [faqs]);
  const renderBannerSection = () => {
    if (loading && !hasBanners) {
      return (
        <div className="space-y-4 mb-6">
          <div className="h-44 bg-gray-200 animate-pulse rounded-3xl" />
        </div>
      );
    }

    if (!hasBanners) return null;

    return (
      <div className="space-y-4 mb-6">
        {banners.map((banner) => (
          <div
            key={banner.id}
            className="relative overflow-hidden rounded-3xl border border-gray-200 shadow-sm bg-gradient-to-r from-indigo-50 via-white to-white"
          >
            <div className="flex flex-col lg:flex-row items-start lg:items-center">
              <div className="p-6 lg:p-8 flex-1">
                <p className="text-xs font-semibold uppercase tracking-widest text-indigo-500">
                  {banner.banner_type || 'Banner'}
                </p>
                <h2 className="text-2xl sm:text-3xl font-bold text-gray-900 mt-2">{banner.title}</h2>
                {banner.description && (
                  <p className="text-sm text-gray-600 mt-3 max-w-2xl">{banner.description}</p>
                )}
                {banner.button_text && banner.link_url && (
                  <a
                    href={banner.link_url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="inline-flex items-center px-4 py-2 mt-4 rounded-full bg-gray-900 text-white text-sm font-semibold shadow hover:bg-gray-800"
                  >
                    {banner.button_text}
                  </a>
                )}
              </div>
              {banner.image_url && (
                <div className="w-full lg:w-80 h-48 lg:h-full relative">
                  <img
                    src={banner.image_url}
                    alt={banner.title}
                    className="w-full h-full object-cover lg:rounded-l-none rounded-b-3xl lg:rounded-r-3xl"
                    loading="lazy"
                  />
                </div>
              )}
            </div>
          </div>
        ))}
      </div>
    );
  };


  const toggleFaq = (faqId) => {
    setExpandedFaqId((prev) => (prev === faqId ? null : faqId));
  };

  const formattedUpdatedAt = useMemo(() => {
    if (!page?.updated_at) return null;
    try {
      return new Date(page.updated_at).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
      });
    } catch (err) {
      return null;
    }
  }, [page]);

  const renderContent = () => {
    if (loading) {
      return (
        <div className="bg-white border border-gray-200 rounded-2xl p-8 shadow-sm animate-pulse space-y-4">
          <div className="h-6 bg-gray-200 rounded w-1/2" />
          <div className="h-4 bg-gray-200 rounded w-3/4" />
          <div className="h-4 bg-gray-200 rounded w-2/3" />
          <div className="h-4 bg-gray-200 rounded w-full" />
          <div className="h-4 bg-gray-200 rounded w-5/6" />
        </div>
      );
    }

    if (error) {
      return (
        <div className="bg-red-50 border border-red-200 rounded-2xl p-6 flex items-start gap-3 text-red-700">
          <AlertCircle className="w-5 h-5 mt-0.5" />
          <div>
            <p className="font-semibold">Unable to load this page.</p>
            <p className="text-sm text-red-600 mt-1">{error}</p>
          </div>
        </div>
      );
    }

    if (!page) {
      return null;
    }

    return (
      <article className="bg-white border border-gray-200 rounded-2xl p-6 sm:p-10 shadow-sm">
        <header className="mb-8">
          <p className="text-xs font-semibold uppercase tracking-widest text-gray-500">Static Page</p>
          <h1 className="text-3xl sm:text-4xl font-bold text-gray-900 mt-2">{page.title}</h1>
          {formattedUpdatedAt && (
            <p className="text-sm text-gray-500 mt-3">Last updated {formattedUpdatedAt}</p>
          )}
        </header>
        <section
          className="prose prose-lg max-w-none text-gray-800"
          dangerouslySetInnerHTML={{ __html: page.content || '' }}
        />
      </article>
    );
  };

  const renderFaqSection = () => {
    if (!hasFaqs) return null;

    return (
      <section className="mt-10 bg-white border border-gray-200 rounded-2xl p-6 sm:p-8 shadow-sm">
        <div className="mb-6">
          <p className="text-xs font-semibold uppercase tracking-widest text-gray-500">FAQs</p>
          <h2 className="text-2xl font-bold text-gray-900 mt-1">Need more clarity?</h2>
          <p className="text-sm text-gray-500">Answers curated specifically for this page.</p>
        </div>
        <div className="divide-y divide-gray-200">
          {faqs.map((faq) => {
            const isOpen = expandedFaqId === faq.id;
            return (
              <div key={faq.id} className="py-4">
                <button
                  type="button"
                  onClick={() => toggleFaq(faq.id)}
                  className="w-full flex items-center justify-between text-left"
                >
                  <div>
                    <p className="text-base font-semibold text-gray-900">{faq.question}</p>
                    {faq.category && (
                      <span className="inline-flex px-2 py-0.5 mt-1 text-xs font-medium rounded-full bg-indigo-50 text-indigo-700">
                        {faq.category}
                      </span>
                    )}
                  </div>
                  <ChevronDown
                    className={`w-5 h-5 text-gray-500 transition-transform ${
                      isOpen ? 'transform rotate-180' : ''
                    }`}
                  />
                </button>
                {isOpen && (
                  <div className="mt-3 text-gray-700 text-sm leading-relaxed">
                    <div
                      className="prose prose-sm max-w-none"
                      dangerouslySetInnerHTML={{ __html: faq.answer || '' }}
                    />
                  </div>
                )}
              </div>
            );
          })}
        </div>
      </section>
    );
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-4xl mx-auto px-4 py-8">
        <button
          onClick={() => navigate(-1)}
          className="inline-flex items-center gap-2 text-sm font-semibold text-gray-600 hover:text-gray-900 mb-6"
        >
          <ArrowLeft className="w-4 h-4" />
          Back
        </button>
        {renderBannerSection()}
        {renderContent()}
        {renderFaqSection()}
      </div>
    </div>
  );
};

export default StaticPage;

