import React, { useState, useEffect, useCallback } from 'react';
import { adminAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { 
  X, Crown, Calendar, Trash2, Search, RefreshCw, Users, 
  Shield, Store, Sparkles, UserCheck,
  ChevronDown, ChevronUp, Mail, Phone, Filter,
  Ban, AlertCircle, Clock
} from 'lucide-react';

const UserManagement = () => {
  // Data states
  const [users, setUsers] = useState([]);
  const [statistics, setStatistics] = useState(null);
  const [loading, setLoading] = useState(true);
  const [statsLoading, setStatsLoading] = useState(true);
  const [error, setError] = useState('');
  
  // Search and filter states - search only triggers on button click
  const [searchInput, setSearchInput] = useState('');
  const [activeSearch, setActiveSearch] = useState('');
  const [statusFilter, setStatusFilter] = useState('ALL');
  const [sortBy, setSortBy] = useState('created_at');
  const [sortOrder, setSortOrder] = useState('desc');
  
  // Modal states
  const [selectedUser, setSelectedUser] = useState(null);
  const [showUserModal, setShowUserModal] = useState(false);
  const [actionLoading, setActionLoading] = useState(null);
  const [showSubscriptionModal, setShowSubscriptionModal] = useState(false);
  const [subscriptionPlans, setSubscriptionPlans] = useState([]);
  const [loadingPlans, setLoadingPlans] = useState(false);
  const [selectedPlanId, setSelectedPlanId] = useState(null);
  const [assigningSubscription, setAssigningSubscription] = useState(false);
  
  // UI states
  const [showFilters, setShowFilters] = useState(false);
  const [expandedRows, setExpandedRows] = useState(new Set());

  // Fetch users with current filters
  const fetchUsers = useCallback(async () => {
    try {
      setLoading(true);
      const response = await adminAPI.getUsers({
        search: activeSearch,
        status: statusFilter,
        sort_by: sortBy,
        sort_order: sortOrder
      });
      
      if (response.ok) {
        setUsers(response.data.users || []);
        setError('');
      } else {
        setError(response.message || 'Failed to load users');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  }, [activeSearch, statusFilter, sortBy, sortOrder]);

  // Fetch statistics
  const fetchStatistics = useCallback(async () => {
    try {
      setStatsLoading(true);
      const response = await adminAPI.getUserStatistics();
      if (response.ok) {
        setStatistics(response.data);
      }
    } catch (err) {
      console.error('Failed to load statistics:', err);
    } finally {
      setStatsLoading(false);
    }
  }, []);

  // Initial load
  useEffect(() => {
    fetchUsers();
    fetchStatistics();
  }, [fetchUsers, fetchStatistics]);

  // Handle search button click or Enter key
  const handleSearch = () => {
    setActiveSearch(searchInput);
  };

  const handleSearchKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  };

  // Clear search
  const clearSearch = () => {
    setSearchInput('');
    setActiveSearch('');
  };

  // Handle filter changes
  const handleFilterChange = (filterType, value) => {
    if (filterType === 'status') {
      setStatusFilter(value);
    }
  };

  const handleUserAction = async (userId, action) => {
    try {
      setActionLoading(userId);
      const response = await adminAPI.updateUserStatus(userId, action);
      
      if (response.ok) {
        await fetchUsers();
        await fetchStatistics();
        if (selectedUser && selectedUser.id === userId) {
          setSelectedUser({ ...selectedUser, status: action });
        }
        toast.success(`User ${action.toLowerCase()} successfully`);
        setError('');
      } else {
        if (response.status === 403) {
          toast.error('Cannot modify admin user status');
        } else {
          toast.error(response.message || 'Failed to update user status');
        }
      }
    } catch (err) {
      if (err.response && err.response.status === 403) {
        toast.error('Cannot modify admin user status');
      } else {
        toast.error('Network error. Please try again.');
      }
    } finally {
      setActionLoading(null);
    }
  };

  const handleDeleteUser = async (userId) => {
    if (!window.confirm('Are you sure you want to delete this user? This action cannot be undone.')) {
      return;
    }

    try {
      setActionLoading(userId);
      const response = await adminAPI.deleteUser(userId);
      
      if (response.ok) {
        toast.success('User deleted successfully');
        await fetchUsers();
        await fetchStatistics();
        setShowUserModal(false);
        setSelectedUser(null);
      } else {
        const errorMessage = response.message || 'Failed to delete user';
        if (response.status === 403 || errorMessage.includes('admin')) {
          toast.error('Cannot delete admin users');
        } else {
          toast.error(errorMessage);
        }
      }
    } catch (err) {
      if (err.response && err.response.status === 403) {
        toast.error('Cannot delete admin users');
      } else {
        toast.error('Network error. Please try again.');
      }
    } finally {
      setActionLoading(null);
    }
  };

  const openUserModal = async (user) => {
    try {
      const response = await adminAPI.getUserDetails(user.id);
      if (response.ok) {
        setSelectedUser(response.data);
        setShowUserModal(true);
      } else {
        toast.error(response.message || 'Failed to load user details');
      }
    } catch (err) {
      toast.error('Network error. Please try again.');
    }
  };

  const loadSubscriptionPlans = async () => {
    try {
      setLoadingPlans(true);
      const response = await adminAPI.getSubscriptionPlans();
      if (response.ok) {
        setSubscriptionPlans(response.data?.plans || []);
      } else {
        toast.error('Failed to load subscription plans');
      }
    } catch (err) {
      toast.error('Failed to load subscription plans');
    } finally {
      setLoadingPlans(false);
    }
  };

  const openSubscriptionModal = async () => {
    setSelectedPlanId(null);
    await loadSubscriptionPlans();
    setShowSubscriptionModal(true);
  };

  const handleAssignSubscription = async () => {
    if (!selectedPlanId) {
      toast.error('Please select a subscription plan');
      return;
    }

    if (!selectedUser?.email) {
      toast.error('User email not found');
      return;
    }

    try {
      setAssigningSubscription(true);
      const response = await adminAPI.assignSubscription(selectedUser.email, selectedPlanId);
      
      if (response.ok) {
        toast.success('Subscription assigned successfully');
        setShowSubscriptionModal(false);
        const userDetailsResponse = await adminAPI.getUserDetails(selectedUser.id);
        if (userDetailsResponse.ok) {
          setSelectedUser(userDetailsResponse.data);
        }
        await fetchStatistics();
      } else {
        toast.error(response.message || 'Failed to assign subscription');
      }
    } catch (err) {
      toast.error(err.response?.data?.message || 'Failed to assign subscription');
    } finally {
      setAssigningSubscription(false);
    }
  };

  const handleRemoveSubscription = async () => {
    if (!selectedUser?.email) {
      toast.error('User email not found');
      return;
    }

    if (!selectedUser?.is_subscribed) {
      toast.error('User does not have an active subscription');
      return;
    }

    if (!window.confirm(`Remove subscription from ${selectedUser.email}?`)) {
      return;
    }

    try {
      setAssigningSubscription(true);
      const response = await adminAPI.removeSubscription(selectedUser.email);
      
      if (response.ok) {
        toast.success('Subscription removed successfully');
        const userDetailsResponse = await adminAPI.getUserDetails(selectedUser.id);
        if (userDetailsResponse.ok) {
          setSelectedUser(userDetailsResponse.data);
        }
        await fetchStatistics();
      } else {
        toast.error(response.message || 'Failed to remove subscription');
      }
    } catch (err) {
      toast.error(err.response?.data?.message || 'Failed to remove subscription');
    } finally {
      setAssigningSubscription(false);
    }
  };

  // Badge color helpers - matching Dashboard style
  const getRoleBadge = (userType, isAdmin) => {
    if (isAdmin) {
      return { bg: 'bg-red-100', text: 'text-red-700', label: 'Admin' };
    }
    switch (userType) {
      case 'INFLUENCER':
        return { bg: 'bg-purple-100', text: 'text-purple-700', label: 'Influencer' };
      case 'BRAND':
        return { bg: 'bg-blue-100', text: 'text-blue-700', label: 'Brand' };
      default:
        return { bg: 'bg-gray-100', text: 'text-gray-700', label: 'User' };
    }
  };

  const getStatusBadge = (status) => {
    switch (status) {
      case 'ACTIVE':
        return { bg: 'bg-emerald-100', text: 'text-emerald-700', dot: 'bg-emerald-500' };
      case 'SUSPENDED':
        return { bg: 'bg-amber-100', text: 'text-amber-700', dot: 'bg-amber-500' };
      case 'DELETED':
        return { bg: 'bg-red-100', text: 'text-red-700', dot: 'bg-red-500' };
      default:
        return { bg: 'bg-gray-100', text: 'text-gray-700', dot: 'bg-gray-500' };
    }
  };

  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    const date = new Date(dateString);
    return date.toLocaleDateString('en-IN', { 
      day: '2-digit', 
      month: 'short', 
      year: 'numeric' 
    });
  };

  const formatDateTime = (dateString) => {
    if (!dateString) return 'N/A';
    const date = new Date(dateString);
    return date.toLocaleString('en-IN', { 
      day: '2-digit', 
      month: 'short', 
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  // Toggle row expansion (for mobile)
  const toggleRowExpand = (userId) => {
    const newExpanded = new Set(expandedRows);
    if (newExpanded.has(userId)) {
      newExpanded.delete(userId);
    } else {
      newExpanded.add(userId);
    }
    setExpandedRows(newExpanded);
  };

  // Hero Stat Card - matching Dashboard style
  const HeroStat = ({ icon: Icon, label, value, subValue, color = "purple" }) => {
    const colorClasses = {
      purple: "from-purple-600 to-purple-800 shadow-purple-500/30",
      emerald: "from-emerald-600 to-emerald-800 shadow-emerald-500/30",
      amber: "from-amber-500 to-amber-700 shadow-amber-500/30",
      rose: "from-rose-500 to-rose-700 shadow-rose-500/30",
      blue: "from-blue-600 to-blue-800 shadow-blue-500/30",
      indigo: "from-indigo-600 to-indigo-800 shadow-indigo-500/30"
    };
    
    return (
      <div className={`relative overflow-hidden bg-gradient-to-br ${colorClasses[color]} rounded-xl p-4 text-white shadow-lg`}>
        <div className="absolute top-0 right-0 w-20 h-20 bg-white/10 rounded-full -mr-8 -mt-8" />
        <div className="relative z-10">
          <div className="flex items-center gap-2 mb-1">
            <Icon className="w-4 h-4 opacity-80" />
            <span className="text-xs font-medium opacity-90 uppercase tracking-wide">{label}</span>
          </div>
          {statsLoading ? (
            <div className="w-8 h-8 border-2 border-white/30 border-t-white rounded-full animate-spin" />
          ) : (
            <div className="text-2xl sm:text-3xl font-bold font-poppins">{value?.toLocaleString() || 0}</div>
          )}
          {subValue && <div className="text-xs opacity-75 mt-0.5">{subValue}</div>}
        </div>
      </div>
    );
  };

  // Compact Metric - matching Dashboard style
  const CompactMetric = ({ label, value, color = "gray" }) => {
    const colors = {
      gray: "text-gray-600 bg-gray-100",
      purple: "text-purple-700 bg-purple-100",
      emerald: "text-emerald-700 bg-emerald-100",
      amber: "text-amber-700 bg-amber-100",
      rose: "text-rose-700 bg-rose-100",
      blue: "text-blue-700 bg-blue-100"
    };
    
    return (
      <div className={`flex items-center gap-2 px-3 py-2 rounded-lg ${colors[color]}`}>
        <span className="text-xs font-medium">{label}</span>
        <span className="text-sm font-bold ml-auto">{value}</span>
      </div>
    );
  };

  // Loading State - matching Dashboard style
  if (loading && users.length === 0) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
        <div className="text-center">
          <div className="w-12 h-12 border-3 border-gray-200 border-t-purple-600 rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-sm font-medium text-gray-600 font-poppins">Loading Users...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Compact Header - matching Dashboard style */}
      <div className="bg-white border-b border-gray-200 sticky top-0 z-10">
        <div className="px-4 py-3">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-lg font-bold text-gray-900 font-poppins">User Management</h1>
              <p className="text-[11px] text-gray-500">{users.length} users  Real-time data from database</p>
            </div>
            <button 
              onClick={() => { fetchUsers(); fetchStatistics(); }}
              disabled={loading}
              className="flex items-center gap-1.5 bg-purple-600 text-white px-3 py-2 rounded-lg hover:bg-purple-700 transition-all text-xs font-semibold shadow-md disabled:opacity-50"
            >
              <RefreshCw className={`w-3.5 h-3.5 ${loading ? 'animate-spin' : ''}`} />
              <span className="hidden sm:inline">Refresh</span>
            </button>
          </div>
        </div>
      </div>

      <div className="p-4 space-y-4 max-w-6xl mx-auto">
        {/* Hero Stats Grid - matching Dashboard style */}
        <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-5 gap-3">
          <HeroStat 
            icon={Users} 
            label="Total Users" 
            value={statistics?.total_users}
            subValue={`+${statistics?.new_users_week || 0} this week`}
            color="purple"
          />
          <HeroStat 
            icon={Shield} 
            label="Admins" 
            value={statistics?.admin_count}
            color="rose"
          />
          <HeroStat 
            icon={Store} 
            label="Brands" 
            value={statistics?.brand_count}
            color="blue"
          />
          <HeroStat 
            icon={Sparkles} 
            label="Influencers" 
            value={statistics?.influencer_count}
            color="indigo"
          />
          <HeroStat 
            icon={Crown} 
            label="Subscribed" 
            value={statistics?.subscribed_count}
            color="amber"
          />
        </div>

        {/* Search and Filters - White card style */}
        <div className="bg-white rounded-xl shadow-sm border border-gray-100 overflow-hidden">
          <div className="p-4">
            {/* Search Row */}
            <div className="flex flex-col sm:flex-row gap-3">
              {/* Search Input */}
              <div className="flex-1 flex gap-2">
                <div className="relative flex-1">
                  <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
                  <input
                    type="text"
                    placeholder="Search by name, email, phone, or type (brand, influencer)..."
                    value={searchInput}
                    onChange={(e) => setSearchInput(e.target.value)}
                    onKeyPress={handleSearchKeyPress}
                    className="w-full pl-10 pr-10 py-2.5 bg-gray-50 border border-gray-200 rounded-lg text-gray-900 text-sm placeholder-gray-400 focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all"
                  />
                  {searchInput && (
                    <button 
                      onClick={clearSearch}
                      className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  )}
                </div>
                <button
                  onClick={handleSearch}
                  className="px-4 py-2.5 bg-purple-600 hover:bg-purple-700 text-white rounded-lg font-semibold text-xs transition-all flex items-center gap-2 shadow-md"
                >
                  <Search className="w-3.5 h-3.5" />
                  <span className="hidden sm:inline">Search</span>
                </button>
              </div>

              {/* Filter Toggle Button */}
              <button
                onClick={() => setShowFilters(!showFilters)}
                className={`flex items-center gap-2 px-4 py-2.5 rounded-lg font-semibold text-xs transition-all ${
                  showFilters || statusFilter !== 'ALL'
                    ? 'bg-purple-100 text-purple-700 border border-purple-200'
                    : 'bg-gray-100 text-gray-700 border border-gray-200 hover:bg-gray-200'
                }`}
              >
                <Filter className="w-3.5 h-3.5" />
                <span>Filters</span>
                {statusFilter !== 'ALL' && (
                  <span className="w-2 h-2 rounded-full bg-purple-600" />
                )}
                {showFilters ? <ChevronUp className="w-3.5 h-3.5" /> : <ChevronDown className="w-3.5 h-3.5" />}
              </button>
            </div>

            {/* Filters Row (Collapsible) */}
            {showFilters && (
              <div className="mt-4 pt-4 border-t border-gray-100 grid grid-cols-1 sm:grid-cols-2 gap-4">
                {/* Status Filter */}
                <div>
                  <label className="block text-[10px] font-semibold text-gray-500 uppercase mb-1.5">Status</label>
                  <select
                    value={statusFilter}
                    onChange={(e) => handleFilterChange('status', e.target.value)}
                    className="w-full px-3 py-2 bg-gray-50 border border-gray-200 rounded-lg text-gray-900 text-sm focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                  >
                    <option value="ALL">All Status</option>
                    <option value="ACTIVE">Active</option>
                    <option value="SUSPENDED">Suspended</option>
                    <option value="DELETED">Deleted</option>
                  </select>
                </div>

                {/* Sort */}
                <div>
                  <label className="block text-[10px] font-semibold text-gray-500 uppercase mb-1.5">Sort By</label>
                  <select
                    value={`${sortBy}-${sortOrder}`}
                    onChange={(e) => {
                      const [field, order] = e.target.value.split('-');
                      setSortBy(field);
                      setSortOrder(order);
                    }}
                    className="w-full px-3 py-2 bg-gray-50 border border-gray-200 rounded-lg text-gray-900 text-sm focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                  >
                    <option value="created_at-desc">Newest First</option>
                    <option value="created_at-asc">Oldest First</option>
                    <option value="name-asc">Name A-Z</option>
                    <option value="name-desc">Name Z-A</option>
                  </select>
                </div>
              </div>
            )}

            {/* Active Filters Display */}
            {(activeSearch || statusFilter !== 'ALL') && (
              <div className="mt-3 flex flex-wrap gap-2">
                {activeSearch && (
                  <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-purple-100 text-purple-700 rounded-full text-xs font-medium">
                    Search: "{activeSearch}"
                    <button onClick={clearSearch} className="hover:text-purple-900">
                      <X className="w-3 h-3" />
                    </button>
                  </span>
                )}
                {statusFilter !== 'ALL' && (
                  <span className="inline-flex items-center gap-1.5 px-2.5 py-1 bg-emerald-100 text-emerald-700 rounded-full text-xs font-medium">
                    Status: {statusFilter}
                    <button onClick={() => setStatusFilter('ALL')} className="hover:text-emerald-900">
                      <X className="w-3 h-3" />
                    </button>
                  </span>
                )}
              </div>
            )}
          </div>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-rose-50 border border-rose-200 rounded-xl p-4 flex items-center gap-3">
            <AlertCircle className="w-5 h-5 text-rose-600 flex-shrink-0" />
            <p className="text-sm text-rose-700">{error}</p>
          </div>
        )}

        {/* Users List - White card style */}
        <div className="bg-white rounded-xl shadow-sm border border-gray-100 overflow-hidden">
          {/* Desktop Table */}
          <div className="hidden lg:block overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-gray-100 bg-gray-50/50">
                  <th className="text-left px-4 py-3 text-[10px] font-semibold text-gray-500 uppercase tracking-wider">User</th>
                  <th className="text-left px-4 py-3 text-[10px] font-semibold text-gray-500 uppercase tracking-wider">Type</th>
                  <th className="text-left px-4 py-3 text-[10px] font-semibold text-gray-500 uppercase tracking-wider">Status</th>
                  <th className="text-left px-4 py-3 text-[10px] font-semibold text-gray-500 uppercase tracking-wider">Subscription</th>
                  <th className="text-left px-4 py-3 text-[10px] font-semibold text-gray-500 uppercase tracking-wider">Uploads</th>
                  <th className="text-left px-4 py-3 text-[10px] font-semibold text-gray-500 uppercase tracking-wider">Joined</th>
                  <th className="text-right px-4 py-3 text-[10px] font-semibold text-gray-500 uppercase tracking-wider">Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-50">
                {users.length === 0 ? (
                  <tr>
                    <td colSpan={7} className="px-4 py-12 text-center">
                      <div className="w-12 h-12 bg-gray-100 rounded-xl flex items-center justify-center mx-auto mb-3">
                        <Users className="w-6 h-6 text-gray-400" />
                      </div>
                      <p className="text-sm font-medium text-gray-600">No users found</p>
                      <p className="text-xs text-gray-400 mt-1">Try searching by name, email, or type (brand, influencer)</p>
                    </td>
                  </tr>
                ) : (
                  users.map((user) => {
                    const roleBadge = getRoleBadge(user.user_type, user.is_admin);
                    const statusBadge = getStatusBadge(user.status);
                    
                    return (
                      <tr key={user.id} className="hover:bg-gray-50/50 transition-colors">
                        {/* User Info */}
                        <td className="px-4 py-3">
                          <div className="flex items-center gap-3">
                            <div className="w-9 h-9 rounded-lg bg-gradient-to-br from-purple-600 to-purple-800 flex items-center justify-center text-white font-bold text-sm flex-shrink-0">
                              {user.name?.charAt(0)?.toUpperCase() || 'U'}
                            </div>
                            <div className="min-w-0">
                              <p className="text-sm font-medium text-gray-900 truncate">{user.name || 'No Name'}</p>
                              <p className="text-[11px] text-gray-500 truncate">{user.email}</p>
                              {user.phone && (
                                <p className="text-[10px] text-gray-400 truncate">{user.phone}</p>
                              )}
                            </div>
                          </div>
                        </td>
                        
                        {/* Type Badge */}
                        <td className="px-4 py-3">
                          <span className={`inline-flex items-center px-2 py-0.5 rounded-full text-[10px] font-semibold ${roleBadge.bg} ${roleBadge.text}`}>
                            {roleBadge.label}
                          </span>
                        </td>
                        
                        {/* Status */}
                        <td className="px-4 py-3">
                          <span className={`inline-flex items-center gap-1.5 px-2 py-0.5 rounded-full text-[10px] font-semibold ${statusBadge.bg} ${statusBadge.text}`}>
                            <span className={`w-1.5 h-1.5 rounded-full ${statusBadge.dot}`}></span>
                            {user.status || 'ACTIVE'}
                          </span>
                        </td>
                        
                        {/* Subscription */}
                        <td className="px-4 py-3">
                          {user.is_subscribed ? (
                            <span className="inline-flex items-center gap-1 px-2 py-0.5 rounded-full text-[10px] font-semibold bg-amber-100 text-amber-700">
                              <Crown className="w-3 h-3" />
                              Pro
                            </span>
                          ) : (
                            <span className="text-[11px] text-gray-400">Free</span>
                          )}
                        </td>
                        
                        {/* Uploads */}
                        <td className="px-4 py-3">
                          <span className="text-[11px] text-gray-600">{user.total_uploads || 0}</span>
                        </td>
                        
                        {/* Joined Date */}
                        <td className="px-4 py-3">
                          <span className="text-[11px] text-gray-500">{formatDate(user.created_at)}</span>
                        </td>
                        
                        {/* Actions */}
                        <td className="px-4 py-3">
                          <div className="flex items-center justify-end gap-1.5">
                            <button
                              onClick={() => openUserModal(user)}
                              className="px-2.5 py-1.5 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-lg text-[11px] font-semibold transition-colors"
                            >
                              View
                            </button>
                            {!user.is_admin && user.status !== 'DELETED' && (
                              user.status === 'ACTIVE' ? (
                                <button
                                  onClick={() => handleUserAction(user.id, 'SUSPENDED')}
                                  disabled={actionLoading === user.id}
                                  className="px-2.5 py-1.5 bg-amber-100 hover:bg-amber-200 text-amber-700 rounded-lg text-[11px] font-semibold transition-colors disabled:opacity-50"
                                >
                                  {actionLoading === user.id ? '...' : 'Suspend'}
                                </button>
                              ) : user.status === 'SUSPENDED' ? (
                                <button
                                  onClick={() => handleUserAction(user.id, 'ACTIVE')}
                                  disabled={actionLoading === user.id}
                                  className="px-2.5 py-1.5 bg-emerald-100 hover:bg-emerald-200 text-emerald-700 rounded-lg text-[11px] font-semibold transition-colors disabled:opacity-50"
                                >
                                  {actionLoading === user.id ? '...' : 'Activate'}
                                </button>
                              ) : null
                            )}
                          </div>
                        </td>
                      </tr>
                    );
                  })
                )}
              </tbody>
            </table>
          </div>

          {/* Mobile Card View */}
          <div className="lg:hidden divide-y divide-gray-100">
            {users.length === 0 ? (
              <div className="px-4 py-12 text-center">
                <div className="w-12 h-12 bg-gray-100 rounded-xl flex items-center justify-center mx-auto mb-3">
                  <Users className="w-6 h-6 text-gray-400" />
                </div>
                <p className="text-sm font-medium text-gray-600">No users found</p>
                <p className="text-xs text-gray-400 mt-1">Try searching by name, email, or type (brand, influencer)</p>
              </div>
            ) : (
              users.map((user) => {
                const roleBadge = getRoleBadge(user.user_type, user.is_admin);
                const statusBadge = getStatusBadge(user.status);
                const isExpanded = expandedRows.has(user.id);
                
                return (
                  <div key={user.id} className="p-4">
                    {/* Main Row */}
                    <div className="flex items-start gap-3">
                      <div className="w-10 h-10 rounded-lg bg-gradient-to-br from-purple-600 to-purple-800 flex items-center justify-center text-white font-bold flex-shrink-0">
                        {user.name?.charAt(0)?.toUpperCase() || 'U'}
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="flex items-start justify-between gap-2">
                          <div className="min-w-0">
                            <p className="text-sm font-medium text-gray-900 truncate">{user.name || 'No Name'}</p>
                            <p className="text-[11px] text-gray-500 truncate">{user.email}</p>
                          </div>
                          <div className="flex items-center gap-2 flex-shrink-0">
                            <span className={`px-2 py-0.5 rounded-full text-[10px] font-semibold ${roleBadge.bg} ${roleBadge.text}`}>
                              {roleBadge.label}
                            </span>
                            <button
                              onClick={() => toggleRowExpand(user.id)}
                              className="p-1.5 hover:bg-gray-100 rounded-lg transition-colors"
                            >
                              {isExpanded ? (
                                <ChevronUp className="w-4 h-4 text-gray-400" />
                              ) : (
                                <ChevronDown className="w-4 h-4 text-gray-400" />
                              )}
                            </button>
                          </div>
                        </div>
                        
                        {/* Quick Stats Row */}
                        <div className="flex items-center gap-2 mt-2">
                          <span className={`inline-flex items-center gap-1 px-2 py-0.5 rounded-full text-[10px] font-semibold ${statusBadge.bg} ${statusBadge.text}`}>
                            <span className={`w-1.5 h-1.5 rounded-full ${statusBadge.dot}`}></span>
                            {user.status || 'ACTIVE'}
                          </span>
                          {user.is_subscribed && (
                            <span className="inline-flex items-center gap-1 px-2 py-0.5 rounded-full text-[10px] font-semibold bg-amber-100 text-amber-700">
                              <Crown className="w-3 h-3" />
                              Pro
                            </span>
                          )}
                          <span className="text-[10px] text-gray-400">{formatDate(user.created_at)}</span>
                        </div>
                      </div>
                    </div>
                    
                    {/* Expanded Content */}
                    {isExpanded && (
                      <div className="mt-4 pt-4 border-t border-gray-100">
                        <div className="grid grid-cols-2 gap-3 mb-4">
                          <div className="bg-gray-50 rounded-lg p-3">
                            <p className="text-[10px] text-gray-500 uppercase font-semibold">Phone</p>
                            <p className="text-sm font-medium text-gray-900 mt-0.5">{user.phone || 'Not set'}</p>
                          </div>
                          <div className="bg-gray-50 rounded-lg p-3">
                            <p className="text-[10px] text-gray-500 uppercase font-semibold">Uploads</p>
                            <p className="text-sm font-medium text-gray-900 mt-0.5">{user.total_uploads || 0}</p>
                          </div>
                        </div>
                        
                        {/* Action Buttons */}
                        <div className="flex items-center gap-2">
                          <button
                            onClick={() => openUserModal(user)}
                            className="flex-1 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg text-xs font-semibold transition-colors"
                          >
                            View Details
                          </button>
                          {!user.is_admin && user.status !== 'DELETED' && (
                            user.status === 'ACTIVE' ? (
                              <button
                                onClick={() => handleUserAction(user.id, 'SUSPENDED')}
                                disabled={actionLoading === user.id}
                                className="flex-1 py-2 bg-amber-100 hover:bg-amber-200 text-amber-700 rounded-lg text-xs font-semibold transition-colors disabled:opacity-50"
                              >
                                {actionLoading === user.id ? '...' : 'Suspend'}
                              </button>
                            ) : user.status === 'SUSPENDED' ? (
                              <button
                                onClick={() => handleUserAction(user.id, 'ACTIVE')}
                                disabled={actionLoading === user.id}
                                className="flex-1 py-2 bg-emerald-100 hover:bg-emerald-200 text-emerald-700 rounded-lg text-xs font-semibold transition-colors disabled:opacity-50"
                              >
                                {actionLoading === user.id ? '...' : 'Activate'}
                              </button>
                            ) : null
                          )}
                        </div>
                      </div>
                    )}
                  </div>
                );
              })
            )}
          </div>
        </div>
      </div>

      {/* User Details Modal */}
      {showUserModal && selectedUser && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen p-4">
            <div className="fixed inset-0 bg-black/50" onClick={() => setShowUserModal(false)} />
            
            <div className="relative bg-white rounded-2xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
              {/* Modal Header */}
              <div className="sticky top-0 bg-white border-b border-gray-100 px-6 py-4 flex items-center justify-between">
                <h3 className="text-lg font-bold text-gray-900 font-poppins">User Details</h3>
                <button
                  onClick={() => setShowUserModal(false)}
                  className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
                >
                  <X className="w-5 h-5 text-gray-400" />
                </button>
              </div>

              <div className="p-6">
                {/* User Header */}
                <div className="flex items-start gap-4 mb-6 pb-6 border-b border-gray-100">
                  <div className="w-14 h-14 rounded-xl bg-gradient-to-br from-purple-600 to-purple-800 flex items-center justify-center text-white text-xl font-bold flex-shrink-0">
                    {selectedUser.name?.charAt(0)?.toUpperCase() || 'U'}
                  </div>
                  <div className="flex-1 min-w-0">
                    <h4 className="text-lg font-bold text-gray-900">{selectedUser.name || 'No Name'}</h4>
                    <div className="flex flex-wrap items-center gap-2 mt-2">
                      {(() => {
                        const badge = getRoleBadge(selectedUser.user_type, selectedUser.is_admin);
                        return (
                          <span className={`px-2.5 py-1 rounded-full text-xs font-semibold ${badge.bg} ${badge.text}`}>
                            {badge.label}
                          </span>
                        );
                      })()}
                      {(() => {
                        const badge = getStatusBadge(selectedUser.status);
                        return (
                          <span className={`inline-flex items-center gap-1.5 px-2.5 py-1 rounded-full text-xs font-semibold ${badge.bg} ${badge.text}`}>
                            <span className={`w-1.5 h-1.5 rounded-full ${badge.dot}`}></span>
                            {selectedUser.status || 'ACTIVE'}
                          </span>
                        );
                      })()}
                      {selectedUser.is_subscribed && (
                        <span className="inline-flex items-center gap-1.5 px-2.5 py-1 rounded-full text-xs font-semibold bg-amber-100 text-amber-700">
                          <Crown className="w-3.5 h-3.5" />
                          Pro Subscriber
                        </span>
                      )}
                    </div>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {/* Contact Information */}
                  <div className="bg-gray-50 rounded-xl p-4">
                    <h5 className="text-[10px] font-semibold text-gray-500 uppercase tracking-wider mb-3">Contact Information</h5>
                    <div className="space-y-3">
                      <div className="flex items-center gap-3">
                        <Mail className="w-4 h-4 text-gray-400" />
                        <span className="text-sm text-gray-900">{selectedUser.email || 'Not set'}</span>
                      </div>
                      <div className="flex items-center gap-3">
                        <Phone className="w-4 h-4 text-gray-400" />
                        <span className="text-sm text-gray-900">{selectedUser.phone || 'Not set'}</span>
                      </div>
                      <div className="flex items-center gap-3">
                        <Calendar className="w-4 h-4 text-gray-400" />
                        <span className="text-sm text-gray-900">Joined {formatDateTime(selectedUser.created_at)}</span>
                      </div>
                    </div>
                  </div>

                  {/* User Info */}
                  <div className="bg-gray-50 rounded-xl p-4">
                    <h5 className="text-[10px] font-semibold text-gray-500 uppercase tracking-wider mb-3">User Statistics</h5>
                    <div className="grid grid-cols-2 gap-3">
                      <div className="bg-white rounded-lg p-3 text-center shadow-sm">
                        <p className="text-xl font-bold text-purple-700">{selectedUser.total_uploads || 0}</p>
                        <p className="text-[10px] text-gray-500 uppercase">Uploads</p>
                      </div>
                      <div className="bg-white rounded-lg p-3 text-center shadow-sm">
                        <p className="text-xl font-bold text-emerald-700">{selectedUser.listings_count || 0}</p>
                        <p className="text-[10px] text-gray-500 uppercase">Listings</p>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Subscription Section */}
                <div className="mt-6 bg-gray-50 rounded-xl p-4">
                  <h5 className="text-[10px] font-semibold text-gray-500 uppercase tracking-wider mb-3">Subscription</h5>
                  <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
                    <div className="flex items-center gap-3">
                      <div className={`p-3 rounded-xl ${selectedUser.is_subscribed ? 'bg-amber-100' : 'bg-gray-200'}`}>
                        <Crown className={`w-6 h-6 ${selectedUser.is_subscribed ? 'text-amber-600' : 'text-gray-400'}`} />
                      </div>
                      <div>
                        <p className="text-sm font-semibold text-gray-900">
                          {selectedUser.is_subscribed ? 'Pro Subscriber' : 'Free User'}
                        </p>
                        {selectedUser.subscription_expiration && (
                          <p className="text-[11px] text-gray-500 flex items-center gap-1 mt-0.5">
                            <Clock className="w-3 h-3" />
                            Expires: {formatDate(selectedUser.subscription_expiration)}
                          </p>
                        )}
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      <button
                        onClick={openSubscriptionModal}
                        className="px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg text-xs font-semibold transition-colors flex items-center gap-2"
                      >
                        <Crown className="w-3.5 h-3.5" />
                        {selectedUser.is_subscribed ? 'Change Plan' : 'Assign Plan'}
                      </button>
                      {selectedUser.is_subscribed && (
                        <button
                          onClick={handleRemoveSubscription}
                          disabled={assigningSubscription}
                          className="px-4 py-2 bg-rose-100 hover:bg-rose-200 text-rose-700 rounded-lg text-xs font-semibold transition-colors disabled:opacity-50"
                        >
                          Remove
                        </button>
                      )}
                    </div>
                  </div>
                </div>

                {/* Action Buttons */}
                <div className="mt-6 pt-6 border-t border-gray-100 flex flex-wrap items-center justify-end gap-3">
                  {!selectedUser.is_admin && selectedUser.status !== 'DELETED' && (
                    <>
                      {selectedUser.status === 'ACTIVE' ? (
                        <button
                          onClick={() => handleUserAction(selectedUser.id, 'SUSPENDED')}
                          disabled={actionLoading === selectedUser.id}
                          className="px-5 py-2.5 bg-amber-100 hover:bg-amber-200 text-amber-700 rounded-xl font-semibold text-sm transition-colors disabled:opacity-50 flex items-center gap-2"
                        >
                          <Ban className="w-4 h-4" />
                          {actionLoading === selectedUser.id ? 'Processing...' : 'Suspend User'}
                        </button>
                      ) : selectedUser.status === 'SUSPENDED' ? (
                        <button
                          onClick={() => handleUserAction(selectedUser.id, 'ACTIVE')}
                          disabled={actionLoading === selectedUser.id}
                          className="px-5 py-2.5 bg-emerald-100 hover:bg-emerald-200 text-emerald-700 rounded-xl font-semibold text-sm transition-colors disabled:opacity-50 flex items-center gap-2"
                        >
                          <UserCheck className="w-4 h-4" />
                          {actionLoading === selectedUser.id ? 'Processing...' : 'Activate User'}
                        </button>
                      ) : null}
                      <button
                        onClick={() => handleDeleteUser(selectedUser.id)}
                        disabled={actionLoading === selectedUser.id}
                        className="px-5 py-2.5 bg-rose-600 hover:bg-rose-700 text-white rounded-xl font-semibold text-sm transition-colors disabled:opacity-50 flex items-center gap-2"
                      >
                        <Trash2 className="w-4 h-4" />
                        {actionLoading === selectedUser.id ? 'Processing...' : 'Delete User'}
                      </button>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Subscription Assignment Modal */}
      {showSubscriptionModal && selectedUser && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen p-4">
            <div className="fixed inset-0 bg-black/50" onClick={() => setShowSubscriptionModal(false)} />
            
            <div className="relative bg-white rounded-2xl shadow-2xl max-w-lg w-full">
              {/* Modal Header */}
              <div className="border-b border-gray-100 px-6 py-4 flex items-center justify-between">
                <div>
                  <h3 className="text-lg font-bold text-gray-900 font-poppins">Assign Subscription</h3>
                  <p className="text-xs text-gray-500 mt-0.5">{selectedUser.email}</p>
                </div>
                <button
                  onClick={() => setShowSubscriptionModal(false)}
                  className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
                >
                  <X className="w-5 h-5 text-gray-400" />
                </button>
              </div>

              <div className="p-6">
                {loadingPlans ? (
                  <div className="text-center py-8">
                    <div className="w-10 h-10 border-3 border-gray-200 border-t-purple-600 rounded-full animate-spin mx-auto mb-4" />
                    <p className="text-sm text-gray-500">Loading plans...</p>
                  </div>
                ) : subscriptionPlans.length === 0 ? (
                  <div className="text-center py-8">
                    <p className="text-sm text-gray-500">No subscription plans available</p>
                  </div>
                ) : (
                  <div className="space-y-3 max-h-80 overflow-y-auto">
                    {subscriptionPlans.map((plan) => (
                      <div
                        key={plan.plan_id}
                        onClick={() => setSelectedPlanId(plan.plan_id)}
                        className={`p-4 rounded-xl cursor-pointer transition-all border-2 ${
                          selectedPlanId === plan.plan_id
                            ? 'border-purple-500 bg-purple-50'
                            : 'border-gray-200 hover:border-gray-300 bg-white'
                        }`}
                      >
                        <div className="flex items-center justify-between">
                          <div className="flex-1">
                            <div className="flex items-center gap-2 mb-1">
                              <h4 className="font-semibold text-gray-900 text-sm">{plan.plan_name}</h4>
                              <span className={`px-1.5 py-0.5 rounded text-[9px] font-semibold ${
                                plan.plan_type === 'YEARLY' ? 'bg-emerald-100 text-emerald-700' :
                                plan.plan_type === 'MONTHLY' ? 'bg-blue-100 text-blue-700' :
                                'bg-purple-100 text-purple-700'
                              }`}>
                                {plan.plan_type}
                              </span>
                            </div>
                            {plan.plan_description && (
                              <p className="text-xs text-gray-500 mb-2">{plan.plan_description}</p>
                            )}
                            <div className="flex items-baseline gap-1">
                              <span className="text-lg font-bold text-purple-700">{parseFloat(plan.price).toFixed(2)}</span>
                              <span className="text-[10px] text-gray-500">/ {plan.duration_days} days</span>
                            </div>
                          </div>
                          <div className={`w-5 h-5 rounded-full border-2 flex items-center justify-center ${
                            selectedPlanId === plan.plan_id
                              ? 'border-purple-600 bg-purple-600'
                              : 'border-gray-300'
                          }`}>
                            {selectedPlanId === plan.plan_id && (
                              <div className="w-2.5 h-2.5 rounded-full bg-white" />
                            )}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}

                {/* Action Buttons */}
                <div className="flex items-center justify-end gap-3 mt-6 pt-6 border-t border-gray-100">
                  <button
                    onClick={() => setShowSubscriptionModal(false)}
                    className="px-5 py-2.5 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-xl font-semibold text-sm transition-colors"
                  >
                    Cancel
                  </button>
                  <button
                    onClick={handleAssignSubscription}
                    disabled={!selectedPlanId || assigningSubscription}
                    className="px-5 py-2.5 bg-purple-600 hover:bg-purple-700 text-white rounded-xl font-semibold text-sm transition-colors disabled:opacity-50 flex items-center gap-2"
                  >
                    {assigningSubscription ? (
                      <>
                        <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                        Assigning...
                      </>
                    ) : (
                      <>
                        <Crown className="w-4 h-4" />
                        Assign
                      </>
                    )}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UserManagement;
