import React, { useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const Welcome = () => {
  const navigate = useNavigate();
  const { isAuthenticated, loading } = useAuth();
  const phoneRef = useRef(null);
  const prefersReducedMotion = window.matchMedia('(prefers-reduced-motion: reduce)').matches;

  useEffect(() => {
    // Redirect authenticated users to home
    if (!loading && isAuthenticated) {
      navigate('/home', { replace: true });
    }
  }, [isAuthenticated, loading, navigate]);

  useEffect(() => {
    if (prefersReducedMotion) return;

    // Entrance animation for phone
    const phone = phoneRef.current;

    if (phone) {
      phone.style.animation = 'fadeInSlide 0.8s ease-out';
    }
  }, [prefersReducedMotion]);

  const handleGetStarted = () => {
    navigate('/login');
  };

  // Don't render if loading or authenticated
  if (loading || isAuthenticated) {
    return null;
  }

  return (
    <div 
      className="min-h-screen w-full relative overflow-hidden flex flex-col"
      role="main"
      aria-label="Welcome to GetNayi"
    >
      {/* Purple to Blue Radial Gradient Background */}
      <div 
        className="absolute inset-0"
        style={{
          background: 'radial-gradient(ellipse at center, #6A00FF 0%, #7C3AED 25%, #9333EA 45%, #A855F7 65%, #3B82F6 85%, #60A5FA 100%)'
        }}
      />

      {/* Soft White Fog Blur at Lower-Left - Large and Prominent */}
      <div 
        className="absolute bottom-0 left-0 w-full h-3/4 opacity-50"
        style={{
          background: 'radial-gradient(ellipse 180% 120% at 15% 100%, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.6) 30%, rgba(255, 255, 255, 0.3) 50%, transparent 75%)',
          filter: 'blur(100px)',
          transform: 'translateY(10%)'
        }}
        aria-hidden="true"
      />

      {/* Phone Mockup Container - Positioned just above logo banner */}
      <div 
        className="absolute z-10"
        style={{
          bottom: '250px', // Positioned just above the logo block (logo at 48px + ~330px height + spacing)
          left: '50%',
          transform: 'translateX(-50%)',
          width: '220px',
          height: '430px'
        }}
      >
        <div 
          ref={phoneRef}
          className="relative w-full h-full"
          style={{
            filter: 'drop-shadow(0 30px 60px -12px rgba(0, 0, 0, 0.35))'
          }}
        >
          {/* Phone Image */}
          <img
            src="/assets/images/phone-welcome.png"
            alt="Phone mockup displaying product with product image and ADD TO CART badges"
            className="w-full h-full object-contain"
            style={{
              userSelect: 'none',
              pointerEvents: 'none'
            }}
          />
        </div>
      </div>

      {/* Bottom-Left Brand Block */}
      <div 
        className="absolute z-10"
        style={{
          bottom: '48px',
          left: '24px',
          width: '60%',
          maxWidth: '320px'
        }}
      >
        {/* Logo + Wordmark */}
        <div className="mb-6">
          <img
            src="/assets/images/logo-wordmark.png"
            alt="GetNayi logo and wordmark"
            className="h-12 object-contain"
            style={{
              userSelect: 'none',
              pointerEvents: 'none'
            }}
          />
        </div>

        {/* Headline - Two line headline */}
        <h2 
          className="text-3xl font-bold mb-6 leading-tight font-poppins"
          style={{
            color: '#1A1A1A', // Near-black
            fontSize: '32px',
            lineHeight: '1.2'
          }}
        >
          Unleash <span className="text-[#6A00FF]">New Age</span> of<br />Ecommerce
        </h2>

        {/* Get Started CTA Button - Deep purple pill */}
        <button
          onClick={handleGetStarted}
          className="w-full text-white font-bold py-4 px-8 rounded-full text-base transition-all duration-200 shadow-xl font-poppins touch-manipulation active:scale-95"
          style={{
            background: '#6A00FF', // Deep purple
            boxShadow: '0 12px 30px -5px rgba(106, 0, 255, 0.55), 0 4px 8px -2px rgba(106, 0, 255, 0.4)',
            fontSize: '16px',
            letterSpacing: '0.025em',
            fontWeight: '700'
          }}
          aria-label="Get started with GetNayi"
          onMouseDown={(e) => {
            if (!prefersReducedMotion) {
              e.currentTarget.style.transform = 'scale(0.95)';
            }
          }}
          onMouseUp={(e) => {
            e.currentTarget.style.transform = 'scale(1)';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.transform = 'scale(1)';
          }}
        >
          Get Started
        </button>
      </div>

      {/* CSS Animations */}
      <style>{`
        @keyframes fadeInSlide {
          from {
            opacity: 0;
            transform: translateY(30px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @media (prefers-reduced-motion: reduce) {
          * {
            animation-duration: 0.01ms !important;
            animation-iteration-count: 1 !important;
            transition-duration: 0.01ms !important;
          }
        }
      `}</style>
    </div>
  );
};

export default Welcome;
