import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Package, Briefcase, Loader, List } from 'lucide-react';
import { providerAPI } from '../utils/api';
import { useAuth } from '../context/AuthContext';

const Listing = () => {
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();
  const [loading, setLoading] = useState(true);
  const [hasProvider, setHasProvider] = useState(false);

  useEffect(() => {
    if (isAuthenticated) {
      checkProviderProfile();
    } else {
      setLoading(false);
    }
  }, [isAuthenticated]);

  const checkProviderProfile = async () => {
    try {
      setLoading(true);
      const response = await providerAPI.getMe();
      if (response && response.provider) {
        setHasProvider(true);
      } else {
        setHasProvider(false);
      }
    } catch (error) {
      // Handle 401 gracefully - user not authenticated or no provider
      if (error.response?.status === 401) {
        setHasProvider(false);
      } else {
        console.error('Error checking provider:', error);
        setHasProvider(false);
      }
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
      </div>
    );
  }

  if (!hasProvider) {
    return (
      <div className="min-h-screen bg-white">
        <header className="w-full bg-white border-b border-gray-200 sticky top-0 z-50">
          <div className="px-4 sm:px-5 py-3.5 sm:py-4 flex items-center justify-center">
            <div className="flex items-center">
              <span className="text-2xl sm:text-[24px] font-poppins font-bold">
                <span className="text-black">Get</span>
                <span className="text-[#6C4DFF]">nayi</span>
              </span>
            </div>
          </div>
        </header>
        <div className="px-4 sm:px-5 py-12 flex items-center justify-center min-h-screen">
          <div className="text-center max-w-md">
            <h2 className="text-2xl font-bold text-gray-900 mb-4 font-poppins">Provider Profile Required</h2>
            <p className="text-gray-600 mb-6 font-poppins">
              You need to register as a provider before creating listings.
            </p>
            <button
              onClick={() => navigate('/provider/register')}
              className="bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-6 py-3 rounded-full transition-colors font-medium font-poppins shadow-md hover:shadow-lg active:scale-[0.96]"
            >
              Register as Provider
            </button>
          </div>
        </div>
      </div>
    );
  }

  const listingOptions = [
    {
      id: 'product',
      title: 'List a Product',
      description: 'Showcase your physical products to thousands of users',
      icon: Package,
      route: '/listing/upload?type=PRODUCT'
    }
  ];

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="w-full bg-white border-b border-gray-200 sticky top-0 z-50">
        <div className="px-4 sm:px-5 py-3.5 sm:py-4 flex items-center justify-center">
          <div className="flex items-center">
            <span className="text-2xl sm:text-[24px] font-poppins font-bold">
              <span className="text-black">Get</span>
              <span className="text-[#6C4DFF]">nayi</span>
            </span>
          </div>
        </div>
      </header>

      <div className="px-4 sm:px-5 py-6 sm:py-8 pb-32 sm:pb-12 max-w-4xl mx-auto">
        {/* Header Section */}
        <div className="flex items-center justify-between mb-8">
          <div>
            <h1 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-2 font-poppins">Create a Listing</h1>
            <p className="text-sm sm:text-base text-gray-600 font-poppins">Choose what you'd like to list</p>
          </div>
          <button
            onClick={() => navigate('/listing/my')}
            className="bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-4 sm:px-6 py-2 sm:py-3 rounded-full text-sm sm:text-base font-semibold transition-all flex items-center gap-2 shadow-md hover:shadow-lg active:scale-[0.96] font-poppins"
          >
            <List className="w-4 h-4 sm:w-5 sm:h-5" />
            <span className="hidden sm:inline">My Listings</span>
            <span className="sm:hidden">My</span>
          </button>
        </div>

        {/* Listing Options */}
        <div className="grid md:grid-cols-2 gap-6 mb-12">
          {listingOptions.map((option) => {
            const Icon = option.icon;
            return (
              <div
                key={option.id}
                onClick={() => navigate(option.route)}
                className="bg-white border-2 border-gray-200 rounded-2xl shadow-sm hover:shadow-lg hover:border-[#6C4DFF] transition-all duration-300 cursor-pointer overflow-hidden group active:scale-[0.98]"
              >
                <div className="h-24 sm:h-32 bg-[#E8D5FF] flex items-center justify-center group-hover:bg-[#6C4DFF] transition-colors duration-300">
                  <Icon className="w-12 h-12 sm:w-16 sm:h-16 text-[#6C4DFF] group-hover:text-white transition-colors duration-300" strokeWidth={1.5} />
                </div>
                <div className="p-4 sm:p-6">
                  <h3 className="text-lg sm:text-xl font-bold text-gray-900 mb-2 font-poppins">{option.title}</h3>
                  <p className="text-gray-600 font-poppins mb-4 text-xs sm:text-sm">{option.description}</p>
                  <div className="w-full bg-[#6C4DFF] text-white py-2 sm:py-3 rounded-full text-sm sm:text-base font-medium text-center font-poppins hover:bg-[#5a3ddb] transition-colors">
                    Get Started 
                  </div>
                </div>
              </div>
            );
          })}
        </div>

        {/* Info Section */}
        <div className="bg-gray-50 border border-gray-200 rounded-2xl shadow-sm p-4 sm:p-8">
          <h2 className="text-lg sm:text-xl font-bold text-gray-900 mb-4 sm:mb-6 font-poppins">How it works</h2>
          <div className="grid md:grid-cols-3 gap-4 sm:gap-6">
            <div className="text-center">
              <div className="w-10 h-10 sm:w-12 sm:h-12 bg-[#6C4DFF] rounded-full flex items-center justify-center mx-auto mb-2 sm:mb-3">
                <span className="text-white font-bold text-base sm:text-lg font-poppins">1</span>
              </div>
              <h3 className="text-sm sm:text-base font-semibold text-gray-900 mb-1 font-poppins">Choose Type</h3>
              <p className="text-xs sm:text-sm text-gray-600 font-poppins">Select whether you're listing a product or service</p>
            </div>
            <div className="text-center">
              <div className="w-10 h-10 sm:w-12 sm:h-12 bg-[#6C4DFF] rounded-full flex items-center justify-center mx-auto mb-2 sm:mb-3">
                <span className="text-white font-bold text-base sm:text-lg font-poppins">2</span>
              </div>
              <h3 className="text-sm sm:text-base font-semibold text-gray-900 mb-1 font-poppins">Upload Details</h3>
              <p className="text-xs sm:text-sm text-gray-600 font-poppins">Add video, images, and information about your listing</p>
            </div>
            <div className="text-center">
              <div className="w-10 h-10 sm:w-12 sm:h-12 bg-[#6C4DFF] rounded-full flex items-center justify-center mx-auto mb-2 sm:mb-3">
                <span className="text-white font-bold text-base sm:text-lg font-poppins">3</span>
              </div>
              <h3 className="text-sm sm:text-base font-semibold text-gray-900 mb-1 font-poppins">Go Live</h3>
              <p className="text-xs sm:text-sm text-gray-600 font-poppins">Your listing will be visible to thousands of users</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Listing;
