import React, { useState, useEffect } from 'react';
import { adminAPI } from '../utils/api';

const SubscriptionManagement = () => {
  const [activeTab, setActiveTab] = useState('plans');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  
  // Plans state
  const [plans, setPlans] = useState([]);
  const [showPlanModal, setShowPlanModal] = useState(false);
  const [editingPlan, setEditingPlan] = useState(null);
  const [planForm, setPlanForm] = useState({
    plan_name: '',
    plan_description: '',
    plan_type: 'MONTHLY',
    price: '',
    duration_days: '',
    features: '',
    is_active: true
  });

  // Coupons state
  const [coupons, setCoupons] = useState([]);
  const [showCouponModal, setShowCouponModal] = useState(false);
  const [editingCoupon, setEditingCoupon] = useState(null);
  const [couponForm, setCouponForm] = useState({
    coupon_code: '',
    coupon_name: '',
    discount_type: 'PERCENTAGE',
    discount_value: '',
    usage_limit: '',
    valid_from: '',
    valid_until: '',
    is_active: true
  });

  // Transactions state
  const [transactions, setTransactions] = useState([]);
  const [transactionFilter, setTransactionFilter] = useState('ALL');

  // Alerts state
  const [alerts, setAlerts] = useState([]);

  useEffect(() => {
    fetchAllData();
  }, []);

  const fetchAllData = async () => {
    try {
      setLoading(true);
      await Promise.all([
        fetchPlans(),
        fetchCoupons(),
        fetchTransactions(),
        fetchAlerts()
      ]);
    } catch (err) {
      setError('Failed to load subscription data');
    } finally {
      setLoading(false);
    }
  };

  const fetchPlans = async () => {
    try {
      const response = await adminAPI.getSubscriptionPlans();
      if (response.ok) {
        setPlans(response.data.plans || []);
      }
    } catch (err) {
      console.error('Failed to fetch plans:', err);
    }
  };

  const fetchCoupons = async () => {
    try {
      const response = await adminAPI.getDiscountCoupons();
      if (response.ok) {
        setCoupons(response.data.coupons || []);
      }
    } catch (err) {
      console.error('Failed to fetch coupons:', err);
    }
  };

  const fetchTransactions = async () => {
    try {
      const response = await adminAPI.getSubscriptionTransactions();
      if (response.ok) {
        setTransactions(response.data.transactions || []);
      }
    } catch (err) {
      console.error('Failed to fetch transactions:', err);
    }
  };

  const fetchAlerts = async () => {
    try {
      const response = await adminAPI.getSubscriptionAlerts();
      if (response.ok) {
        setAlerts(response.data.alerts || []);
      }
    } catch (err) {
      console.error('Failed to fetch alerts:', err);
    }
  };

  const handlePlanSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = editingPlan 
        ? await adminAPI.updateSubscriptionPlan(editingPlan.plan_id, planForm)
        : await adminAPI.createSubscriptionPlan(planForm);
      
      if (response.ok) {
        await fetchPlans();
        setShowPlanModal(false);
        setEditingPlan(null);
        setPlanForm({
          plan_name: '',
          plan_description: '',
          plan_type: 'MONTHLY',
          price: '',
          duration_days: '',
          features: '',
          is_active: true
        });
      } else {
        setError(response.message || 'Failed to save plan');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const handleCouponSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = editingCoupon
        ? await adminAPI.updateDiscountCoupon(editingCoupon.coupon_id, couponForm)
        : await adminAPI.createDiscountCoupon(couponForm);
      
      if (response.ok) {
        await fetchCoupons();
        setShowCouponModal(false);
        setEditingCoupon(null);
        setCouponForm({
          coupon_code: '',
          coupon_name: '',
          discount_type: 'PERCENTAGE',
          discount_value: '',
          usage_limit: '',
          valid_from: '',
          valid_until: '',
          is_active: true
        });
      } else {
        setError(response.message || 'Failed to save coupon');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    }
  };

  const handleEditPlan = (plan) => {
    setEditingPlan(plan);
    setPlanForm({
      plan_name: plan.plan_name,
      plan_description: plan.plan_description,
      plan_type: plan.plan_type,
      price: plan.price,
      duration_days: plan.duration_days,
      features: typeof plan.features === 'string' ? plan.features : JSON.stringify(plan.features, null, 2),
      is_active: plan.is_active
    });
    setShowPlanModal(true);
  };

  const handleEditCoupon = (coupon) => {
    setEditingCoupon(coupon);
    setCouponForm({
      coupon_code: coupon.coupon_code,
      coupon_name: coupon.coupon_name,
      discount_type: coupon.discount_type,
      discount_value: coupon.discount_value,
      usage_limit: coupon.usage_limit,
      valid_from: coupon.valid_from ? coupon.valid_from.split('T')[0] : '',
      valid_until: coupon.valid_until ? coupon.valid_until.split('T')[0] : '',
      is_active: coupon.is_active
    });
    setShowCouponModal(true);
  };

  const getStatusBadgeColor = (status) => {
    switch (status) {
      case 'COMPLETED': return 'bg-green-100 text-green-800';
      case 'PENDING': return 'bg-yellow-100 text-yellow-800';
      case 'FAILED': return 'bg-red-100 text-red-800';
      case 'REFUNDED': return 'bg-blue-100 text-blue-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getPlanTypeBadgeColor = (type) => {
    switch (type) {
      case 'MONTHLY': return 'bg-blue-100 text-blue-800';
      case 'YEARLY': return 'bg-green-100 text-green-800';
      case 'INFLUENCER': return 'bg-purple-100 text-purple-800';
      case 'BRAND': return 'bg-orange-100 text-orange-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const filteredTransactions = transactions.filter(transaction => {
    if (transactionFilter === 'ALL') return true;
    return transaction.payment_status === transactionFilter;
  });

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-20 h-20 border-4 border-gray-200 border-t-blue-600 rounded-full animate-spin mx-auto mb-6"></div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2">Loading Subscription Data</h2>
          <p className="text-lg text-gray-600">Fetching subscription information...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="px-4 sm:px-6 py-4 sm:py-6 lg:py-8">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3 sm:gap-4">
            <div>
              <h1 className="text-xl sm:text-2xl lg:text-4xl font-bold text-gray-900 mb-1 sm:mb-2">Subscription Management</h1>
              <p className="text-gray-600 text-sm sm:text-base lg:text-lg">Manage subscription plans, coupons, and transactions</p>
            </div>
            <button 
              onClick={fetchAllData}
              className="bg-blue-600 text-white px-4 sm:px-6 py-2.5 sm:py-3 rounded-lg sm:rounded-xl hover:bg-blue-700 transition-all duration-200 flex items-center space-x-2 sm:space-x-3 shadow-lg text-sm sm:text-base"
            >
              <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
              </svg>
              <span className="font-semibold">Refresh</span>
            </button>
          </div>
        </div>
      </div>

      <div className="px-4 sm:px-6 py-4 sm:py-6 lg:py-8">
        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg sm:rounded-xl p-3 sm:p-4 mb-4 sm:mb-6">
            <div className="flex items-center">
              <svg className="w-4 h-4 sm:w-5 sm:h-5 text-red-600 mr-2 sm:mr-3 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-red-800 font-medium text-sm sm:text-base">{error}</p>
            </div>
          </div>
        )}

        {/* Tabs */}
        <div className="bg-white rounded-xl sm:rounded-2xl shadow-lg border border-gray-100 overflow-hidden mb-6 sm:mb-8">
          <div className="border-b border-gray-200 overflow-x-auto">
            <nav className="flex min-w-max">
              <button
                onClick={() => setActiveTab('plans')}
                className={`px-3 sm:px-6 py-3 sm:py-4 text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                  activeTab === 'plans'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Plans ({plans.length})
              </button>
              <button
                onClick={() => setActiveTab('coupons')}
                className={`px-3 sm:px-6 py-3 sm:py-4 text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                  activeTab === 'coupons'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Coupons ({coupons.length})
              </button>
              <button
                onClick={() => setActiveTab('transactions')}
                className={`px-3 sm:px-6 py-3 sm:py-4 text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                  activeTab === 'transactions'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Txns ({transactions.length})
              </button>
              <button
                onClick={() => setActiveTab('alerts')}
                className={`px-3 sm:px-6 py-3 sm:py-4 text-xs sm:text-sm font-medium transition-colors whitespace-nowrap ${
                  activeTab === 'alerts'
                    ? 'bg-blue-600 text-white border-b-2 border-blue-600'
                    : 'text-gray-600 hover:text-gray-900 hover:bg-gray-50'
                }`}
              >
                Alerts ({alerts.length})
              </button>
            </nav>
          </div>

          <div className="p-4 sm:p-6">
            {/* Subscription Plans Tab */}
            {activeTab === 'plans' && (
              <div>
                <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-3 mb-4 sm:mb-6">
                  <h3 className="text-lg sm:text-xl font-semibold text-gray-900">Subscription Plans</h3>
                  <button
                    onClick={() => {
                      setEditingPlan(null);
                      setPlanForm({
                        plan_name: '',
                        plan_description: '',
                        plan_type: 'MONTHLY',
                        price: '',
                        duration_days: '',
                        features: '',
                        is_active: true
                      });
                      setShowPlanModal(true);
                    }}
                    className="bg-green-600 text-white px-3 sm:px-4 py-2 rounded-lg sm:rounded-xl hover:bg-green-700 transition-colors text-sm sm:text-base"
                  >
                    Add New Plan
                  </button>
                </div>

                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
                  {plans.map((plan) => (
                    <div key={plan.plan_id} className="bg-gray-50 rounded-lg sm:rounded-xl p-4 sm:p-6 border border-gray-200">
                      <div className="flex justify-between items-start mb-3 sm:mb-4">
                        <h4 className="text-base sm:text-lg font-semibold text-gray-900 pr-2">{plan.plan_name}</h4>
                        <span className={`px-2 py-1 rounded-full text-xs font-medium flex-shrink-0 ${getPlanTypeBadgeColor(plan.plan_type)}`}>
                          {plan.plan_type}
                        </span>
                      </div>
                      <p className="text-gray-600 mb-3 sm:mb-4 text-sm line-clamp-2">{plan.plan_description}</p>
                      <div className="flex justify-between items-center mb-3 sm:mb-4">
                        <span className="text-xl sm:text-2xl font-bold text-blue-600">{plan.price}</span>
                        <span className="text-xs sm:text-sm text-gray-500">{plan.duration_days} days</span>
                      </div>
                      <div className="flex justify-between">
                        <button
                          onClick={() => handleEditPlan(plan)}
                          className="text-blue-600 hover:text-blue-800 text-xs sm:text-sm font-medium"
                        >
                          Edit
                        </button>
                        <span className={`text-xs sm:text-sm ${plan.is_active ? 'text-green-600' : 'text-red-600'}`}>
                          {plan.is_active ? 'Active' : 'Inactive'}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Discount Coupons Tab */}
            {activeTab === 'coupons' && (
              <div>
                <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-3 mb-4 sm:mb-6">
                  <h3 className="text-lg sm:text-xl font-semibold text-gray-900">Discount Coupons</h3>
                  <button
                    onClick={() => {
                      setEditingCoupon(null);
                      setCouponForm({
                        coupon_code: '',
                        coupon_name: '',
                        discount_type: 'PERCENTAGE',
                        discount_value: '',
                        usage_limit: '',
                        valid_from: '',
                        valid_until: '',
                        is_active: true
                      });
                      setShowCouponModal(true);
                    }}
                    className="bg-green-600 text-white px-3 sm:px-4 py-2 rounded-lg sm:rounded-xl hover:bg-green-700 transition-colors text-sm sm:text-base"
                  >
                    Add New Coupon
                  </button>
                </div>

                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6">
                  {coupons.map((coupon) => (
                    <div key={coupon.coupon_id} className="bg-gray-50 rounded-lg sm:rounded-xl p-4 sm:p-6 border border-gray-200">
                      <div className="flex justify-between items-start mb-3 sm:mb-4">
                        <h4 className="text-base sm:text-lg font-semibold text-gray-900 pr-2 truncate">{coupon.coupon_name}</h4>
                        <span className="px-2 py-1 bg-purple-100 text-purple-800 rounded-full text-xs font-medium flex-shrink-0">
                          {coupon.coupon_code}
                        </span>
                      </div>
                      <div className="mb-3 sm:mb-4 space-y-1.5 sm:space-y-2">
                        <div className="flex justify-between items-center">
                          <span className="text-xs sm:text-sm text-gray-600">Discount:</span>
                          <span className="font-semibold text-gray-900 text-sm">
                            {coupon.discount_type === 'PERCENTAGE'
                              ? `${coupon.discount_value}%`
                              : `${Number(coupon.discount_value).toFixed(2)}`}
                          </span>
                        </div>
                        <div className="flex justify-between items-center">
                          <span className="text-xs sm:text-sm text-gray-600">Usage:</span>
                          <span className="text-xs sm:text-sm text-gray-900">
                            {coupon.used_count}/{coupon.usage_limit || ''}
                          </span>
                        </div>
                        <div className="flex justify-between items-center">
                          <span className="text-xs sm:text-sm text-gray-600">Valid Until:</span>
                          <span className="text-xs sm:text-sm text-gray-900">{new Date(coupon.valid_until).toLocaleDateString()}</span>
                        </div>
                      </div>
                      <div className="flex justify-between">
                        <button
                          onClick={() => handleEditCoupon(coupon)}
                          className="text-blue-600 hover:text-blue-800 text-xs sm:text-sm font-medium"
                        >
                          Edit
                        </button>
                        <span className={`text-xs sm:text-sm ${coupon.is_active ? 'text-green-600' : 'text-red-600'}`}>
                          {coupon.is_active ? 'Active' : 'Inactive'}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Transactions Tab */}
            {activeTab === 'transactions' && (
              <div>
                <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-3 mb-4 sm:mb-6">
                  <h3 className="text-lg sm:text-xl font-semibold text-gray-900">Transaction Logs</h3>
                  <select
                    value={transactionFilter}
                    onChange={(e) => setTransactionFilter(e.target.value)}
                    className="px-3 sm:px-4 py-2 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm sm:text-base"
                  >
                    <option value="ALL">All Status</option>
                    <option value="COMPLETED">Completed</option>
                    <option value="PENDING">Pending</option>
                    <option value="FAILED">Failed</option>
                    <option value="REFUNDED">Refunded</option>
                  </select>
                </div>

                <div className="overflow-x-auto -mx-4 sm:mx-0">
                  <table className="w-full min-w-[500px]">
                    <thead>
                      <tr className="border-b border-gray-200">
                        <th className="text-left py-2 sm:py-3 px-3 sm:px-4 font-semibold text-gray-900 text-xs sm:text-sm">User</th>
                        <th className="text-left py-2 sm:py-3 px-3 sm:px-4 font-semibold text-gray-900 text-xs sm:text-sm">Plan</th>
                        <th className="text-left py-2 sm:py-3 px-3 sm:px-4 font-semibold text-gray-900 text-xs sm:text-sm">Amount</th>
                        <th className="text-left py-2 sm:py-3 px-3 sm:px-4 font-semibold text-gray-900 text-xs sm:text-sm">Status</th>
                        <th className="text-left py-2 sm:py-3 px-3 sm:px-4 font-semibold text-gray-900 text-xs sm:text-sm hidden sm:table-cell">Date</th>
                      </tr>
                    </thead>
                    <tbody>
                      {filteredTransactions.map((transaction) => (
                        <tr key={transaction.transaction_id} className="border-b border-gray-100">
                          <td className="py-2 sm:py-3 px-3 sm:px-4 text-xs sm:text-sm">{transaction.user_name}</td>
                          <td className="py-2 sm:py-3 px-3 sm:px-4 text-xs sm:text-sm">{transaction.plan_name}</td>
                          <td className="py-2 sm:py-3 px-3 sm:px-4">
                            <div>
                              <div className="font-semibold text-xs sm:text-sm">${transaction.final_amount}</div>
                              {transaction.discount_amount > 0 && (
                                <div className="text-xs text-green-600">
                                  -${transaction.discount_amount}
                                </div>
                              )}
                            </div>
                          </td>
                          <td className="py-2 sm:py-3 px-3 sm:px-4">
                            <span className={`px-2 py-0.5 sm:py-1 rounded-full text-xs font-medium ${getStatusBadgeColor(transaction.payment_status)}`}>
                              {transaction.payment_status}
                            </span>
                          </td>
                          <td className="py-2 sm:py-3 px-3 sm:px-4 text-xs sm:text-sm hidden sm:table-cell">{new Date(transaction.created_at).toLocaleDateString()}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            )}

            {/* Alerts Tab */}
            {activeTab === 'alerts' && (
              <div>
                <h3 className="text-lg sm:text-xl font-semibold text-gray-900 mb-4 sm:mb-6">Subscription Alerts</h3>
                <div className="space-y-3 sm:space-y-4">
                  {alerts.map((alert) => (
                    <div key={alert.alert_id} className="bg-gray-50 rounded-lg sm:rounded-xl p-3 sm:p-4 border border-gray-200">
                      <div className="flex flex-col sm:flex-row sm:justify-between sm:items-start gap-2 sm:gap-4">
                        <div className="min-w-0">
                          <h4 className="font-semibold text-gray-900 text-sm sm:text-base">{alert.alert_type.replace('_', ' ')}</h4>
                          <p className="text-gray-600 mt-1 text-xs sm:text-sm">{alert.alert_message}</p>
                          <p className="text-xs sm:text-sm text-gray-500 mt-1 sm:mt-2">User: {alert.user_name}</p>
                        </div>
                        <div className="flex sm:flex-col items-center sm:items-end gap-2 sm:text-right flex-shrink-0">
                          <span className={`px-2 py-0.5 sm:py-1 rounded-full text-xs font-medium ${alert.is_sent ? 'bg-green-100 text-green-800' : 'bg-yellow-100 text-yellow-800'}`}>
                            {alert.is_sent ? 'Sent' : 'Pending'}
                          </span>
                          <p className="text-xs sm:text-sm text-gray-500">{new Date(alert.created_at).toLocaleDateString()}</p>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Plan Modal */}
      {showPlanModal && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-end sm:items-center justify-center min-h-screen pt-4 px-0 sm:px-4 pb-0 sm:pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowPlanModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-t-2xl sm:rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl w-full max-h-[90vh] overflow-y-auto">
              <div className="bg-white px-4 sm:px-6 py-5 sm:py-8">
                <div className="flex items-center justify-between mb-4 sm:mb-6">
                  <h3 className="text-lg sm:text-2xl font-bold text-gray-900">
                    {editingPlan ? 'Edit Subscription Plan' : 'Add New Subscription Plan'}
                  </h3>
                  <button
                    onClick={() => setShowPlanModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-5 h-5 sm:w-6 sm:h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <form onSubmit={handlePlanSubmit} className="space-y-4 sm:space-y-6">
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 sm:gap-6">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Plan Name *</label>
                      <input
                        type="text"
                        required
                        value={planForm.plan_name}
                        onChange={(e) => setPlanForm({...planForm, plan_name: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                        placeholder="Enter plan name"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Plan Type *</label>
                      <select
                        required
                        value={planForm.plan_type}
                        onChange={(e) => setPlanForm({...planForm, plan_type: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                      >
                        <option value="MONTHLY">Monthly</option>
                        <option value="YEARLY">Yearly</option>
                        <option value="INFLUENCER">Influencer</option>
                        <option value="BRAND">Brand</option>
                      </select>
                    </div>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Description *</label>
                    <textarea
                      required
                      rows={3}
                      value={planForm.plan_description}
                      onChange={(e) => setPlanForm({...planForm, plan_description: e.target.value})}
                      className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                      placeholder="Enter plan description"
                    />
                  </div>

                  <div className="grid grid-cols-2 gap-4 sm:gap-6">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Price (INR) *</label>
                      <input
                        type="number"
                        required
                        min="0"
                        step="0.01"
                        value={planForm.price}
                        onChange={(e) => setPlanForm({...planForm, price: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                        placeholder="Enter price"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Duration *</label>
                      <input
                        type="number"
                        required
                        min="1"
                        value={planForm.duration_days}
                        onChange={(e) => setPlanForm({...planForm, duration_days: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                        placeholder="Days"
                      />
                    </div>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Features (JSON)</label>
                    <textarea
                      rows={3}
                      value={planForm.features}
                      onChange={(e) => setPlanForm({...planForm, features: e.target.value})}
                      className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                      placeholder='{"videos_per_month": 10}'
                    />
                  </div>

                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      id="is_active"
                      checked={planForm.is_active}
                      onChange={(e) => setPlanForm({...planForm, is_active: e.target.checked})}
                      className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                    />
                    <label htmlFor="is_active" className="ml-2 block text-sm text-gray-900">
                      Active Plan
                    </label>
                  </div>

                  <div className="flex items-center justify-end space-x-3 sm:space-x-4 pt-4 sm:pt-6 border-t border-gray-200">
                    <button
                      type="button"
                      onClick={() => setShowPlanModal(false)}
                      className="px-4 sm:px-6 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl text-gray-700 hover:bg-gray-50 transition-colors font-semibold text-sm sm:text-base"
                    >
                      Cancel
                    </button>
                    <button
                      type="submit"
                      className="bg-blue-600 text-white px-4 sm:px-6 py-2.5 sm:py-3 rounded-lg sm:rounded-xl hover:bg-blue-700 transition-colors font-semibold text-sm sm:text-base"
                    >
                      {editingPlan ? 'Update Plan' : 'Create Plan'}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Coupon Modal */}
      {showCouponModal && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-end sm:items-center justify-center min-h-screen pt-4 px-0 sm:px-4 pb-0 sm:pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={() => setShowCouponModal(false)}></div>
            
            <div className="inline-block align-bottom bg-white rounded-t-2xl sm:rounded-2xl text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-2xl w-full max-h-[90vh] overflow-y-auto">
              <div className="bg-white px-4 sm:px-6 py-5 sm:py-8">
                <div className="flex items-center justify-between mb-4 sm:mb-6">
                  <h3 className="text-lg sm:text-2xl font-bold text-gray-900">
                    {editingCoupon ? 'Edit Discount Coupon' : 'Add New Discount Coupon'}
                  </h3>
                  <button
                    onClick={() => setShowCouponModal(false)}
                    className="text-gray-400 hover:text-gray-600"
                  >
                    <svg className="w-5 h-5 sm:w-6 sm:h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>

                <form onSubmit={handleCouponSubmit} className="space-y-4 sm:space-y-6">
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 sm:gap-6">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Coupon Code *</label>
                      <input
                        type="text"
                        required
                        value={couponForm.coupon_code}
                        onChange={(e) => setCouponForm({...couponForm, coupon_code: e.target.value.toUpperCase()})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                        placeholder="WELCOME10"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Coupon Name *</label>
                      <input
                        type="text"
                        required
                        value={couponForm.coupon_name}
                        onChange={(e) => setCouponForm({...couponForm, coupon_name: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                        placeholder="Welcome Discount"
                      />
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4 sm:gap-6">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Discount Type *</label>
                      <select
                        required
                        value={couponForm.discount_type}
                        onChange={(e) => setCouponForm({...couponForm, discount_type: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                      >
                        <option value="PERCENTAGE">Percentage</option>
                        <option value="FIXED_AMOUNT">Fixed Amount</option>
                      </select>
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Discount Value *</label>
                      <input
                        type="number"
                        required
                        min="0"
                        step="0.01"
                        value={couponForm.discount_value}
                        onChange={(e) => setCouponForm({...couponForm, discount_value: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                        placeholder="10"
                      />
                    </div>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Usage Limit</label>
                    <input
                      type="number"
                      min="1"
                      value={couponForm.usage_limit}
                      onChange={(e) => setCouponForm({...couponForm, usage_limit: e.target.value})}
                      className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                      placeholder="Leave blank for unlimited"
                    />
                  </div>

                  <div className="grid grid-cols-2 gap-4 sm:gap-6">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Valid From *</label>
                      <input
                        type="date"
                        required
                        value={couponForm.valid_from}
                        onChange={(e) => setCouponForm({...couponForm, valid_from: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                      />
                    </div>

                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1 sm:mb-2">Valid Until *</label>
                      <input
                        type="date"
                        required
                        value={couponForm.valid_until}
                        onChange={(e) => setCouponForm({...couponForm, valid_until: e.target.value})}
                        className="w-full px-3 sm:px-4 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-900 text-sm sm:text-base"
                      />
                    </div>
                  </div>

                  <div className="flex items-center">
                    <input
                      type="checkbox"
                      id="coupon_is_active"
                      checked={couponForm.is_active}
                      onChange={(e) => setCouponForm({...couponForm, is_active: e.target.checked})}
                      className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                    />
                    <label htmlFor="coupon_is_active" className="ml-2 block text-sm text-gray-900">
                      Active Coupon
                    </label>
                  </div>

                  <div className="flex items-center justify-end space-x-3 sm:space-x-4 pt-4 sm:pt-6 border-t border-gray-200">
                    <button
                      type="button"
                      onClick={() => setShowCouponModal(false)}
                      className="px-4 sm:px-6 py-2.5 sm:py-3 border border-gray-300 rounded-lg sm:rounded-xl text-gray-700 hover:bg-gray-50 transition-colors font-semibold text-sm sm:text-base"
                    >
                      Cancel
                    </button>
                    <button
                      type="submit"
                      className="bg-blue-600 text-white px-4 sm:px-6 py-2.5 sm:py-3 rounded-lg sm:rounded-xl hover:bg-blue-700 transition-colors font-semibold text-sm sm:text-base"
                    >
                      {editingCoupon ? 'Update Coupon' : 'Create Coupon'}
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default SubscriptionManagement;
