import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { ArrowLeft, User } from 'lucide-react';
import { userAPI } from '../utils/api';
import { toast } from 'react-hot-toast';

const Followers = () => {
  const [searchParams] = useSearchParams();
  const userId = searchParams.get('user_id');
  const navigate = useNavigate();
  const [followers, setFollowers] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (userId) {
      loadFollowers();
    } else {
      toast.error('User ID required');
      navigate('/');
    }
  }, [userId]);

  const loadFollowers = async () => {
    try {
      setLoading(true);
      const data = await userAPI.getFollowers(parseInt(userId));
      if (data.ok) {
        setFollowers(data.followers || []);
      }
    } catch (error) {
      console.error('Error loading followers:', error);
      toast.error('Failed to load followers');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-4 py-4 sticky top-0 z-20">
        <div className="flex items-center gap-4 max-w-4xl mx-auto">
          <button
            onClick={() => navigate(-1)}
            className="p-2 hover:bg-gray-100 rounded-full transition-colors"
          >
            <ArrowLeft className="w-5 h-5 text-gray-900" />
          </button>
          <h1 className="text-xl font-bold text-gray-900 font-poppins">Followers</h1>
        </div>
      </div>

      {/* Followers List */}
      <div className="max-w-4xl mx-auto px-4 py-4">
        {loading ? (
          <div className="text-center py-8 text-gray-500 text-sm font-poppins">
            Loading followers...
          </div>
        ) : followers.length > 0 ? (
          <div className="space-y-1">
            {followers.map((follower) => (
              <div
                key={follower.user_id}
                className="flex items-center gap-3 px-3 py-2 border-b border-gray-100 hover:bg-gray-50 transition-colors"
              >
                <div 
                  className="w-10 h-10 rounded-full bg-gradient-to-br from-[#6A00FF] to-purple-600 flex items-center justify-center border border-gray-200 flex-shrink-0 cursor-pointer"
                  onClick={() => {
                    if (follower.username) {
                      navigate(`/profile/${follower.username}`);
                    } else {
                      navigate(`/profile?user_id=${follower.user_id}`);
                    }
                  }}
                >
                  {follower.profile_image_url ? (
                    <img
                      src={follower.profile_image_url}
                      alt={follower.full_name || follower.username}
                      className="w-full h-full rounded-full object-cover"
                    />
                  ) : follower.business_name ? (
                    <span className="text-white text-sm font-poppins font-bold">
                      {follower.business_name.charAt(0).toUpperCase()}
                    </span>
                  ) : (
                    <User className="w-5 h-5 text-white" />
                  )}
                </div>
                <div 
                  className="flex-1 min-w-0 cursor-pointer"
                  onClick={() => {
                    if (follower.username) {
                      navigate(`/profile/${follower.username}`);
                    } else {
                      navigate(`/profile?user_id=${follower.user_id}`);
                    }
                  }}
                >
                  <h3 className="font-semibold text-sm text-gray-900 font-poppins truncate">
                    {follower.business_name || follower.full_name || follower.username || 'User'}
                  </h3>
                  {follower.username && (
                    <p className="text-xs text-gray-500 font-poppins truncate">@{follower.username}</p>
                  )}
                  {follower.provider_type && !follower.username && (
                    <p className="text-xs text-gray-500 font-poppins capitalize truncate">
                      {follower.provider_type.toLowerCase()}
                    </p>
                  )}
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <User className="w-16 h-16 text-gray-300 mx-auto mb-4" />
            <p className="text-gray-500 font-poppins">No followers yet</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Followers;

