import React, { useState } from 'react';
import { userAPI } from '../utils/api';
import { toast } from 'react-hot-toast';

const PromotionSubmission = () => {
  const [formData, setFormData] = useState({
    submission_type: 'POST',
    platform: 'INSTAGRAM',
    content_url: '',
    content_description: '',
    screenshot_url: '',
    engagement_metrics: {
      likes: '',
      comments: '',
      shares: '',
      reach: ''
    }
  });
  
  const [loading, setLoading] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleEngagementChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      engagement_metrics: {
        ...prev.engagement_metrics,
        [name]: value
      }
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Validate required fields
    const requiredFields = ['content_url', 'content_description'];
    for (const field of requiredFields) {
      if (!formData[field]) {
        toast.error(`${field.replace('_', ' ')} is required`);
        return;
      }
    }

    try {
      setLoading(true);
      const response = await userAPI.submitPromotion(formData);
      
      if (response.ok) {
        toast.success('Promotion submission sent successfully!');
        setFormData({
          submission_type: 'POST',
          platform: 'INSTAGRAM',
          content_url: '',
          content_description: '',
          screenshot_url: '',
          engagement_metrics: {
            likes: '',
            comments: '',
            shares: '',
            reach: ''
          }
        });
      } else {
        toast.error(response.message || 'Failed to submit promotion');
      }
    } catch (error) {
      toast.error('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-4xl mx-auto p-6 pb-32 sm:pb-24">
        <div className="bg-white rounded-2xl shadow-lg border border-gray-100 overflow-hidden">
          <div className="bg-gradient-to-r from-purple-600 to-pink-600 px-8 py-6">
            <h2 className="text-3xl font-bold text-white mb-2">Submit Promotion for Review</h2>
            <p className="text-purple-100">
              Submit your GetNayi platform promotion for admin review and potential concessions
            </p>
          </div>

          <form onSubmit={handleSubmit} className="p-8 space-y-6">
            {/* Content Type and Platform */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Content Type *
                </label>
                <select
                  name="submission_type"
                  value={formData.submission_type}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                  required
                >
                  <option value="POST">Post</option>
                  <option value="VIDEO">Video</option>
                  <option value="STORY">Story</option>
                  <option value="REEL">Reel</option>
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Platform *
                </label>
                <select
                  name="platform"
                  value={formData.platform}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                  required
                >
                  <option value="INSTAGRAM">Instagram</option>
                  <option value="FACEBOOK">Facebook</option>
                  <option value="TWITTER">Twitter</option>
                  <option value="TIKTOK">TikTok</option>
                  <option value="YOUTUBE">YouTube</option>
                  <option value="LINKEDIN">LinkedIn</option>
                  <option value="OTHER">Other</option>
                </select>
              </div>
            </div>

            {/* Content URL */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Content URL *
              </label>
              <input
                type="url"
                name="content_url"
                value={formData.content_url}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                placeholder="https://instagram.com/p/your-post"
                required
              />
            </div>

            {/* Content Description */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Content Description *
              </label>
              <textarea
                name="content_description"
                value={formData.content_description}
                onChange={handleInputChange}
                rows={4}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                placeholder="Describe your promotion content and how it mentions GetNayi platform..."
                required
              />
            </div>

            {/* Screenshot URL */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Screenshot URL (Optional)
              </label>
              <input
                type="url"
                name="screenshot_url"
                value={formData.screenshot_url}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                placeholder="https://example.com/screenshot.jpg"
              />
            </div>

            {/* Engagement Metrics */}
            <div className="border-t pt-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Engagement Metrics (Optional)</h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Likes
                  </label>
                  <input
                    type="number"
                    name="likes"
                    value={formData.engagement_metrics.likes}
                    onChange={handleEngagementChange}
                    min="0"
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                    placeholder="0"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Comments
                  </label>
                  <input
                    type="number"
                    name="comments"
                    value={formData.engagement_metrics.comments}
                    onChange={handleEngagementChange}
                    min="0"
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                    placeholder="0"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Shares
                  </label>
                  <input
                    type="number"
                    name="shares"
                    value={formData.engagement_metrics.shares}
                    onChange={handleEngagementChange}
                    min="0"
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                    placeholder="0"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Reach
                  </label>
                  <input
                    type="number"
                    name="reach"
                    value={formData.engagement_metrics.reach}
                    onChange={handleEngagementChange}
                    min="0"
                    className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                    placeholder="0"
                  />
                </div>
              </div>
            </div>

            {/* Submission Info */}
            <div className="bg-purple-50 border border-purple-200 rounded-xl p-4">
              <div className="flex items-start">
                <svg className="w-5 h-5 text-purple-600 mt-0.5 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                <div>
                  <h4 className="text-sm font-medium text-purple-900">Review Process</h4>
                  <p className="text-sm text-purple-700 mt-1">
                    Your promotion submission will be reviewed by our admin team. If approved, you may receive concessions 
                    on your next billing cycle. Please ensure your content genuinely promotes the GetNayi platform.
                  </p>
                </div>
              </div>
            </div>

            {/* Submit Button */}
            <div className="flex justify-end space-x-4 pt-6 border-t">
              <button
                type="button"
                onClick={() => window.history.back()}
                className="px-6 py-3 border border-gray-300 rounded-xl text-gray-700 hover:bg-gray-50 transition-colors font-semibold"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={loading}
                className="bg-purple-600 text-white px-8 py-3 rounded-xl hover:bg-purple-700 transition-colors font-semibold disabled:opacity-50 flex items-center space-x-2"
              >
                {loading ? (
                  <>
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    <span>Submitting...</span>
                  </>
                ) : (
                  <>
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8" />
                    </svg>
                    <span>Submit for Review</span>
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default PromotionSubmission;
