import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { Bell, MessageCircle, Upload, Video, Image as ImageIcon, X, Loader, CheckCircle2, AlertCircle, Plus, Trash2, Ticket } from 'lucide-react';
import { subscriptionAPI, providerAPI, listingAPI, categoriesAPI, uploadAPI, videoAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const UploadListing = () => {
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();
  const [searchParams] = useSearchParams();
  const listingType = 'PRODUCT';
  const MAX_COUPONS_PER_PRODUCT = 5;

  const createEmptyProduct = () => ({
    type: 'PRODUCT',
    product_name: '',
    price: '',
    description: '',
    link: '',
    category_id: '',
    thumbnail: '',
    thumbnailPreview: null,
    thumbnailUploadToken: null,
    uploadingThumbnail: false,
    coupons: [],
    couponCodeInput: '',
    couponDescriptionInput: ''
  });

  const [subscriptionStatus, setSubscriptionStatus] = useState(null);
  const [provider, setProvider] = useState(null);
  const [hasProvider, setHasProvider] = useState(false);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [uploading, setUploading] = useState(false);

  // Video state
  const [videoId, setVideoId] = useState(null);
  const [videoUrl, setVideoUrl] = useState('');
  const [thumbnailUrl, setThumbnailUrl] = useState('');
  const [videoPreview, setVideoPreview] = useState(null);
  const [thumbnailPreview, setThumbnailPreview] = useState(null);
  const [videoUploadToken, setVideoUploadToken] = useState(null);
  const [thumbnailUploadToken, setThumbnailUploadToken] = useState(null);
  const [uploadingThumbnail, setUploadingThumbnail] = useState(false);
  const [uploadingVideo, setUploadingVideo] = useState(false);
  const [creatingVideo, setCreatingVideo] = useState(false);
  const [videoEntryError, setVideoEntryError] = useState(null);
  const [previousVideoUrls, setPreviousVideoUrls] = useState({ videoUrl: null, thumbnailUrl: null });

  // Products state - array of products
  const [products, setProducts] = useState([createEmptyProduct()]);

  // Validation errors
  const [errors, setErrors] = useState({});

  // Refs for file inputs to reset them when removing files
  const videoInputRef = useRef(null);
  const thumbnailInputRef = useRef(null);

  useEffect(() => {
    if (isAuthenticated) {
      loadData();
    } else {
      setLoading(false);
    }
  }, [isAuthenticated]);

  const loadData = async () => {
    try {
      setLoading(true);
      const [subStatus, categoriesData] = await Promise.all([
        subscriptionAPI.getStatus(),
        categoriesAPI.getAll()
      ]);

      setSubscriptionStatus(subStatus);
      setCategories(categoriesData.categories || []);

      try {
        const providerData = await providerAPI.getMe();
        if (providerData && providerData.provider) {
          setProvider(providerData.provider);
          setHasProvider(true);
        } else {
          setHasProvider(false);
        }
      } catch (error) {
        // Handle 401 gracefully - user not authenticated or no provider
        if (error.response?.status === 401) {
          setHasProvider(false);
        } else {
          console.error('Error checking provider:', error);
          setHasProvider(false);
        }
      }
    } catch (error) {
      console.error('Error loading data:', error);
      toast.error('Failed to load data');
    } finally {
      setLoading(false);
    }
  };

  const validateVideo = () => {
    const newErrors = {};
    if (!videoUrl || !videoUploadToken) {
      newErrors.video = 'Video is required';
    }
    if (!thumbnailUrl || !thumbnailUploadToken) {
      newErrors.thumbnail = 'Thumbnail is required';
    }
    setErrors(prev => ({ ...prev, ...newErrors }));
    return Object.keys(newErrors).length === 0;
  };

  const validateProducts = () => {
    const newErrors = {};
    products.forEach((product, index) => {
      if (!product.product_name?.trim()) {
        newErrors[`product_${index}_name`] = 'Product/Service name is required';
      }
      if (!product.price || parseFloat(product.price) <= 0) {
        newErrors[`product_${index}_price`] = 'Valid price is required';
      }
      if (!product.description?.trim()) {
        newErrors[`product_${index}_description`] = 'Description is required';
      }
    });
    setErrors(prev => ({ ...prev, ...newErrors }));
    return Object.keys(newErrors).length === 0;
  };

  const handleVideoChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const maxVideoSize = 25; // MB
    if (file.size > maxVideoSize * 1024 * 1024) {
      toast.error(`Video file too large. Maximum size is ${maxVideoSize}MB`);
      return;
    }

    const allowedTypes = ['video/mp4', 'video/mov', 'video/avi', 'video/webm', 'video/mkv'];
    const allowedExtensions = ['mp4', 'mov', 'avi', 'webm', 'mkv'];
    const fileExt = file.name.split('.').pop()?.toLowerCase();
    
    if (!allowedExtensions.includes(fileExt) && !allowedTypes.includes(file.type)) {
      toast.error(`Invalid file type. Allowed formats: ${allowedExtensions.join(', ')}`);
      return;
    }

    // Enforce max duration 30 seconds using metadata
    const objectUrl = URL.createObjectURL(file);
    const tempVideo = document.createElement('video');
    const durationCheck = new Promise((resolve, reject) => {
      tempVideo.preload = 'metadata';
      tempVideo.onloadedmetadata = () => {
        const duration = tempVideo.duration || 0;
        URL.revokeObjectURL(objectUrl);
        if (isNaN(duration)) {
          resolve(); // if not available, allow upload (backend can revalidate)
          return;
        }
        if (duration > 30) {
          reject(new Error('Video is longer than 30 seconds'));
        } else {
          resolve();
        }
      };
      tempVideo.onerror = () => {
        URL.revokeObjectURL(objectUrl);
        resolve(); // allow if metadata fails to load
      };
      tempVideo.src = objectUrl;
    });

    try {
      await durationCheck;
    } catch (err) {
      toast.error('Maximum duration is 30 seconds');
      return;
    }

    setUploadingVideo(true);
    setErrors(prev => ({ ...prev, video: null }));
    setVideoEntryError(null);

    try {
      const response = await uploadAPI.uploadVideo(file);
      
      if (response.ok && response.url) {
        const uploadedVideoUrl = response.url;
        
        // If video entry already exists, reset it since we're changing the video
        // This ensures we create a new entry with the new video
        if (videoId) {
          console.log('Video changed - resetting video entry to create new one');
          setVideoId(null);
          setPreviousVideoUrls({ videoUrl: null, thumbnailUrl: null });
        }
        
        setVideoUrl(uploadedVideoUrl);
        setVideoPreview(URL.createObjectURL(file));
        setVideoUploadToken(response.success_token || null);
        
        // Create video entry if thumbnail is already uploaded
        if (thumbnailUrl && thumbnailUploadToken) {
          try {
            await createVideoEntry(uploadedVideoUrl, thumbnailUrl);
          } catch (entryError) {
            // Error already handled in createVideoEntry, just log it
            console.error('Video entry creation failed after video upload:', entryError);
            setVideoEntryError('Video entry creation failed. Please try creating it manually.');
          }
        }
        
        toast.success('Video uploaded successfully!', { duration: 3000 });
      } else {
        throw new Error(response.message || 'Upload failed');
      }
    } catch (error) {
      console.error('Video upload error:', error);
      toast.error(
        error.response?.data?.message || error.message || 'Failed to upload video'
      );
      e.target.value = '';
      // Reset video state on error
      setVideoUrl('');
      setVideoPreview(null);
      setVideoUploadToken(null);
    } finally {
      setUploadingVideo(false);
    }
  };

  const handleThumbnailChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const maxImageSize = 20; // MB
    if (file.size > maxImageSize * 1024 * 1024) {
      toast.error(`Image file too large. Maximum size is ${maxImageSize}MB`);
      return;
    }

    const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
    const allowedExtensions = ['jpg', 'jpeg', 'png', 'gif', 'webp'];
    const fileExt = file.name.split('.').pop()?.toLowerCase();
    
    if (!allowedExtensions.includes(fileExt) && !allowedTypes.includes(file.type)) {
      toast.error(`Invalid file type. Allowed formats: ${allowedExtensions.join(', ')}`);
      return;
    }

    setUploadingThumbnail(true);
    setErrors(prev => ({ ...prev, thumbnail: null }));
    setVideoEntryError(null);

    try {
      const response = await uploadAPI.uploadImage(file);
      
      if (response.ok && response.url) {
        const uploadedThumbnailUrl = response.url;
        setThumbnailUrl(uploadedThumbnailUrl);
        setThumbnailPreview(URL.createObjectURL(file));
        setThumbnailUploadToken(response.success_token || null);
        
        // If video entry already exists, check if thumbnail URL changed
        // If changed, reset videoId to force recreation with new thumbnail
        if (videoId && videoUrl && videoUploadToken) {
          // Check if thumbnail URL actually changed
          if (previousVideoUrls.thumbnailUrl !== uploadedThumbnailUrl) {
            console.log('Thumbnail changed - will recreate video entry with new thumbnail');
            setVideoId(null);
            setPreviousVideoUrls({ videoUrl: null, thumbnailUrl: null });
          } else {
            // Same thumbnail, just update existing entry
            try {
              console.log('Updating existing video entry with same thumbnail...');
              const updateResponse = await videoAPI.update(videoId, {
                thumbnail_url: uploadedThumbnailUrl
              });
              if (updateResponse.ok) {
                setPreviousVideoUrls(prev => ({ ...prev, thumbnailUrl: uploadedThumbnailUrl }));
                toast.success('Thumbnail updated successfully!', { duration: 3000 });
                return; // Early return since we updated existing entry
              } else {
                throw new Error(updateResponse.error || 'Update failed');
              }
            } catch (updateError) {
              console.error('Video entry update failed:', updateError);
              toast.error('Failed to update video entry. Creating new one...');
              setVideoId(null);
              setPreviousVideoUrls({ videoUrl: null, thumbnailUrl: null });
              // Fall through to create new entry if update fails
            }
          }
        }
        
        // Create video entry if video is already uploaded and no existing entry
        if (videoUrl && videoUploadToken && !videoId) {
          try {
            await createVideoEntry(videoUrl, uploadedThumbnailUrl);
          } catch (entryError) {
            // Error already handled in createVideoEntry, just log it
            console.error('Video entry creation failed after thumbnail upload:', entryError);
            setVideoEntryError('Video entry creation failed. Please try creating it manually.');
          }
        }
        
        if (!videoId) {
          toast.success('Thumbnail uploaded successfully!', { duration: 3000 });
        }
      } else {
        throw new Error(response.message || 'Upload failed');
      }
    } catch (error) {
      console.error('Thumbnail upload error:', error);
      toast.error(
        error.response?.data?.message || error.message || 'Failed to upload thumbnail'
      );
      e.target.value = '';
      // Reset thumbnail state on error
      setThumbnailUrl('');
      setThumbnailPreview(null);
      setThumbnailUploadToken(null);
    } finally {
      setUploadingThumbnail(false);
    }
  };

  const createVideoEntry = async (videoUrlToUse = null, thumbnailUrlToUse = null, forceRecreate = false) => {
    // Use provided URLs or fall back to state
    const finalVideoUrl = videoUrlToUse || videoUrl;
    const finalThumbnailUrl = thumbnailUrlToUse || thumbnailUrl;
    
    // Validate we have both URLs
    if (!finalVideoUrl || !finalThumbnailUrl) {
      const errorMsg = 'Both video and thumbnail URLs are required';
      setVideoEntryError(errorMsg);
      toast.error(errorMsg);
      throw new Error(errorMsg);
    }
    
    // Validate we have upload tokens
    if (!videoUploadToken || !thumbnailUploadToken) {
      const errorMsg = 'Please wait for uploads to complete';
      setVideoEntryError(errorMsg);
      toast.error(errorMsg);
      throw new Error(errorMsg);
    }
    
    // Check if video/thumbnail URLs have changed from previous entry
    const urlsChanged = 
      previousVideoUrls.videoUrl !== finalVideoUrl || 
      previousVideoUrls.thumbnailUrl !== finalThumbnailUrl;
    
    // If videoId exists and URLs haven't changed, return existing ID
    if (videoId && !forceRecreate && !urlsChanged) {
      console.log('Video already created with same URLs, skipping...');
      return videoId;
    }
    
    // If videoId exists but URLs changed, delete old entry first
    if (videoId && (forceRecreate || urlsChanged)) {
      console.log('Video/thumbnail changed - deleting old video entry:', videoId);
      try {
        await videoAPI.delete(videoId);
        console.log('Old video entry deleted successfully');
        setVideoId(null);
      } catch (deleteError) {
        console.error('Error deleting old video entry (non-critical):', deleteError);
        // Continue anyway - backend cleanup will handle orphaned entries
        setVideoId(null);
      }
    }
    
    console.log('Creating video entry...', { finalVideoUrl, finalThumbnailUrl });
    setCreatingVideo(true);
    setVideoEntryError(null);
    
    try {
      const response = await videoAPI.create({
        video_url: finalVideoUrl,
        thumbnail_url: finalThumbnailUrl,
        video_category: 'USER_CONTENT'
      });
      
      console.log('Video creation response:', response);
      
      if (response.ok && response.video_id) {
        setVideoId(response.video_id);
        setPreviousVideoUrls({ videoUrl: finalVideoUrl, thumbnailUrl: finalThumbnailUrl });
        setVideoEntryError(null);
        console.log('Video ID set:', response.video_id);
        toast.success('Video entry created! Now add products.', { duration: 2000 });
        return response.video_id;
      } else {
        const errorMsg = response.error || response.message || 'Failed to create video entry';
        console.error('Video creation failed:', errorMsg);
        setVideoEntryError(errorMsg);
        throw new Error(errorMsg);
      }
    } catch (error) {
      console.error('Error creating video entry:', error);
      const errorData = error.response?.data || {};
      const errorMessage = errorData.error || error.message || 'Failed to create video entry';
      setVideoEntryError(errorMessage);
      
      // Handle listing limit reached error
      if (errorData.limit_reached) {
        toast.error(`Listing limit reached (${errorData.current_count}/${errorData.limit}). Please subscribe for unlimited listings!`);
        navigate('/subscription/upgrade', { state: { from: 'upload-listing' } });
      } else {
        toast.error(errorMessage);
      }
      throw error; // Re-throw so caller knows it failed
    } finally {
      setCreatingVideo(false);
    }
  };

  const removeVideo = async () => {
    // Delete video entry from backend if it exists and has no products
    if (videoId) {
      try {
        console.log('Deleting video entry:', videoId);
        await videoAPI.delete(videoId);
        console.log('Video entry deleted successfully');
      } catch (error) {
        console.error('Error deleting video entry (non-critical):', error);
        // Continue with state reset even if delete fails
      }
    }
    
    // Reset video entry when video is removed
    setVideoId(null);
    setVideoUrl('');
    setVideoPreview(null);
    setVideoUploadToken(null);
    setVideoEntryError(null);
    setPreviousVideoUrls({ videoUrl: null, thumbnailUrl: null });
    if (videoPreview) {
      URL.revokeObjectURL(videoPreview);
    }
    
    // Reset file input to allow re-upload
    if (videoInputRef.current) {
      videoInputRef.current.value = '';
    }
  };

  const removeThumbnail = () => {
    // If video entry exists, reset it to force recreation with new thumbnail
    if (videoId) {
      console.log('Thumbnail removed - resetting video entry');
      setVideoId(null);
      setPreviousVideoUrls({ videoUrl: null, thumbnailUrl: null });
    }
    setThumbnailUrl('');
    setThumbnailPreview(null);
    setThumbnailUploadToken(null);
    setVideoEntryError(null);
    if (thumbnailPreview) {
      URL.revokeObjectURL(thumbnailPreview);
    }
    
    // Reset file input
    if (thumbnailInputRef.current) {
      thumbnailInputRef.current.value = '';
    }
  };
  
  const handleManualCreateVideoEntry = async () => {
    try {
      // Force recreate if videoId exists (user clicked button after changing video/thumbnail)
      await createVideoEntry(null, null, true);
    } catch (error) {
      // Error already handled in createVideoEntry
      console.error('Manual video entry creation failed:', error);
    }
  };

  const handleProductChange = (index, field, value) => {
    const updatedProducts = [...products];
    updatedProducts[index] = { ...updatedProducts[index], [field]: value };
    setProducts(updatedProducts);
    
    // Clear error when user starts typing
    const errorKey = `product_${index}_${field}`;
    if (errors[errorKey]) {
      setErrors(prev => ({ ...prev, [errorKey]: null }));
    }
  };

  const addProduct = () => {
    setProducts([...products, createEmptyProduct()]);
  };

  const handleProductThumbnailChange = async (e, index) => {
    const file = e.target.files[0];
    if (!file) return;

    const maxImageSize = 20; // MB
    if (file.size > maxImageSize * 1024 * 1024) {
      toast.error(`Image file too large. Maximum size is ${maxImageSize}MB`);
      return;
    }

    const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
    const allowedExtensions = ['jpg', 'jpeg', 'png', 'gif', 'webp'];
    const fileExt = file.name.split('.').pop()?.toLowerCase();
    
    if (!allowedExtensions.includes(fileExt) && !allowedTypes.includes(file.type)) {
      toast.error(`Invalid file type. Allowed formats: ${allowedExtensions.join(', ')}`);
      return;
    }

    // Set uploading state
    setProducts(prevProducts => {
      const updated = [...prevProducts];
      updated[index] = { ...updated[index], uploadingThumbnail: true };
      return updated;
    });

    try {
      const response = await uploadAPI.uploadImage(file);
      
      if (response.ok && response.url) {
        // Update with success state
        setProducts(prevProducts => {
          const updated = [...prevProducts];
          updated[index] = {
            ...updated[index],
            thumbnail: response.url,
            thumbnailPreview: URL.createObjectURL(file),
            thumbnailUploadToken: response.success_token || null,
            uploadingThumbnail: false
          };
          return updated;
        });
        toast.success(`Product ${index + 1} thumbnail uploaded successfully!`, { duration: 2000 });
      } else {
        throw new Error(response.message || 'Upload failed');
      }
    } catch (error) {
      console.error('Product thumbnail upload error:', error);
      toast.error(
        error.response?.data?.message || error.message || `Failed to upload product ${index + 1} thumbnail`
      );
      e.target.value = '';
      // Set error state - clear uploading flag
      setProducts(prevProducts => {
        const updated = [...prevProducts];
        updated[index] = { ...updated[index], uploadingThumbnail: false };
        return updated;
      });
    }
  };

  const removeProductThumbnail = (index) => {
    const updatedProducts = [...products];
    if (updatedProducts[index].thumbnailPreview) {
      URL.revokeObjectURL(updatedProducts[index].thumbnailPreview);
    }
    updatedProducts[index].thumbnail = '';
    updatedProducts[index].thumbnailPreview = null;
    updatedProducts[index].thumbnailUploadToken = null;
    setProducts(updatedProducts);
  };

  const removeProduct = (index) => {
    if (products.length > 1) {
      // Clean up thumbnail preview URL if exists
      if (products[index].thumbnailPreview) {
        URL.revokeObjectURL(products[index].thumbnailPreview);
      }
      const updatedProducts = products.filter((_, i) => i !== index);
      setProducts(updatedProducts);
      // Clear errors for removed product
      const newErrors = { ...errors };
      Object.keys(newErrors).forEach(key => {
        if (key.startsWith(`product_${index}_`)) {
          delete newErrors[key];
        }
      });
      setErrors(newErrors);
    }
  };

  const handleCouponInputChange = (productIndex, field, value) => {
    setProducts(prevProducts => {
      const updated = [...prevProducts];
      const updatedProduct = { ...updated[productIndex] };
      if (field === 'code') {
        updatedProduct.couponCodeInput = value;
      } else {
        updatedProduct.couponDescriptionInput = value;
      }
      updated[productIndex] = updatedProduct;
      return updated;
    });
  };

  const addCouponForProduct = (productIndex) => {
    const product = products[productIndex];
    if (!product) return;

    const code = (product.couponCodeInput || '').trim().toUpperCase();
    const description = (product.couponDescriptionInput || '').trim();

    if (!code) {
      toast.error('Enter a coupon code before adding');
      return;
    }

    if ((product.coupons || []).length >= MAX_COUPONS_PER_PRODUCT) {
      toast.error(`You can add up to ${MAX_COUPONS_PER_PRODUCT} coupons per product`);
      return;
    }

    if ((product.coupons || []).some(coupon => coupon.code === code)) {
      toast.error('Coupon already added');
      return;
    }

    const newCoupon = {
      code,
      description: description || null
    };

    setProducts(prevProducts => {
      const updated = [...prevProducts];
      const updatedProduct = { ...updated[productIndex] };
      updatedProduct.coupons = [...(updatedProduct.coupons || []), newCoupon];
      updatedProduct.couponCodeInput = '';
      updatedProduct.couponDescriptionInput = '';
      updated[productIndex] = updatedProduct;
      return updated;
    });

    toast.success('Coupon added');
  };

  const removeCouponFromProduct = (productIndex, couponIndex) => {
    setProducts(prevProducts => {
      const updated = [...prevProducts];
      const updatedProduct = { ...updated[productIndex] };
      updatedProduct.coupons = (updatedProduct.coupons || []).filter((_, idx) => idx !== couponIndex);
      updated[productIndex] = updatedProduct;
      return updated;
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate video uploads first
    if (!videoUrl || !videoUploadToken) {
      toast.error('Please upload video first');
      setErrors(prev => ({ ...prev, video: 'Video is required' }));
      return;
    }
    
    if (!thumbnailUrl || !thumbnailUploadToken) {
      toast.error('Please upload thumbnail first');
      setErrors(prev => ({ ...prev, thumbnail: 'Thumbnail is required' }));
      return;
    }

    // Ensure video entry is created
    let currentVideoId = videoId;
    if (!currentVideoId) {
      console.log('Creating video entry on form submit...');
      try {
        currentVideoId = await createVideoEntry();
        if (!currentVideoId) {
          toast.error('Failed to create video entry. Please try again or contact support.');
          return;
        }
      } catch (error) {
        console.error('Error creating video entry on submit:', error);
        toast.error('Failed to create video entry. Please try creating it manually first.');
        return;
      }
    }

    // Validate products
    if (!validateProducts()) {
      toast.error('Please fill in all required product fields');
      return;
    }

    if (!provider) {
      toast.error('Please create a provider profile first');
      navigate('/provider/register');
      return;
    }

    // Check listing limit using new can_create_listing field
    if (subscriptionStatus && subscriptionStatus.can_create_listing === false) {
      toast.error(`Listing limit reached (${subscriptionStatus.listing_limit || 5} max). Please subscribe for unlimited listings!`);
      navigate('/subscription/upgrade', { state: { from: 'upload-listing' } });
      return;
    }

    setUploading(true);
    try {
      // Use currentVideoId from above (might have been just created)
      const finalVideoId = currentVideoId || videoId;
      
      if (!finalVideoId) {
        toast.error('Video ID is missing. Please try uploading video again.');
        setUploading(false);
        return;
      }
      
      // Submit all products together
      const response = await listingAPI.create({
        video_id: finalVideoId,
        products: products.map(product => ({
          type: product.type,
          product_name: product.product_name.trim(),
          price: parseFloat(product.price),
          description: product.description.trim(),
          link: (product.link || '').trim() || null,
          category_id: product.category_id || null,
          thumbnail: product.thumbnail || null,
          coupons: (product.coupons || []).map(coupon => ({
            code: coupon.code,
            description: coupon.description || null
          }))
        }))
      });
      
      if (response.ok && response.message) {
        toast.success(`Listing request submitted successfully! ${response.product_count} product(s) linked to your video.`);
        // Reset form
        setVideoId(null);
        setVideoUrl('');
        setThumbnailUrl('');
        setVideoPreview(null);
        setThumbnailPreview(null);
        setVideoUploadToken(null);
        setThumbnailUploadToken(null);
        setProducts([createEmptyProduct()]);
        setErrors({});
        // Navigate to my listings after a delay
        setTimeout(() => {
          navigate('/listing/my');
        }, 2000);
      } else {
        toast.error(response.error || 'Failed to submit listing request');
      }
    } catch (error) {
      console.error('Error creating listing:', error);
      toast.error(error.response?.data?.error || 'Failed to create listing request');
    } finally {
      setUploading(false);
    }
  };

  const isFormValid = () => {
    // Check video is uploaded
    if (!videoUrl || !videoUploadToken || !thumbnailUrl || !thumbnailUploadToken) {
      return false;
    }
    
    // Check at least one product is valid
    return products.some(product => 
      product.product_name?.trim() &&
      product.price &&
      parseFloat(product.price) > 0 &&
      product.description?.trim()
    );
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
      </div>
    );
  }

  if (!hasProvider) {
    return (
      <div className="min-h-screen bg-white">
        <header className="w-full bg-white border-b border-gray-200 sticky top-0 z-50">
          <div className="px-4 sm:px-5 py-3.5 sm:py-4 flex items-center justify-between">
            <div className="flex items-center">
              <span className="text-2xl sm:text-[24px] font-poppins font-bold">
                <span className="text-black">Get</span>
                <span className="text-[#6C4DFF]">nayi</span>
              </span>
            </div>
            <div className="flex items-center gap-3 sm:gap-4">
              <button className="text-gray-600 hover:text-gray-900 transition-colors">
                <Bell className="w-5 h-5 sm:w-6 sm:h-6" />
              </button>
              <button className="text-gray-600 hover:text-gray-900 transition-colors">
                <MessageCircle className="w-5 h-5 sm:w-6 sm:h-6" />
              </button>
            </div>
          </div>
        </header>
        <div className="px-4 sm:px-5 py-12 flex items-center justify-center min-h-screen">
          <div className="text-center max-w-md">
            <h2 className="text-2xl font-bold text-gray-900 mb-4 font-poppins">Provider Profile Required</h2>
            <p className="text-gray-600 mb-6 font-poppins">
              You need to register as a provider before creating listings.
            </p>
            <button
              onClick={() => navigate('/provider/register')}
              className="bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white px-6 py-3 rounded-full transition-colors font-medium font-poppins shadow-md hover:shadow-lg active:scale-[0.96]"
            >
              Register as Provider
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="w-full bg-white border-b border-gray-200 sticky top-0 z-50">
        <div className="px-4 sm:px-5 py-3.5 sm:py-4 flex items-center justify-between">
          <div className="flex items-center">
            <span className="text-2xl sm:text-[24px] font-poppins font-bold">
              <span className="text-black">Get</span>
              <span className="text-[#6C4DFF]">nayi</span>
            </span>
          </div>
          <div className="flex items-center gap-3 sm:gap-4">
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <Bell className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <MessageCircle className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
          </div>
        </div>
      </header>

      <div className="px-4 sm:px-5 py-6 sm:py-8 pb-32 sm:pb-12 max-w-2xl mx-auto">
        {/* Subscription Status */}
        {subscriptionStatus && !subscriptionStatus.is_subscribed && (
          <div className={`mb-6 p-4 ${subscriptionStatus.can_create_listing === false ? 'bg-red-50 border-red-200' : 'bg-purple-50 border-purple-200'} border rounded-xl`}>
            <div className="flex items-center justify-between">
              <div>
                <p className={`text-sm font-medium ${subscriptionStatus.can_create_listing === false ? 'text-red-900' : 'text-purple-900'} font-poppins`}>
                  Free Plan
                </p>
                <p className={`text-xs ${subscriptionStatus.can_create_listing === false ? 'text-red-700' : 'text-purple-700'} font-poppins mt-1`}>
                  {subscriptionStatus.can_create_listing === false 
                    ? `Listing limit reached (${subscriptionStatus.current_listing_count || 0}/${subscriptionStatus.listing_limit || 5})`
                    : `Listings: ${subscriptionStatus.current_listing_count || 0}/${subscriptionStatus.listing_limit || 5} (${subscriptionStatus.slots_available || 0} slots available)`
                  }
                </p>
              </div>
              <button
                onClick={() => navigate('/subscription/upgrade', { state: { from: 'upload-listing' } })}
                className={`text-xs font-medium ${subscriptionStatus.can_create_listing === false ? 'text-red-600 hover:text-red-700' : 'text-[#6C4DFF] hover:text-[#5a3ddb]'} font-poppins underline`}
              >
                {subscriptionStatus.can_create_listing === false ? 'Subscribe Now' : 'Upgrade'}
              </button>
            </div>
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Video Upload Section */}
          <div>
            <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
              Video <span className="text-red-500">*</span>
            </label>
            <label className="block cursor-pointer">
              <div className="relative w-full h-48 bg-gradient-to-br from-[#E8D5FF] to-[#F3E8FF] rounded-2xl border-2 border-dashed border-[#6C4DFF] flex items-center justify-center transition-all hover:border-[#5a3ddb] hover:bg-[#F0E5FF]">
                {uploadingVideo ? (
                  <div className="flex flex-col items-center gap-3">
                    <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
                    <p className="text-[#6C4DFF] font-medium text-sm font-poppins">Uploading video...</p>
                  </div>
                ) : videoPreview ? (
                  <div className="relative w-full h-full p-4">
                    <video src={videoPreview} controls className="w-full h-full object-contain rounded-lg" />
                    <button
                      type="button"
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        removeVideo();
                      }}
                      className="absolute top-2 right-2 bg-red-500 text-white rounded-full p-2 hover:bg-red-600 transition-colors"
                    >
                      <X className="w-4 h-4" />
                    </button>
                    {videoUploadToken && (
                      <div className="absolute bottom-2 left-2 bg-green-500 text-white px-3 py-1 rounded-full flex items-center gap-1 text-xs font-medium">
                        <CheckCircle2 className="w-3 h-3" />
                        Uploaded
                      </div>
                    )}
                  </div>
                ) : (
                  <div className="flex flex-col items-center gap-3">
                    <div className="w-16 h-16 bg-[#6C4DFF] rounded-full flex items-center justify-center">
                      <Video className="w-8 h-8 text-white" />
                    </div>
                    <div className="text-center">
                      <p className="text-[#6C4DFF] font-semibold text-base font-poppins">+ Add your Video</p>
                      <p className="text-gray-500 text-xs mt-1 font-poppins">MP4, MOV, AVI, WEBM, MKV up to 25MB and 30s max</p>
                    </div>
                  </div>
                )}
              </div>
              <input
                ref={videoInputRef}
                type="file"
                accept="video/*"
                onChange={handleVideoChange}
                className="hidden"
                disabled={uploadingVideo}
              />
            </label>
            {errors.video && (
              <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-poppins">
                <AlertCircle className="w-3 h-3" />
                {errors.video}
              </p>
            )}
          </div>

          {/* Thumbnail Upload Section */}
          <div>
            <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
              Thumbnail Image <span className="text-red-500">*</span>
            </label>
            <label className="block cursor-pointer">
              <div className="relative w-full h-48 bg-gradient-to-br from-[#E8D5FF] to-[#F3E8FF] rounded-2xl border-2 border-dashed border-[#6C4DFF] flex items-center justify-center transition-all hover:border-[#5a3ddb] hover:bg-[#F0E5FF]">
                {uploadingThumbnail ? (
                  <div className="flex flex-col items-center gap-3">
                    <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
                    <p className="text-[#6C4DFF] font-medium text-sm font-poppins">Uploading thumbnail...</p>
                  </div>
                ) : thumbnailPreview ? (
                  <div className="relative w-full h-full p-4">
                    <img src={thumbnailPreview} alt="Thumbnail" className="w-full h-full object-cover rounded-lg" />
                    <button
                      type="button"
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        removeThumbnail();
                      }}
                      className="absolute top-2 right-2 bg-red-500 text-white rounded-full p-2 hover:bg-red-600 transition-colors"
                    >
                      <X className="w-4 h-4" />
                    </button>
                    {thumbnailUploadToken && (
                      <div className="absolute bottom-2 left-2 bg-green-500 text-white px-3 py-1 rounded-full flex items-center gap-1 text-xs font-medium">
                        <CheckCircle2 className="w-3 h-3" />
                        Uploaded
                      </div>
                    )}
                  </div>
                ) : (
                  <div className="flex flex-col items-center gap-3">
                    <div className="w-16 h-16 bg-[#6C4DFF] rounded-full flex items-center justify-center">
                      <ImageIcon className="w-8 h-8 text-white" />
                    </div>
                    <div className="text-center">
                      <p className="text-[#6C4DFF] font-semibold text-base font-poppins">+ Add your Image</p>
                      <p className="text-gray-500 text-xs mt-1 font-poppins">JPG, PNG, GIF, WEBP up to 20MB</p>
                    </div>
                  </div>
                )}
              </div>
              <input
                ref={thumbnailInputRef}
                type="file"
                accept="image/*"
                onChange={handleThumbnailChange}
                className="hidden"
                disabled={uploadingThumbnail || uploading}
              />
            </label>
            {errors.thumbnail && (
              <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-poppins">
                <AlertCircle className="w-3 h-3" />
                {errors.thumbnail}
              </p>
            )}
          </div>

          {/* Video Status */}
          {videoId && (
            <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-xl">
              <div className="flex items-center gap-2">
                <CheckCircle2 className="w-5 h-5 text-green-600" />
                <p className="text-sm font-medium text-green-900 font-poppins">
                  Video entry created successfully! Now add products below.
                </p>
              </div>
            </div>
          )}
          
          {/* Video Entry Creation Status */}
          {videoUrl && videoUploadToken && thumbnailUrl && thumbnailUploadToken && (
            <div className="mb-6 space-y-3">
              {creatingVideo ? (
                <div className="p-4 bg-blue-50 border border-blue-200 rounded-xl">
                  <div className="flex items-center gap-2">
                    <Loader className="w-5 h-5 animate-spin text-blue-600" />
                    <p className="text-sm font-medium text-blue-900 font-poppins">
                      Creating video entry...
                    </p>
                  </div>
                </div>
              ) : videoEntryError ? (
                <div className="p-4 bg-red-50 border border-red-200 rounded-xl">
                  <div className="flex flex-col gap-3">
                    <div className="flex items-center gap-2">
                      <AlertCircle className="w-5 h-5 text-red-600" />
                      <p className="text-sm font-medium text-red-900 font-poppins">
                        {videoEntryError}
                      </p>
                    </div>
                    <button
                      type="button"
                      onClick={handleManualCreateVideoEntry}
                      disabled={creatingVideo}
                      className="self-start px-4 py-2 bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white rounded-lg font-medium text-sm font-poppins transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                      {creatingVideo ? 'Creating...' : 'Create Video Entry'}
                    </button>
                  </div>
                </div>
              ) : !videoId || (previousVideoUrls.videoUrl !== videoUrl || previousVideoUrls.thumbnailUrl !== thumbnailUrl) ? (
                <div className="p-4 bg-yellow-50 border border-yellow-200 rounded-xl">
                  <div className="flex flex-col gap-3">
                    <div className="flex items-center gap-2">
                      <AlertCircle className="w-5 h-5 text-yellow-600" />
                      <p className="text-sm font-medium text-yellow-900 font-poppins">
                        {videoId 
                          ? 'Video or thumbnail changed. Create new video entry to use latest files.'
                          : 'Video and thumbnail uploaded. Create video entry to continue.'}
                      </p>
                    </div>
                    <button
                      type="button"
                      onClick={handleManualCreateVideoEntry}
                      disabled={creatingVideo}
                      className="self-start px-4 py-2 bg-[#6C4DFF] hover:bg-[#5a3ddb] text-white rounded-lg font-medium text-sm font-poppins transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                      {creatingVideo ? 'Creating...' : 'Create Video Entry'}
                    </button>
                  </div>
                </div>
              ) : null}
            </div>
          )}

          {/* Products Section */}
          <div className="space-y-6">
            <div className="flex items-center justify-between">
              <h3 className="text-lg font-semibold text-gray-900 font-poppins">
                Products ({products.length})
              </h3>
              <div className="flex flex-col items-end gap-1">
                <button
                  type="button"
                  onClick={addProduct}
                  disabled={!videoId || uploading || creatingVideo}
                  className="flex items-center gap-2 px-4 py-2 bg-[#6C4DFF] text-white rounded-lg font-medium text-sm font-poppins hover:bg-[#5a3ddb] transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                  title={!videoId ? 'Please upload video and thumbnail first' : ''}
                >
                  <Plus className="w-4 h-4" />
                  Add Product
                </button>
                {!videoId && (videoUrl || thumbnailUrl) && (
                  <p className="text-xs text-gray-500 font-poppins text-right">
                    {!videoUrl || !thumbnailUrl 
                      ? 'Upload both video and thumbnail' 
                      : !videoUploadToken || !thumbnailUploadToken
                      ? 'Waiting for uploads to complete...'
                      : 'Create video entry to add products'}
                  </p>
                )}
              </div>
            </div>

            {products.map((product, index) => (
              <div key={index} className="p-5 border border-gray-200 rounded-xl space-y-4 bg-gray-50">
                <div className="flex items-center justify-between mb-4">
                  <h4 className="text-base font-semibold text-gray-900 font-poppins">
                    Product {index + 1}
                  </h4>
                  {products.length > 1 && (
                    <button
                      type="button"
                      onClick={() => removeProduct(index)}
                      className="text-red-500 hover:text-red-700 transition-colors p-1"
                    >
                      <Trash2 className="w-4 h-4" />
                    </button>
                  )}
                </div>

                <div className="space-y-4">
                  {/* Product Name */}
                  <div>
                    <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
                      Product Name <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      value={product.product_name}
                      onChange={(e) => handleProductChange(index, 'product_name', e.target.value)}
                      className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-poppins text-gray-900 placeholder-gray-400 ${
                        errors[`product_${index}_name`] ? 'border-red-500' : 'border-gray-300'
                      }`}
                      placeholder="Enter product name"
                      disabled={!videoId || uploading}
                    />
                    {errors[`product_${index}_name`] && (
                      <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-poppins">
                        <AlertCircle className="w-3 h-3" />
                        {errors[`product_${index}_name`]}
                      </p>
                    )}
                  </div>

                  {/* Price & Category */}
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
                        Price () <span className="text-red-500">*</span>
                      </label>
                      <input
                        type="number"
                        min="0"
                        step="0.01"
                        value={product.price}
                        onChange={(e) => handleProductChange(index, 'price', e.target.value)}
                        className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-poppins text-gray-900 placeholder-gray-400 ${
                          errors[`product_${index}_price`] ? 'border-red-500' : 'border-gray-300'
                        }`}
                        placeholder="0.00"
                        disabled={!videoId || uploading}
                      />
                      {errors[`product_${index}_price`] && (
                        <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-poppins">
                          <AlertCircle className="w-3 h-3" />
                          {errors[`product_${index}_price`]}
                        </p>
                      )}
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
                        Category
                      </label>
                      <select
                        value={product.category_id || ''}
                        onChange={(e) => handleProductChange(index, 'category_id', e.target.value)}
                        className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-poppins text-gray-900 bg-white ${
                          errors[`product_${index}_category`] ? 'border-red-500' : 'border-gray-300'
                        }`}
                        disabled={!videoId || uploading}
                      >
                        <option value="">Select category (optional)</option>
                        {categories.map(cat => (
                          <option key={cat.category_id} value={cat.category_id}>
                            {cat.category_name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>

                  {/* Description */}
                  <div>
                    <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
                      Description <span className="text-red-500">*</span>
                    </label>
                    <textarea
                      rows={3}
                      value={product.description}
                      onChange={(e) => handleProductChange(index, 'description', e.target.value)}
                      className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-poppins text-gray-900 placeholder-gray-400 resize-none ${
                        errors[`product_${index}_description`] ? 'border-red-500' : 'border-gray-300'
                      }`}
                      placeholder="Describe your product"
                      disabled={!videoId || uploading}
                    />
                    {errors[`product_${index}_description`] && (
                      <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-poppins">
                        <AlertCircle className="w-3 h-3" />
                        {errors[`product_${index}_description`]}
                      </p>
                    )}
                  </div>

                  {/* Link */}
                  <div>
                    <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
                      Link (Website/Store)
                    </label>
                    <input
                      type="url"
                      value={product.link || ''}
                      onChange={(e) => handleProductChange(index, 'link', e.target.value)}
                      className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-poppins text-gray-900 placeholder-gray-400"
                      placeholder="https://your-website.com (optional)"
                      disabled={!videoId || uploading}
                    />
                  </div>

                  {/* Coupons */}
                  <div className="border border-dashed border-[#6C4DFF]/50 rounded-xl p-4 bg-white shadow-sm shadow-[#6C4DFF]/5">
                    <div className="flex items-center justify-between gap-3 flex-wrap">
                      <div className="flex items-center gap-2">
                        <Ticket className="w-5 h-5 text-[#6C4DFF]" />
                        <div>
                          <p className="text-sm font-semibold text-gray-900 font-poppins">
                            Coupons
                          </p>
                          <p className="text-xs text-gray-500 font-poppins">
                            Add up to {MAX_COUPONS_PER_PRODUCT} coupon codes for this product
                          </p>
                        </div>
                      </div>
                      <span className="text-xs font-semibold text-gray-500 font-poppins">
                        {(product.coupons?.length || 0)}/{MAX_COUPONS_PER_PRODUCT}
                      </span>
                    </div>

                    {(product.coupons?.length || 0) > 0 && (
                      <div className="mt-4 space-y-2">
                        {product.coupons.map((coupon, couponIndex) => (
                          <div
                            key={`${coupon.code}-${couponIndex}`}
                            className="flex items-center justify-between rounded-lg border border-gray-200 bg-gray-50 px-3 py-2"
                          >
                            <div>
                              <p className="text-sm font-semibold text-gray-900 font-mono tracking-wide">
                                {coupon.code}
                              </p>
                              {coupon.description && (
                                <p className="text-xs text-gray-600 mt-0.5">
                                  {coupon.description}
                                </p>
                              )}
                            </div>
                            <button
                              type="button"
                              onClick={() => removeCouponFromProduct(index, couponIndex)}
                              className="text-gray-400 hover:text-red-500 transition-colors"
                              title="Remove coupon"
                              disabled={uploading}
                            >
                              <Trash2 className="w-4 h-4" />
                            </button>
                          </div>
                        ))}
                      </div>
                    )}

                    <div className="mt-4 grid md:grid-cols-[1fr_1.5fr_auto] gap-3">
                      <input
                        type="text"
                        value={product.couponCodeInput || ''}
                        onChange={(e) => handleCouponInputChange(index, 'code', e.target.value.toUpperCase())}
                        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-poppins text-gray-900 placeholder-gray-400"
                        placeholder="Coupon code (e.g. SAVE20)"
                        disabled={!videoId || uploading}
                      />
                      <input
                        type="text"
                        value={product.couponDescriptionInput || ''}
                        onChange={(e) => handleCouponInputChange(index, 'description', e.target.value)}
                        className="w-full px-4 py-3 border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-poppins text-gray-900 placeholder-gray-400"
                        placeholder="Short description (optional)"
                        disabled={!videoId || uploading}
                      />
                      <button
                        type="button"
                        onClick={() => addCouponForProduct(index)}
                        disabled={
                          uploading ||
                          !videoId ||
                          (product.coupons?.length || 0) >= MAX_COUPONS_PER_PRODUCT
                        }
                        className="px-4 py-3 bg-[#6C4DFF] text-white rounded-xl font-semibold text-sm font-poppins hover:bg-[#5a3ddb] transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                        title={
                          (product.coupons?.length || 0) >= MAX_COUPONS_PER_PRODUCT
                            ? `You can only add ${MAX_COUPONS_PER_PRODUCT} coupons`
                            : ''
                        }
                      >
                        Add coupon
                      </button>
                    </div>
                  </div>

                  {/* Product Thumbnail */}
                  <div>
                    <label className="block text-sm font-medium text-gray-900 mb-2 font-poppins">
                      Product Thumbnail
                    </label>
                    <label className="block cursor-pointer">
                      <div className="relative w-full h-32 bg-gradient-to-br from-[#E8D5FF] to-[#F3E8FF] rounded-xl border-2 border-dashed border-[#6C4DFF] flex items-center justify-center transition-all hover:border-[#5a3ddb] hover:bg-[#F0E5FF]">
                        {product.uploadingThumbnail ? (
                          <div className="flex flex-col items-center gap-2">
                            <Loader className="w-5 h-5 animate-spin text-[#6C4DFF]" />
                            <p className="text-[#6C4DFF] font-medium text-xs font-poppins">Uploading...</p>
                          </div>
                        ) : product.thumbnailPreview ? (
                          <div className="relative w-full h-full p-2">
                            <img src={product.thumbnailPreview} alt="Product thumbnail" className="w-full h-full object-cover rounded-lg" />
                            <button
                              type="button"
                              onClick={(e) => {
                                e.preventDefault();
                                e.stopPropagation();
                                removeProductThumbnail(index);
                              }}
                              className="absolute top-1 right-1 bg-red-500 text-white rounded-full p-1.5 hover:bg-red-600 transition-colors"
                            >
                              <X className="w-3 h-3" />
                            </button>
                            {product.thumbnailUploadToken && (
                              <div className="absolute bottom-1 left-1 bg-green-500 text-white px-2 py-0.5 rounded-full flex items-center gap-1 text-xs font-medium">
                                <CheckCircle2 className="w-2.5 h-2.5" />
                                Uploaded
                              </div>
                            )}
                          </div>
                        ) : (
                          <div className="flex flex-col items-center gap-2">
                            <div className="w-10 h-10 bg-[#6C4DFF] rounded-full flex items-center justify-center">
                              <ImageIcon className="w-5 h-5 text-white" />
                            </div>
                            <div className="text-center">
                              <p className="text-[#6C4DFF] font-semibold text-sm font-poppins">+ Add Thumbnail</p>
                              <p className="text-gray-500 text-xs mt-0.5 font-poppins">Optional</p>
                            </div>
                          </div>
                        )}
                      </div>
                      <input
                        type="file"
                        accept="image/*"
                        onChange={(e) => handleProductThumbnailChange(e, index)}
                        className="hidden"
                        disabled={product.uploadingThumbnail || uploading || !videoId || (product.thumbnailUploadToken !== null)}
                      />
                    </label>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Submit Button */}
          <div className="pt-4">
            <button
              type="submit"
              disabled={uploading || creatingVideo || !isFormValid()}
              className="w-full bg-[#6C4DFF] text-white py-4 rounded-xl font-semibold text-base font-poppins shadow-lg hover:shadow-xl active:scale-[0.98] transition-all disabled:opacity-50 disabled:cursor-not-allowed disabled:active:scale-100"
            >
              {uploading || creatingVideo ? (
                <span className="flex items-center justify-center gap-2">
                  <Loader className="w-5 h-5 animate-spin" />
                  {creatingVideo ? 'Creating Video...' : `Submitting ${products.length} Product(s)...`}
                </span>
              ) : (
                `Submit ${products.length} Product${products.length > 1 ? 's' : ''}`
              )}
            </button>
            {!isFormValid() && (
              <p className="text-xs text-gray-500 text-center mt-2 font-poppins">
                {!videoId ? 'Please upload video and thumbnail first' : 'Please fill in all required product fields to submit'}
              </p>
            )}
          </div>
        </form>
      </div>

    </div>
  );
};

export default UploadListing;
