import axios from 'axios';

// change mode according to your need
const isProduction = true;
const API_BASE_URL = 'https://getnayi.mycartly.in';

const api = axios.create({
  baseURL: API_BASE_URL,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 10000, // 10 second timeout
});

// Add request interceptor to include token from localStorage in Authorization header
api.interceptors.request.use(
  (config) => {
    // Get token from localStorage and add to Authorization header
    const token = localStorage.getItem('auth_token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    } else {
      // Log when token is missing for authenticated endpoints (except auth checks)
      if (config.url && !config.url.includes('/api/me') && !config.url.includes('/api/auth/')) {
        console.warn(`[API] No auth token found for request: ${config.url}`);
      }
    }
    
    // Log request details for debugging
    if (config.url?.includes('approve')) {
      console.log('=== APPROVE REQUEST DEBUG ===');
      console.log('URL:', config.url);
      console.log('Method:', config.method);
      console.log('Data (before transform):', config.data);
      console.log('Data type:', typeof config.data);
      console.log('Headers:', JSON.stringify(config.headers, null, 2));
      console.log('===========================');
    }
    return config;
  },
  (error) => {
    console.error('Request error:', error);
    return Promise.reject(error);
  }
);

// Add response interceptor for debugging and error handling
api.interceptors.response.use(
  (response) => {
    // Only log non-auth-check responses to reduce noise
    const isAuthCheck = response.config?.url?.includes('/api/me');
    if (!isAuthCheck) {
      console.log('Response received:', response.status, response.config.url);
    }
    
    // Check if backend returned an error in the response body
    if (response.data && response.data.ok === false) {
      const error = new Error(response.data.error || 'Request failed');
      error.response = {
        ...response,
        data: response.data
      };
      return Promise.reject(error);
    }
    
    return response;
  },
  (error) => {
    const isAuthCheck = error.config?.url?.includes('/api/me');
    const is401 = error.response?.status === 401;
    
    // Don't log expected 401 errors on auth checks (user not logged in)
    if (!is401 || !isAuthCheck) {
      console.error('Response error:', error.response?.status, error.config?.url, error.message);
    }
    
    // Enhance error message for common cases
    if (!error.response) {
      error.message = 'Network error. Please check your internet connection.';
      error.code = 'NETWORK_ERROR';
    } else if (error.response.status === 401) {
      // For auth check endpoints, don't treat 401 as an error (user might not be logged in)
      if (isAuthCheck) {
        // Return a structured error that calling code can handle gracefully
        error.isExpected = true;
      }
      // Don't automatically clear tokens - let the calling code handle it
      // Only clear token if explicitly told to do so
    } else if (error.response.status >= 500) {
      error.message = error.response?.data?.error || 'Server error. Please try again later.';
    }
    
    // If the error response has data, return it so the calling code can handle it
    if (error.response?.data) {
      return Promise.reject({
        ...error,
        response: {
          ...error.response,
          data: error.response.data
        }
      });
    }
    return Promise.reject(error);
  }
);

