import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { authAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';
import { signInWithGooglePopup } from '../utils/firebaseAuth';

const Login = () => {
  const navigate = useNavigate();
  const { login } = useAuth();
  
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleGoogleSignIn = async () => {
    setError('');
    setLoading(true);
    try {
      // Sign in with Google using Firebase
      const { user } = await signInWithGooglePopup();
      
      // Print Firebase user data
      console.log('=== GOOGLE SIGN-IN - FIREBASE USER DATA ===');
      console.log('Firebase User:', {
        uid: user.uid,
        email: user.email,
        displayName: user.displayName,
        photoURL: user.photoURL,
        emailVerified: user.emailVerified,
        providerData: user.providerData,
        metadata: {
          creationTime: user.metadata.creationTime,
          lastSignInTime: user.metadata.lastSignInTime
        }
      });
      console.log('==========================================');
      
      // Get the Firebase ID token
      const idToken = await user.getIdToken();
      console.log('Firebase ID Token received (length):', idToken.length);
      
      // Send token to backend for verification and user creation/login
      const data = await authAPI.signInWithGoogle(idToken);
      
      // Print backend response data
      console.log('=== GOOGLE SIGN-IN - BACKEND RESPONSE ===');
      console.log('Backend Response:', {
        ok: data.ok,
        needsOnboard: data.needsOnboard,
        hasToken: !!data.token,
        hasUser: !!data.user,
        user: data.user,
        message: data.message
      });
      console.log('==========================================');
      
      // Check if backend returned an error
      if (!data.ok) {
        const errorMsg = data.error || 'Authentication failed. Please try again.';
        console.error('Backend returned error:', errorMsg);
        setError(errorMsg);
        toast.error(errorMsg);
        setLoading(false);
        return;
      }
      
      if (data.needsOnboard) {
        // Store the ID token temporarily for signup
        sessionStorage.setItem('google_id_token', idToken);
        console.log('New user - redirecting to signup with onboarding token');
        toast.success('Please complete your profile');
        navigate('/signup', { state: { onboardingToken: data.onboardingToken, isGoogleAuth: true } });
      } else {
        // Store token in localStorage for Authorization header
        if (data.token) {
          localStorage.setItem('auth_token', data.token);
          console.log('Auth token stored in localStorage');
        }
        
        // Persist user in auth context
        if (data.user) {
          console.log('=== GOOGLE SIGN-IN - FINAL USER DATA ===');
          console.log('User Data:', {
            user_id: data.user.user_id,
            username: data.user.username,
            full_name: data.user.full_name,
            email: data.user.email,
            phone: data.user.phone,
            profile_picture: data.user.profile_picture,
            is_provider: data.user.is_provider,
            is_influencer: data.user.is_influencer,
            is_admin: data.user.is_admin,
            capsules: data.user.capsules,
            type: data.user.type
          });
          console.log('========================================');
          login(data.user);
        }
        
        // Navigate users directly to home page after login
        toast.success('Signed in successfully!');
        navigate('/home', { replace: true });
      }
    } catch (err) {
      console.error('Google sign-in error:', err);
      let errorMessage = 'Failed to sign in with Google';
      
      if (err.code === 'auth/popup-blocked') {
        errorMessage = 'Popup was blocked. Please allow popups and try again.';
      } else if (err.code === 'auth/popup-closed-by-user') {
        errorMessage = 'Sign-in was cancelled.';
      } else if (err.code === 'auth/network-request-failed') {
        errorMessage = 'Network error. Please check your internet connection.';
      } else if (err.code === 'auth/too-many-requests') {
        errorMessage = 'Too many sign-in attempts. Please try again later.';
      } else if (err.response?.data?.error) {
        errorMessage = err.response.data.error;
        // Show solution if provided
        if (err.response.data.solution) {
          errorMessage += `\n\n${err.response.data.solution}`;
        }
        // Show details if provided
        if (err.response.data.details && !errorMessage.includes(err.response.data.details)) {
          errorMessage += `\n\n${err.response.data.details}`;
        }
      } else if (err.response?.data?.details) {
        errorMessage = err.response.data.details;
      } else if (err.message) {
        errorMessage = err.message;
      }
      
      setError(errorMessage);
      toast.error(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-white relative overflow-hidden">
      {/* Subtle background accents */}
      <div className="pointer-events-none absolute -top-32 -right-32 w-96 h-96 rounded-full bg-[#6A00FF]/10 blur-3xl" />
      <div className="pointer-events-none absolute -bottom-24 -left-24 w-80 h-80 rounded-full bg-purple-400/10 blur-3xl" />

      <div className="flex items-center justify-center p-4 sm:p-6 min-h-screen">
        <div className="bg-white/80 backdrop-blur border border-gray-200 rounded-2xl shadow-xl p-6 sm:p-8 w-full max-w-md relative z-10">
          <div className="text-center mb-6 sm:mb-8">
            <h1 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-2 font-poppins">Welcome to GetNayi</h1>
            <p className="text-sm sm:text-base text-gray-600 font-poppins">
              Sign in with your Google account
            </p>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-600 rounded-lg text-xs sm:text-sm font-poppins">
              {error}
            </div>
          )}

          {/* Google Sign In Button */}
          <button
            type="button"
            onClick={handleGoogleSignIn}
            disabled={loading}
            className="w-full flex items-center justify-center gap-3 bg-white border-2 border-gray-300 text-gray-700 py-4 rounded-xl text-base font-medium active:bg-gray-50 disabled:bg-gray-100 disabled:border-gray-200 transition-colors touch-manipulation font-poppins hover:border-gray-400"
          >
            <svg className="w-5 h-5" viewBox="0 0 24 24">
              <path
                fill="#4285F4"
                d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
              />
              <path
                fill="#34A853"
                d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
              />
              <path
                fill="#FBBC05"
                d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
              />
              <path
                fill="#EA4335"
                d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
              />
            </svg>
            {loading ? 'Signing in...' : 'Continue with Google'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Login;

