import React, { useState, useRef, useEffect } from 'react';
import { Settings, Edit3, BarChart3, Share2, LogOut, ChevronDown, FileText } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { toast } from 'react-hot-toast';
import { cmsAPI } from '../utils/api';

const SettingsDropdown = ({ user }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [cmsPages, setCmsPages] = useState([]);
  const [pagesLoading, setPagesLoading] = useState(false);
  const [pagesError, setPagesError] = useState('');
  const dropdownRef = useRef(null);
  const pagesLoadedRef = useRef(false);
  const navigate = useNavigate();
  const { logout } = useAuth();

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  const fetchCmsPages = async () => {
    try {
      setPagesLoading(true);
      setPagesError('');
      const response = await cmsAPI.getPublishedPages();
      if (response?.ok) {
        setCmsPages(response.pages || []);
      } else {
        setPagesError(response?.message || 'Failed to load pages');
      }
    } catch (error) {
      console.error('CMS pages load error:', error);
      setPagesError('Failed to load pages');
    } finally {
      setPagesLoading(false);
      pagesLoadedRef.current = true;
    }
  };

  useEffect(() => {
    if (isOpen && !pagesLoadedRef.current) {
      fetchCmsPages();
    }
  }, [isOpen]);

  const handleShareProfile = () => {
    const profileUrl = user?.username 
      ? `${window.location.origin}/profile/${user.username}`
      : `${window.location.origin}/profile?user_id=${user?.user_id}`;
    
    navigator.clipboard.writeText(profileUrl).then(() => {
      toast.success('Profile link copied to clipboard!');
    }).catch(() => {
      toast.error('Failed to copy link');
    });
    setIsOpen(false);
  };

  const handleLogout = async () => {
    try {
      await logout();
      navigate('/');
      toast.success('Logged out successfully');
    } catch (e) {
      toast.error('Failed to logout');
    }
    setIsOpen(false);
  };

  return (
    <div className="relative" ref={dropdownRef}>
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="flex items-center gap-2 bg-white hover:bg-gray-50 border border-gray-300 text-gray-900 px-4 py-2 rounded-lg transition-colors font-poppins text-sm"
      >
        <Settings className="w-4 h-4" />
        <span className="hidden sm:inline">Settings</span>
        <ChevronDown className={`w-4 h-4 transition-transform ${isOpen ? 'rotate-180' : ''}`} />
      </button>

      {isOpen && (
        <div className="absolute right-0 mt-2 w-56 bg-white rounded-lg shadow-lg border border-gray-200 py-2 z-50">
          <button
            onClick={() => {
              navigate('/settings');
              setIsOpen(false);
            }}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <Settings className="w-4 h-4 text-gray-600" />
            <span>Subscription Details</span>
          </button>

          <button
            onClick={() => {
              navigate('/profile/edit');
              setIsOpen(false);
            }}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <Edit3 className="w-4 h-4 text-gray-600" />
            <span>Edit Profile</span>
          </button>

          <button
            onClick={() => {
              navigate('/profile/analytics');
              setIsOpen(false);
            }}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <BarChart3 className="w-4 h-4 text-gray-600" />
            <span>Analytics</span>
          </button>

          <button
            onClick={handleShareProfile}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <Share2 className="w-4 h-4 text-gray-600" />
            <span>Share My Profile</span>
          </button>

          {pagesLoading && (
            <div className="px-4 py-2 text-xs text-gray-400 font-poppins">Loading pages...</div>
          )}

          {!pagesLoading && pagesError && (
            <div className="px-4 py-2 text-xs text-red-500 font-poppins">{pagesError}</div>
          )}

          {!pagesLoading && !pagesError && cmsPages.length > 0 && (
            <>
              <div className="border-t border-gray-200 my-1" />
              <p className="px-4 pt-2 text-[11px] font-semibold tracking-wide text-gray-400 uppercase font-poppins">
                Pages
              </p>
              {cmsPages.map((page) => (
                <button
                  key={page.slug}
                  onClick={() => {
                    navigate(`/pages/${page.slug}`);
                    setIsOpen(false);
                  }}
                  className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
                >
                  <FileText className="w-4 h-4 text-gray-600" />
                  <span className="truncate">{page.title}</span>
                </button>
              ))}
            </>
          )}

          <div className="border-t border-gray-200 my-1" />

          <button
            onClick={handleLogout}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-red-50 transition-colors font-poppins text-sm text-red-600"
          >
            <LogOut className="w-4 h-4" />
            <span>Logout</span>
          </button>
        </div>
      )}
    </div>
  );
};

export default SettingsDropdown;


