import React, { createContext, useContext, useState, useEffect } from 'react';
import { authAPI } from '../utils/api';
import { getGoogleRedirectResult, onAuthStateChange, signOut as firebaseSignOut } from '../utils/firebaseAuth';

const AuthContext = createContext(null);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    checkAuth();
    checkGoogleRedirect();
    
    // Listen to Firebase auth state changes
    const unsubscribe = onAuthStateChange(async (firebaseUser) => {
      if (firebaseUser) {
        console.log('=== AUTH CONTEXT - FIREBASE AUTH STATE CHANGE ===');
        console.log('Firebase User:', {
          uid: firebaseUser.uid,
          email: firebaseUser.email,
          displayName: firebaseUser.displayName,
          photoURL: firebaseUser.photoURL
        });
        // If Firebase user exists, verify with backend
        try {
          const idToken = await firebaseUser.getIdToken();
          const data = await authAPI.signInWithGoogle(idToken);
          
          console.log('Auth Context - Backend Response:', {
            ok: data.ok,
            needsOnboard: data.needsOnboard,
            hasToken: !!data.token,
            hasUser: !!data.user
          });
          
          if (data.token) {
            localStorage.setItem('auth_token', data.token);
          }
          
          if (data.user) {
            console.log('Auth Context - Setting User:', {
              user_id: data.user.user_id,
              username: data.user.username,
              email: data.user.email,
              is_admin: data.user.is_admin
            });
            setUser(data.user);
          }
          console.log('================================================');
        } catch (err) {
          console.error('Firebase auth state sync error:', err);
          // Don't show error to user here as this is a background sync
        }
      } else {
        // Only log if we had a user before (not on initial check)
        // This prevents logging "signed out" on every app start
      }
    });
    
    return () => unsubscribe();
  }, []);

  const checkGoogleRedirect = async () => {
    try {
      const result = await getGoogleRedirectResult();
      if (result) {
        console.log('=== AUTH CONTEXT - GOOGLE REDIRECT RESULT ===');
        console.log('Redirect Result User:', {
          uid: result.user.uid,
          email: result.user.email,
          displayName: result.user.displayName
        });
        
        const idToken = await result.user.getIdToken();
        const data = await authAPI.signInWithGoogle(idToken);
        
        console.log('Redirect - Backend Response:', {
          ok: data.ok,
          needsOnboard: data.needsOnboard,
          hasToken: !!data.token,
          hasUser: !!data.user
        });
        
        if (data.needsOnboard) {
          sessionStorage.setItem('google_id_token', idToken);
          console.log('Redirect - New user, storing ID token for signup');
          // Navigation will be handled by the component
        } else {
          if (data.token) {
            localStorage.setItem('auth_token', data.token);
          }
          if (data.user) {
            console.log('Redirect - Setting User:', data.user);
            setUser(data.user);
          }
        }
        console.log('===============================================');
      }
    } catch (err) {
      console.error('Google redirect check error:', err);
      // Don't show error to user here - will be handled by Login page
    }
  };

  const checkAuth = async () => {
    try {
      setLoading(true);
      const data = await authAPI.getCurrentUser();
      if (data.ok && data.user) {
        setUser(data.user);
      } else {
        // No user found or not authenticated - this is normal
        setUser(null);
      }
    } catch (err) {
      // 401 errors are expected when user is not logged in - handle silently
      if (err.isExpected || (err.response?.status === 401 && err.config?.url?.includes('/api/me'))) {
        setUser(null);
      } else {
        // Log unexpected errors
        console.error('Auth check error:', err);
        setUser(null);
      }
    } finally {
      setLoading(false);
    }
  };

  const login = (userData) => {
    console.log('=== AUTH CONTEXT - LOGIN CALLED ===');
    console.log('User Data:', userData);
    console.log('===================================');
    setUser(userData);
  };

  const logout = async () => {
    try {
      // Sign out from Firebase if user was signed in with Google
      try {
        await firebaseSignOut();
      } catch (firebaseErr) {
        // Ignore Firebase signout errors (user might not be signed in with Firebase)
        console.log('Firebase signout:', firebaseErr);
      }
      
      // Sign out from backend
      await authAPI.logout();
      setUser(null);
      // Remove token from localStorage on logout
      localStorage.removeItem('auth_token');
    } catch (err) {
      console.error('Logout error:', err);
      // Still remove token even if logout API call fails
      localStorage.removeItem('auth_token');
      setUser(null);
    }
  };

  const value = {
    user,
    loading,
    error,
    login,
    logout,
    checkAuth,
    isAuthenticated: !!user,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

